<!--
VolcanoLayer.vue - Interactive Volcano Map Layer Component

This component displays volcanic data on a Leaflet map with interactive filtering capabilities.
It shows volcanoes as triangle markers with different colors based on eruption dates:
- Red triangles: Recent eruptions (2000-2025)
- Orange triangles: 1900s eruptions (1900-1999) 
- Black triangles: All volcanoes (when "All Volcanoes" filter is active)

Features:
- Dynamic triangle sizing based on zoom level
- Interactive legend popup with filter checkboxes
- Popup tooltips with volcano details
- Real-time filtering by eruption date ranges
-->

<template>
  <!-- Volcano Legend Popup -->
  <div v-if="isVolcanoActive" class="map-popup">
    <h3>{{ currentTranslations.volcanoLegend }}</h3>
    <div class="popup-content">
      <!-- Filter Controls -->
      <div class="legend-controls">
        <div class="control-item">
          <input 
            id="volcanoes-recent" 
            type="checkbox" 
            v-model="isRecentVolcanoesVisible" 
            @change="toggleVolcanoFilter" 
            class="volcano-checkbox" 
          />
          <label for="volcanoes-recent" class="legend-text">
            {{ currentTranslations.eruptedRecent }}
          </label>
        </div>
        <div class="control-item">
          <input 
            id="volcanoes-1900s" 
            type="checkbox" 
            v-model="is1900sVolcanoesVisible" 
            @change="toggleVolcanoFilter" 
            class="volcano-checkbox" 
          />
          <label for="volcanoes-1900s" class="legend-text">
            {{ currentTranslations.erupted1900s }}
          </label>
        </div>
        <div class="control-item">
          <input 
            id="volcanoes-all" 
            type="checkbox" 
            v-model="isAllVolcanoesVisible" 
            @change="toggleVolcanoFilter" 
            class="volcano-checkbox" 
          />
          <label for="volcanoes-all" class="legend-text">
            {{ currentTranslations.allVolcanoes }}
          </label>
        </div>
      </div>
      
      <!-- Color Legend -->
      <div class="legend-grid">
        <div class="legend-item">
          <div class="legend-triangle" style="border-bottom-color:#ff0000;"></div>
          <span class="legend-text">{{ currentTranslations.recentEruption }}</span>
        </div>
        <div class="legend-item">
          <div class="legend-triangle" style="border-bottom-color:#ff8000;"></div>
          <span class="legend-text">{{ currentTranslations.olderEruption }}</span>
        </div>
        <div class="legend-item">
          <div class="legend-triangle" style="border-bottom-color:black;"></div>
          <span class="legend-text">{{ currentTranslations.allVolcanoes }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue';
import L from 'leaflet';
import volcanoData from '@/assets/volcanoes.json';

// ============================================================================
// COMPONENT PROPS
// ============================================================================

const props = defineProps({
  mapInstance: Object,
  currentTranslations: Object,
  riskLayersToggleRef: Object
});

// ============================================================================
// REACTIVE STATE
// ============================================================================

// Main layer state
const volcanoLayer = ref(null);
const isVolcanoActive = ref(false);

// Checkbox visibility states for different volcano categories
const isRecentVolcanoesVisible = ref(true);
const is1900sVolcanoesVisible = ref(false);
const isAllVolcanoesVisible = ref(false);

// ============================================================================
// STYLING FUNCTIONS
// ============================================================================

/**
 * Determines the visual style for volcano markers based on eruption date
 * @param {string} lastEruption - The last eruption date string
 * @param {boolean} isAllVisible - Whether "All Volcanoes" filter is active
 * @returns {Object} Style object with colors and opacity settings
 */
function getVolcanoStyle(lastEruption, isAllVisible) {
  let fillColor, color;
  const currentYear = new Date().getFullYear();
  let eruptionYear = NaN;

  // Parse eruption year from the lastEruption string
  if (lastEruption !== 'Unknown') {
    const parts = lastEruption.split(' ');
    let year = parseInt(parts[0]);
    const era = parts[1];
    if (era === 'BCE') {
      year = -year;
    }
    eruptionYear = year;
  }

  // Categorize eruptions by date ranges
  const isRecent = !isNaN(eruptionYear) && eruptionYear >= 2000 && eruptionYear <= 2025;
  const is1900s = !isNaN(eruptionYear) && eruptionYear >= 1900 && eruptionYear <= 1999;

  // Set colors based on category and visibility state
  if (isAllVisible) {
    fillColor = 'black'; // Black for all volcanoes when "All Volcanoes" is checked
    color = 'black';
  } else if (isRecent) {
    fillColor = '#ff4444'; // Red for recent eruptions
    color = '#ff0000';
  } else if (is1900s) {
    fillColor = '#ffaa66'; // Orange for 1900s eruptions
    color = '#ff8000';
  } else {
    fillColor = '#cccccc'; // Grey for other/unknown eruptions when not "All Volcanoes"
    color = '#999999';
  }

  return {
    fillColor: fillColor,
    color: color,
    fillOpacity: 0.8,
    weight: 1,
  };
}

// ============================================================================
// LAYER CREATION FUNCTIONS
// ============================================================================

/**
 * Creates a Leaflet GeoJSON layer with triangle markers for volcanoes
 * Triangles scale dynamically with zoom level to maintain consistent visual size
 * @param {Array} volcanoFeatures - Array of volcano GeoJSON features
 * @param {boolean} isAllVisible - Whether "All Volcanoes" filter is active
 * @returns {L.GeoJSON} Leaflet GeoJSON layer with styled volcano markers
 */
function createVolcanoLayer(volcanoFeatures, isAllVisible) {
  return L.geoJSON({ type: "FeatureCollection", features: volcanoFeatures }, {
    pointToLayer: (feature, latlng) => {
      const style = getVolcanoStyle(feature.properties.last_eruption, isAllVisible);
      
      // Calculate triangle size based on zoom level
      const baseSize = 10; // Base size at zoom level 2 (global view)
      const currentZoom = props.mapInstance?.getZoom() || 2;
      
      // Calculate size based on zoom level (inverse relationship)
      // This makes the icon appear the same size regardless of zoom
      const triangleSize = baseSize / Math.pow(1.5, currentZoom - 2);
      
      // Minimum size to prevent icons from becoming too small
      const minSize = 5;
      const finalSize = Math.max(triangleSize, minSize);

      // Create triangle coordinates relative to the volcano position
      const triangleLatLngs = [
        [latlng.lat + 0.3 * (finalSize/5), latlng.lng],             // Apex
        [latlng.lat - 0.3 * (finalSize/5), latlng.lng - 0.3 * (finalSize/5)], // Bottom Left
        [latlng.lat - 0.3 * (finalSize/5), latlng.lng + 0.3 * (finalSize/5)]  // Bottom Right
      ];

      return L.polygon(triangleLatLngs, {
        fillColor: style.fillColor,
        color: style.color,
        fillOpacity: style.fillOpacity,
        weight: style.weight
      });
    },
    onEachFeature: (feature, layer) => {
      // Add popup with volcano information
      const props = feature.properties;
      layer.bindPopup(`
        <div class="volcano-popup">
          <h4>${props.name}</h4>
          <p><strong>Country:</strong> ${props.country}</p>
          <p><strong>Last known eruption:</strong> ${props.last_eruption}</p>
          <p><strong>Elevation:</strong> ${props.elevation}m</p>
          ${props.is_submarine ? '<p class="submarine">SUBMARINE VOLCANO</p>' : ''}
        </div>
      `);
    }
  });
}

// ============================================================================
// FILTERING FUNCTIONS
// ============================================================================

/**
 * Generic function to filter volcanoes by year range
 * @param {number} startYear - Start year of the range (inclusive)
 * @param {number} endYear - End year of the range (inclusive)
 * @returns {Array} Filtered array of volcano features
 */
function filterVolcanoesByYearRange(startYear, endYear) {
  return volcanoData.features.filter(feature => {
    const lastEruption = feature.properties.last_eruption;
    if (lastEruption === 'Unknown') {
      return false;
    }
    const parts = lastEruption.split(' ');
    let eruptionYear = parseInt(parts[0]);
    const era = parts[1];
    if (era === 'BCE') {
      eruptionYear = -eruptionYear;
    }
    return eruptionYear >= startYear && eruptionYear <= endYear;
  });
}

/**
 * Filter volcanoes that erupted recently (2000-current year)
 * @returns {Array} Array of recent volcano features
 */
function filterVolcanoesRecent() {
  const currentYear = new Date().getFullYear();
  return filterVolcanoesByYearRange(2000, currentYear);
}

/**
 * Filter volcanoes that erupted in the 1900s (1900-1999)
 * @returns {Array} Array of 1900s volcano features
 */
function filterVolcanoes1900s() {
  return filterVolcanoesByYearRange(1900, 1999);
}

/**
 * Return all volcano features without filtering
 * @returns {Array} All volcano features
 */
function showAllVolcanoes() {
  return volcanoData.features;
}

// ============================================================================
// ZOOM HANDLING FUNCTIONS
// ============================================================================

/**
 * Updates volcano triangle sizes when the map zoom level changes
 * Maintains consistent visual size across different zoom levels
 */
function updateVolcanoIconSizes() {
  if (!volcanoLayer.value || !props.mapInstance) return;
  const currentZoom = props.mapInstance.getZoom();

  volcanoLayer.value.eachLayer(layer => {
    if (layer instanceof L.Polygon) {
      // Store original coordinates if not already stored
      if (!layer.originalLatLngs) {
        layer.originalLatLngs = layer.getLatLngs()[0].map(point => 
          L.latLng(point.lat, point.lng)
        );
        const bounds = L.latLngBounds(layer.originalLatLngs);
        layer.center = bounds.getCenter();
      }

      // Get updated style based on current visibility settings
      const style = getVolcanoStyle(layer.feature?.properties.last_eruption, isAllVolcanoesVisible.value);

      // Calculate scale factor based on zoom level
      let scale;
      if (currentZoom <= 5) {
        scale = 2.5 - (currentZoom - 2) * 0.35;
      } else if (currentZoom <= 9) {
        const exponent = currentZoom - 5;
        scale = 1.6 * Math.pow(0.45, exponent);
      } else {
        scale = 0.03 * Math.pow(0.5, currentZoom - 10);
      }

      // Ensure minimum scale to keep triangles visible
      scale = Math.max(scale, 0.005);

      // Apply scaling to triangle coordinates
      const scaledLatLngs = layer.originalLatLngs.map(point => {
        const latOffset = point.lat - layer.center.lat;
        const lngOffset = point.lng - layer.center.lng;
        return L.latLng(
          layer.center.lat + latOffset * scale,
          layer.center.lng + lngOffset * scale
        );
      });

      // Update layer with new coordinates and style
      layer.setLatLngs([scaledLatLngs]);
      layer.setStyle(style);
    }
  });
}

// ============================================================================
// MAIN FILTER TOGGLE FUNCTION
// ============================================================================

/**
 * Main function that handles filter changes and updates the map layer
 * Combines selected filters and recreates the volcano layer accordingly
 */
function toggleVolcanoFilter() {
  let filteredFeatures = [];

  requestAnimationFrame(() => {
  // Combine features from selected filters
  if (isRecentVolcanoesVisible.value) {
    filteredFeatures = filteredFeatures.concat(filterVolcanoesRecent());
  }
  if (is1900sVolcanoesVisible.value) {
    filteredFeatures = filteredFeatures.concat(filterVolcanoes1900s());
  }
  if (isAllVolcanoesVisible.value) {
    filteredFeatures = showAllVolcanoes();
  } else if (!isRecentVolcanoesVisible.value && !is1900sVolcanoesVisible.value) {
    filteredFeatures = [];
  }

  // Remove existing layer if present
  if (volcanoLayer.value) {
    props.mapInstance.removeLayer(volcanoLayer.value);
  }

  // Create and add new layer if features are selected
  if (filteredFeatures.length > 0) {
    volcanoLayer.value = createVolcanoLayer(filteredFeatures, isAllVolcanoesVisible.value);
    volcanoLayer.value.addTo(props.mapInstance);
    props.mapInstance.on('zoomend', updateVolcanoIconSizes);
    updateVolcanoIconSizes();
  } else {
    volcanoLayer.value = null;
    props.mapInstance.off('zoomend', updateVolcanoIconSizes);
  }
  });
}

// ============================================================================
// LAYER ACTIVATION/DEACTIVATION HANDLERS
// ============================================================================

/**
 * Handler called when the volcano layer is activated
 * Sets up initial layer state and displays recent volcanoes by default
 */
function onVolcanoLayerActivated() {
  if (!props.riskLayersToggleRef?.isManualToggle) return;
  
  if (!isVolcanoActive.value) {
    if (!volcanoData?.features?.length) {
      return;
    }

    // Initially show recent volcanoes
    const recentVolcanoes = filterVolcanoesRecent();
    volcanoLayer.value = createVolcanoLayer(recentVolcanoes, isAllVolcanoesVisible.value);
    volcanoLayer.value.addTo(props.mapInstance);
    isVolcanoActive.value = true;

    // Calculate bounds based on ALL volcano data for initial view
    const coordinates = volcanoData.features
      .map(f => f.geometry.coordinates)
      .filter(coord => coord.length === 2);
    
    if (coordinates.length > 0) {
      const bounds = L.latLngBounds(
        coordinates.map(coord => [coord[1], coord[0]])
      );
    }

    toggleVolcanoFilter();
  }
}

/**
 * Handler called when the volcano layer is deactivated
 * Cleans up the layer and removes event listeners
 */
function onVolcanoLayerDeactivated() {
  if (isVolcanoActive.value && volcanoLayer.value) {
    props.mapInstance.removeLayer(volcanoLayer.value);
    props.mapInstance.off('zoomend', updateVolcanoIconSizes);
    volcanoLayer.value = null;
    isVolcanoActive.value = false;
  }
}

// ============================================================================
// COMPONENT EXPORTS
// ============================================================================

// Expose functions to parent component
defineExpose({
  onVolcanoLayerActivated,
  onVolcanoLayerDeactivated,
  isVolcanoActive
});
</script>