  <!-- Consent modal -->
  <VueFinalModal v-model="showConsent" :clickToClose="false" :styles="modalStyles">
  <div class="modal-overlay"></div>
  <div class="modal-content consent-modal-content">
    <!-- Show both languages -->
<div class="language-section">
