  </p>
</div>

<div class="language-section">
  <h2>Consent for Session Recording</h2>
  <p>
    As part of a study conducted by TU Darmstadt, this dashboard is being evaluated.
    Your anonymized interactions on the site may be recorded using the open-source tool "OpenReplay" to help improve usability.
    No IP addresses, timestamps, or personal data are stored. You may withdraw at any time by clicking the <span class="inline-abort-button"></span> button 
    in the top-right corner of the map, and all collected session data will be deleted. 
    Your participation is voluntary and you decide whether to consent.
  </p>
</div>

    <div class="button-container">
      <button class="consent-button" @click="acceptTracking">
        Einverstanden & Fortfahren / Accept & Continue
      </button>
      <button class="consent-button" @click="declineTracking">
        Ablehnen / Decline
      </button>
    </div>
  </div>
</VueFinalModal>

  <!-- Interactive functionality overview modal -->
  <VueFinalModal
    v-model="show"
    :clickToClose="false"
    :styles="modalStyles"
  >
    <!-- Modal backdrop overlay -->
    <div class="modal-overlay" @click="closeModal"></div>
    
    <!-- Main modal content -->
    <div class="modal-content interactive-modal">
      <!-- Language picker widget -->
      <div class="lang-picker-widget">
        <div class="lang-picker-container">
          <button 
            class="lang-picker-btn"
            @click="toggleLanguageDropdown"
            :title="getCurrentLanguageName()"
            :class="{ 'expanded': showLanguageDropdown }"
          >
            <span class="globe-icon"></span>
            <div class="lang-details" :class="{ 'visible': showLanguageDropdown }">
              <span class="current-lang-code">{{ currentLanguage.toUpperCase() }}</span>
              <span class="current-lang-name">{{ getCurrentLanguageName() }}</span>
            </div>
            <span class="expand-arrow" :class="{ 'rotated': showLanguageDropdown }"></span>
          </button>
          <div v-if="showLanguageDropdown" class="lang-picker-menu">
            <button 
              v-for="lang in availableLanguages" 
              :key="lang.code"
              @click="changeLanguage(lang.code)"
              :class="['lang-picker-option', { 'selected': currentLanguage === lang.code }]"
              :title="lang.name"
            >
              <span class="option-lang-code">{{ lang.code.toUpperCase() }}</span>
              <span class="option-lang-name">{{ lang.name }}</span>
            </button>
          </div>
        </div>
      </div>

      <!-- Modal header -->
      <h2>{{ translations.welcomeOverview }}</h2>
      <p>{{ translations.belowSummary }}</p>
      
      <!-- Step indicator -->
      <div class="step-indicator">
        <div 
          v-for="(step, index) in steps" 
          :key="index"
          class="step-dot"
          :class="{ active: currentStep === index }"
          @click="setCurrentStep(index)"
        >
          {{ index + 1 }}
        </div>
      </div>
      
      <!-- GIF display area -->
      <div class="gif-container">
        <img 
          :src="getCurrentGif()" 
          :alt="getCurrentStepTitle()"
          class="step-gif"
          :key="currentStep"
        >
      </div>
      
      <!-- Current step content -->
      <div class="step-content">
        <div class="step-title">
          <span class="step-number-large">{{ currentStep + 1 }}</span>
          <h3>{{ getCurrentStepTitle() }}</h3>
        </div>
        <p class="step-description">{{ getCurrentStepDescription() }}</p>
      </div>
      
      <!-- Navigation buttons -->
      <div class="navigation-buttons">
        <button 
          @click="previousStep" 
          :disabled="currentStep === 0"
          class="nav-button prev-button"
        >
           {{ translations.previous || 'Previous' }}
        </button>
        
        <button 
          v-if="currentStep < steps.length - 1"
          @click="nextStep" 
          class="nav-button next-button"
        >
          {{ translations.next || 'Next' }} 
        </button>
        
        <button 
          v-else
          @click="closeModal" 
          class="nav-button close-button-nav"
        >
          {{ translations.getStarted || 'Get Started!' }}
        </button>
      </div>
      
      <!-- Close button (always available) -->
      <button @click="closeModal" class="close-button-x">
        
      </button>
    </div>
  </VueFinalModal>
</template>

<script>
import { ref, computed, onMounted, provide } from 'vue';
import { VueFinalModal } from 'vue-final-modal';
import Tracker from '@openreplay/tracker';

export default {
  name: 'FunctionalityOverview',
  
  components: {
    VueFinalModal
  },
  
  props: {
    currentTranslations: {
      type: Object,
      required: true
    },
    currentLanguage: {
      type: String,
      required: true
    }
  },
  
  setup(props) {
    // Modal visibility states
    const show = ref(false);
    const showConsent = ref(false);
    const hasConsented = ref(false);
    const tracker = ref(null);
    const currentStep = ref(0);
    const showLanguageDropdown = ref(false);

    // Define the steps with their corresponding GIFs and content
    const steps = [
      {
        gif: 'cables.gif',
        titleKey: 'cableInteractionTitle',
        descriptionKey: 'cableInteractionDesc'
      },
      {
        gif: 'landingSite.gif',
        titleKey: 'landingSiteTitle',
        descriptionKey: 'landingSiteDesc'
      },
      {
        gif: 'layers.gif',
        titleKey: 'layerSwitchTitle',
        descriptionKey: 'layerSwitchDesc'
      },
      {
        gif: 'tools.gif',
        titleKey: 'mapToolsTitle',
        descriptionKey: 'mapToolsDesc'
      }
    ];

    // Initialize OpenReplay tracker
    const initializeTracker = () => {
      tracker.value = new Tracker({
        projectKey: 'eMRBZNonYJms799KwZqT',
        ingestPoint: "https://openreplay.dev.peasec.de/ingest",
        privateMode: false,
        canvas: { disableCanvas: false, useAnimationFrame: true, fileExt: 'webp' },
        resourceBaseHref: 'https://ubiquitous-hummingbird-229317.netlify.app',
      });
      
      provide('openreplay-tracker', tracker.value);
      window._trackerInstance = tracker.value;
    };

    // Start tracker if consented
    const startTracker = () => {
      if (tracker.value && !tracker.value.isActive()) {
        try {
          tracker.value.start();
          console.log('[OpenReplay] Tracker started after consent');
        } catch (error) {
          console.error('[OpenReplay] Failed to start tracker:', error);
        }
      }
    };

    // Check for consent on mount
    onMounted(() => {
      initializeTracker();
      
      const consent = localStorage.getItem('openreplay-consent');
      if (consent === 'true') {
        hasConsented.value = true;
        startTracker();
        show.value = true;
      } else {
        showConsent.value = true;
      }
    });

    // Consent handlers
    const acceptTracking = () => {
      localStorage.setItem('openreplay-consent', 'true');
      hasConsented.value = true;
      showConsent.value = false;
      startTracker();
      show.value = true;
    };

    const declineTracking = () => {
      localStorage.setItem('openreplay-consent', 'false');
      hasConsented.value = false;
      showConsent.value = false;
      show.value = true;
    };

    // Method to reset consent (useful for testing)
    const resetConsent = () => {
      localStorage.removeItem('openreplay-consent');
      hasConsented.value = false;
      showConsent.value = true;
      show.value = false;
      if (tracker.value && tracker.value.isActive()) {
        tracker.value.stop();
      }
    };

    // Modal handlers
    const closeModal = () => {
      show.value = false;
      currentStep.value = 0; // Reset to first step
      showLanguageDropdown.value = false;
    };

    const openModal = () => {
      show.value = true;
      currentStep.value = 0; // Start from first step
    };

    // Step navigation
    const nextStep = () => {
      if (currentStep.value < steps.length - 1) {
        currentStep.value++;
      }
    };

    const previousStep = () => {
      if (currentStep.value > 0) {
        currentStep.value--;
      }
    };

    const setCurrentStep = (stepIndex) => {
      if (stepIndex >= 0 && stepIndex < steps.length) {
        currentStep.value = stepIndex;
      }
    };

    // Get current step data
    const getCurrentGif = () => {
      return require(`@/assets/functionalityOverview/${steps[currentStep.value].gif}`);
    };

    const getCurrentStepTitle = () => {
      const titleKey = steps[currentStep.value].titleKey;
      return translations.value[titleKey] || getDefaultTitle(currentStep.value);
    };

    const getCurrentStepDescription = () => {
      const descKey = steps[currentStep.value].descriptionKey;
      return translations.value[descKey] || getDefaultDescription(currentStep.value);
    };

    // Default content fallbacks
    const getDefaultTitle = (stepIndex) => {
      const titles = [
        'Cable Interaction',
        'Landing Site Information',
        'Layer Management',
        'Map Tools'
      ];
      return titles[stepIndex] || 'Step';
    };

    const getDefaultDescription = (stepIndex) => {
  const descriptions = [
    'Click on any cable to view detailed information about that specific cable in the sidebar.',
    'Click on landing sites to explore detailed data and information about specific locations.',
    'Switch between different layers and overlays to inspect various types of data like fishing activity, ocean depth, gas pipelines, and more.',
    'Explore the tools in the top-left corner: switch languages, change cable data sources, and use the distance measuring tool to calculate distances between points on the map.' // Updated description
  ];
  return descriptions[stepIndex] || 'Step description';
};

    // Language management
    const currentLanguage = ref(props.currentLanguage || 'en');
    const availableLanguages = [
      { code: 'en', name: 'English' },
      { code: 'de', name: 'Deutsch' },
      { code: 'es', name: 'Espaol' },
      { code: 'fr', name: 'Franais' },
      { code: 'it', name: 'Italiano' },
      { code: 'zh', name: '' }
    ];

    // Built-in translations
    const builtInTranslations = {
  en: {
    welcomeOverview: 'Welcome to the SDCS Dashboard!',
    belowSummary: 'Learn how to use the core functionalities:',
    cableInteractionTitle: 'Cable Interaction',
    landingSiteTitle: 'Landing Site Information',
    layerSwitchTitle: 'Layer Management',
    mapToolsTitle: 'Map Tools',
    cableInteractionDesc: 'Click on any cable to view detailed information about that specific cable in the sidebar.',
    landingSiteDesc: 'Click on landing sites to explore detailed data and information about specific locations.',
    layerSwitchDesc: 'Switch between different layers and overlays to inspect various types of data like fishing activity, ocean depth, gas pipelines, and more.',
    mapToolsDesc: 'Explore the tools in the top-left corner: switch languages, change cable data sources, and use the distance measuring tool to calculate distances between points on the map.', // Updated description
    previous: 'Previous',
    next: 'Next',
    getStarted: 'Get Started!',
    close: 'Close',
    language: 'Language'
  },
  de: {
    welcomeOverview: 'Willkommen zum SDCS Dashboard!',
    belowSummary: 'Lernen Sie, wie Sie die Kernfunktionen verwenden:',
    cableInteractionTitle: 'Kabel-Interaktion',
    landingSiteTitle: 'Landeplatz-Informationen',
    layerSwitchTitle: 'Layer-Verwaltung',
    mapToolsTitle: 'Karten-Werkzeuge',
    cableInteractionDesc: 'Klicken Sie auf ein Kabel, um detaillierte Informationen ber dieses spezifische Kabel in der Seitenleiste anzuzeigen.',
    landingSiteDesc: 'Klicken Sie auf Landepltze, um detaillierte Daten und Informationen ber bestimmte Standorte zu erkunden.',
    layerSwitchDesc: 'Wechseln Sie zwischen verschiedenen Layern und Overlays, um verschiedene Datentypen wie Fischereiaktivitten, Meerestiefendaten, Gaspipelines und mehr zu untersuchen.',
    mapToolsDesc: 'Entdecken Sie die Werkzeuge in der oberen linken Ecke: Sprachen wechseln, Kabeldatenquellen ndern und das Entfernungsmesswerkzeug verwenden, um Entfernungen zwischen Punkten auf der Karte zu berechnen.', // Updated description
    previous: 'Zurck',
    next: 'Weiter',
    getStarted: 'Los geht\'s!',
    close: 'Schlieen',
    language: 'Sprache'
  },
  es: {
    welcomeOverview: 'Bienvenido al Panel SDCS!',
    belowSummary: 'Aprende a usar las funcionalidades principales:',
    cableInteractionTitle: 'Interaccin con Cables',
    landingSiteTitle: 'Informacin de Sitios de Aterrizaje',
    layerSwitchTitle: 'Gestin de Capas',
    mapToolsTitle: 'Herramientas del Mapa',
    cableInteractionDesc: 'Haz clic en cualquier cable para ver informacin detallada sobre ese cable especfico en la barra lateral.',
    landingSiteDesc: 'Haz clic en los sitios de aterrizaje para explorar datos detallados e informacin sobre ubicaciones especficas.',
    layerSwitchDesc: 'Cambia entre diferentes capas y superposiciones para inspeccionar varios tipos de datos como actividad pesquera, datos de profundidad ocenica, gasoductos y ms.',
    mapToolsDesc: 'Explora las herramientas en la esquina superior izquierda: cambiar idiomas, modificar fuentes de datos de cables y usar la herramienta de medicin de distancia para calcular distancias entre puntos en el mapa.', // Updated description
    previous: 'Anterior',
    next: 'Siguiente',
    getStarted: 'Comenzar!',
    close: 'Cerrar',
    language: 'Idioma'
  },
  fr: {
    welcomeOverview: 'Bienvenue sur le Tableau de Bord SDCS !',
    belowSummary: 'Apprenez  utiliser les fonctionnalits principales :',
    cableInteractionTitle: 'Interaction avec les Cbles',
    landingSiteTitle: 'Informations sur les Sites d\'Atterrissage',
    layerSwitchTitle: 'Gestion des Couches',
    mapToolsTitle: 'Outils de Carte',
    cableInteractionDesc: 'Cliquez sur n\'importe quel cble pour voir des informations dtailles sur ce cble spcifique dans la barre latrale.',
    landingSiteDesc: 'Cliquez sur les sites d\'atterrissage pour explorer des donnes dtailles et des informations sur des emplacements spcifiques.',
    layerSwitchDesc: 'Basculez entre diffrentes couches et superpositions pour inspecter divers types de donnes comme l\'activit de pche, les donnes de profondeur ocanique, les gazoducs, et plus encore.',
    mapToolsDesc: 'Explorez les outils dans le coin suprieur gauche : changer de langue, modifier les sources de donnes de cbles et utiliser l\'outil de mesure de distance pour calculer les distances entre diffrents points sur la carte.', // Updated description
    previous: 'Prcdent',
    next: 'Suivant',
    getStarted: 'Commencer !',
    close: 'Fermer',
    language: 'Langue'
  },
  it: {
    welcomeOverview: 'Benvenuto nella Dashboard SDCS!',
    belowSummary: 'Impara ad usare le funzionalit principali:',
    cableInteractionTitle: 'Interazione con i Cavi',
    landingSiteTitle: 'Informazioni sui Siti di Atterraggio',
    layerSwitchTitle: 'Gestione dei Livelli',
    mapToolsTitle: 'Strumenti della Mappa',
    cableInteractionDesc: 'Clicca su qualsiasi cavo per visualizzare informazioni dettagliate su quel cavo specifico nella barra laterale.',
    landingSiteDesc: 'Clicca sui siti di atterraggio per esplorare dati dettagliati e informazioni su ubicazioni specifiche.',
    layerSwitchDesc: 'Passa tra diversi livelli e sovrapposizioni per ispezionare vari tipi di dati come attivit di pesca, dati di profondit oceanica, gasdotti e altro ancora.',
    mapToolsDesc: 'Esplora gli strumenti nell\'angolo in alto a sinistra: cambiare lingua, modificare le fonti dei dati dei cavi e usare lo strumento di misurazione distanza per calcolare le distanze tra diversi punti sulla mappa.', // Updated description
    previous: 'Precedente',
    next: 'Successivo',
    getStarted: 'Inizia!',
    close: 'Chiudi',
    language: 'Lingua'
  },
  zh: {
    welcomeOverview: 'SDCS',
    belowSummary: '',
    cableInteractionTitle: '',
    landingSiteTitle: '',
    layerSwitchTitle: '',
    mapToolsTitle: '',
    cableInteractionDesc: '',
    landingSiteDesc: '',
    layerSwitchDesc: '',
    mapToolsDesc: '', // Updated description
    previous: '',
    next: '',
    getStarted: '',
    close: '',
    language: ''
  }
};

    // Language dropdown functions
    const toggleLanguageDropdown = () => {
      showLanguageDropdown.value = !showLanguageDropdown.value;
    };

    const getCurrentLanguageName = () => {
      const lang = availableLanguages.find(l => l.code === currentLanguage.value);
      return lang ? lang.name : 'English';
    };

    // Language change handler
    const changeLanguage = (langCode) => {
      currentLanguage.value = langCode;
      showLanguageDropdown.value = false;
    };

    // Computed translations with fallback values
    const translations = computed(() => {
      const current = builtInTranslations[currentLanguage.value] || builtInTranslations.en;
      return {
        ...current,
      };
    });
    
    // Modal styling configuration
    const modalStyles = {
      overlay: {
        zIndex: 1000,
      },
      content: {
        zIndex: 1001,
      },
    };
    
    return {
      show,
      showConsent,
      hasConsented,
      currentStep,
      steps,
      currentLanguage,
      availableLanguages,
      showLanguageDropdown,
      translations,
      modalStyles,
      acceptTracking,
      declineTracking,
      resetConsent,
      closeModal,
      openModal,
      nextStep,
      previousStep,
      setCurrentStep,
      getCurrentGif,
      getCurrentStepTitle,
      getCurrentStepDescription,
      toggleLanguageDropdown,
      getCurrentLanguageName,
      changeLanguage
    };
  },
}
</script>

