/**
Fetches and displays track for a specific vessel within a date range
 */
async function vesselTrack(startDate, endDate) {
  if (!currentTrackedVessel.value) return;

  const extendedStartDate = new Date(startDate);
  const extendedEndDate = new Date(endDate);

  extendedStartDate.setMonth(extendedStartDate.getMonth() - 5);
  extendedEndDate.setMonth(extendedEndDate.getMonth() + 5);

  const baseParams = {
    limit: 1000,
    offset: 0,
    sort: '-start',
    vessels: [currentTrackedVessel.value],
    'start-date': formatDate(extendedStartDate),
    'end-date': formatDate(extendedEndDate),
  };

  try {
    const [portVisitsResponse, loiteringResponse] = await Promise.all([
      axios.get(vesselTrackingUrl, {
        params: {
          ...baseParams,
          'datasets[0]': 'public-global-port-visits-c2-events:latest'
        }
      }),
      axios.get(vesselTrackingUrl, {
        params: {
          ...baseParams,
          'datasets[0]': 'public-global-loitering-events:latest'
        }
      })
    ]);

    // Combine and sort the events from both responses
    const allEvents = [
      ...portVisitsResponse.data.entries.map(entry => ({
        ...entry,
        type: 'Port Visit',
        speed: entry.vessel?.speed !== undefined ? msToKnots(entry.vessel.speed) : null
      })),
      ...loiteringResponse.data.entries.map(entry => ({
        ...entry,
        type: 'Loitering',
        speed: entry.vessel?.speed !== undefined ? msToKnots(entry.vessel.speed) : null
      }))
    ].sort((a, b) => new Date(b.start) - new Date(a.start));

    const formattedEvents = allEvents.map(entry => ({
      start: entry.start,
      end: entry.end,
      position: {
        ...entry.position,
        speed: entry.vessel?.speed ? msToKnots(entry.vessel.speed) : null
      },
      speed: entry.speed,
      flag: entry.vessel.flag,
      name: entry.vessel.name,
      mmsi: entry.vessel.ssvid,
      type: entry.type
    }));

    interpolatedEvents.value = generateInterpolatedEvents(formattedEvents);
    drawVesselTrack(interpolatedEvents.value);
  } catch (error) {
    console.error("Error fetching vessel track data:", error);
  }
}

