/**
Draws the vessel track on the map as a polyline with arrowheads
 */
const drawVesselTrack = (trackData) => {
  let mapInstanceToUse = props.mapInstance;
  
  if (!mapInstanceToUse && props.cableStore) {
    mapInstanceToUse = props.cableStore.getMapInstance;
  }
  
  if (!trackData || trackData.length === 0) {
    return;
  }
  
  const startDate = new Date(formattedStartDate.value);
  const endDate = new Date(formattedEndDate.value);
  
  if (isNaN(startDate) || isNaN(endDate)) {
    console.error("Invalid date range provided:", formattedStartDate.value, formattedEndDate.value);
    return;
  }
  
  // Filter the track data within the date range
  const filteredData = trackData.filter(event => {
    const eventStartDate = new Date(event.start);
    const eventEndDate = new Date(event.end);
    return (
      (eventStartDate >= startDate && eventStartDate <= endDate) ||
      (eventEndDate >= startDate && eventEndDate <= endDate)
    );
  });
    
  if (filteredData.length === 0) {
    console.warn("No track data within the specified date range.");
    return;
  }
  
  // Remove existing vessel track layer if it exists
  if (vesselTrackLayer.value && mapInstanceToUse.hasLayer(vesselTrackLayer.value)) {
    mapInstanceToUse.removeLayer(vesselTrackLayer.value);
  }
  
  // Get the track points
  const trackPoints = filteredData.map(event => {
    if (event.isInterpolated) {
      return [event.position.lat, event.position.lon];
    }
    return [event.position.lat, event.position.lon];
  }).reverse();
    
  try {
    vesselTrackLayer.value = L.polyline(trackPoints, {
      color: '#ad7dad',
      weight: 3,
      opacity: 0.7
    }).arrowheads({
      size: '20px',
      yawn: 30,
      fill: true
    }).addTo(mapInstanceToUse);
    
    // Fit the map to the bounds of the track
    if (vesselTrackLayer.value.getBounds) {
      mapInstanceToUse.fitBounds(vesselTrackLayer.value.getBounds());
    }
  } catch (error) {
    console.error("Error creating polyline:", error);
  }
};

