/**
Interpolates between two positions based on a fraction
 */
const interpolatePosition = (start, end, fraction) => {
  return {
    lat: start.lat + (end.lat - start.lat) * fraction,
    lon: start.lon + (end.lon - start.lon) * fraction,
    speed: start.speed && end.speed ? start.speed + (end.speed - start.speed) * fraction : null
  };
};

/**
Generates interpolated events between actual vessel events
 */
const generateInterpolatedEvents = (events) => {
  if (!events || events.length < 2) return events;

  const allEvents = [];
  const visibleTimeRange = {
    start: startDate.value.getTime(),
    end: endDate.value.getTime()
  };

  // Helper function to check if a time falls within the visible range
  const isTimeInRange = (time) => {
    const timestamp = new Date(time).getTime();
    return timestamp >= visibleTimeRange.start && timestamp <= visibleTimeRange.end;
  };

  // Helper function to get the visible portion of an interpolation range
  const getVisibleInterpolationRange = (startTime, endTime) => {
    return {
      start: Math.max(startTime, visibleTimeRange.start),
      end: Math.min(endTime, visibleTimeRange.end)
    };
  };

  for (let i = 0; i < events.length - 1; i++) {
    const startEvent = events[i];
    const endEvent = events[i + 1];
    const startTime = new Date(startEvent.end).getTime();
    const endTime = new Date(endEvent.start).getTime();

    // Add the start event if it's in range
    if (isTimeInRange(startEvent.start) || isTimeInRange(startEvent.end)) {
      allEvents.push(startEvent);
    }

    // Handle interpolation between events
    const duration = endTime - startTime;
    if (duration > interpolationInterval && (isTimeInRange(startTime) || isTimeInRange(endTime) || (startTime < visibleTimeRange.end && endTime > visibleTimeRange.start))) {
      const visibleRange = getVisibleInterpolationRange(startTime, endTime);
      const visibleDuration = visibleRange.end - visibleRange.start;

      if (visibleDuration > 0) {
        // Calculate number of interpolation steps based on the visible duration
        const steps = Math.floor(visibleDuration / interpolationInterval);
        for (let step = 0; step <= steps; step++) {
          const currentTime = visibleRange.start + stepinterpolationInterval;
          const fraction = (currentTime - startTime) / duration;
          if (currentTime <= visibleRange.end) {
            const interpolatedPos = interpolatePosition(startEvent.position, endEvent.position, fraction);
            allEvents.push({
              isInterpolated: true,
              time: new Date(currentTime).toISOString(),
              position: interpolatedPos,
              startEvent,
              endEvent,
              speed: interpolatedPos.speed
            });
          }
        }
      }
    }
  }

  // Add the last event if it's in range
  if (events.length > 0 && isTimeInRange(events[events.length - 1].start)) {
    allEvents.push(events[events.length - 1]);
  }

  return allEvents.sort((a, b) => {
    const timeA = a.isInterpolated ? new Date(a.time).getTime() : new Date(a.start).getTime();
    const timeB = b.isInterpolated ? new Date(b.time).getTime() : new Date(b.start).getTime();
    return timeA - timeB;
  });
};

