/**
Handles entering vessel tracking mode for a specific vessel
 */
const viewVesselTrack = async(vessel) => {
  try {
    if (!vessel || !vessel.ssvid) {
      console.error('Invalid vessel data provided to viewVesselTrack');
      return;
    }
    
    vesselTrackingSelected.value = true;
    isVesselTrackingMode.value = true;
    
    // Ensure we have the vessel Id
    if (vesselIds[vessel.ssvid]) {
      currentTrackedVessel.value = vesselIds[vessel.ssvid].vesselId;
    } else {
      console.error('Vessel ID not found for ssvid:', vessel.ssvid);
      return;
    }
    
    // Deselect cables
    props.cableStore.deselectCables();
    
    await nextTick();
    
    // Don't filter vessel positions if it's from sidebar
    if (!vessel.fromSidebar && Array.isArray(localVesselPositions.value)) {
      localVesselPositions.value = localVesselPositions.value.filter(v => v.id === vessel.id);
      emit('update:vesselPositions', localVesselPositions.value);
    }
    
    // Start tracking
    await vesselTrack(formattedStartDate.value, formattedEndDate.value);
    
    emit('update:viewingVesselTrack', true);
    // Adjust cable opacity
    props.cableStore.setAllCablesOpacity(0.15);
  } catch (error) {
    console.error('Error in viewVesselTrack:', error);
    isVesselTrackingMode.value = false;
    vesselTrackingSelected.value = false;
  }
};

/**
Handles exiting vessel tracking mode and cleaning up
 */
const exitVesselTrackingMode = () => {
  isVesselTrackingMode.value = false;
  vesselTrackingSelected.value = false;
  currentTrackedVessel.value = null;
  hoveredMarker.value = null;
  emit('update:hoveredMarker', null);
  
  // Clear the cable ships layer group
  const updatedPositions = props.vesselPositions.filter(vessel => {
    if (vessel.fromSidebar) {
      if (vessel.marker && props.mapInstance.hasLayer(vessel.marker)) {
        props.mapInstance.removeLayer(vessel.marker);
      }
      if (vessel.popup && props.mapInstance.hasLayer(vessel.popup)) {
        props.mapInstance.removeLayer(vessel.popup);        
      }        
    }
    props.layerGroup.clearLayers();
  });

  emit('update:viewingVesselTrack', false);
  vesselTracking();
  props.cableStore.dataCables.forEach(cable => {
    cable.opacity = 1;
  });
  if (vesselTrackLayer.value) {
    props.mapInstance.removeLayer(vesselTrackLayer.value);
    vesselTrackLayer.value = null;
  }
};

/**
