      position: [eventData.position.lat, eventData.position.lon],
      flag: isInterpolated ? eventData.startEvent.flag || 'Unknown' : eventData.flag || 'Unknown',
      tooltip: L.tooltip({
        permanent: false,
        direction: 'top',
        className: 'custom-tooltip',
        offset: [0, -10],
        html: true
      }).setContent(
        isInterpolated ?
          `${eventData.startEvent.name || 'Unknown Vessel'}<br>
          Interpolated Time: ${formatDate(eventData.time)}<br>
${(eventData.speed || eventData.position?.speed) ? `Speed: ${(eventData.speed || eventData.position.speed).toFixed(1)} knots<br>` : ''}
          Between events:<br>
          Start: ${formatDate(eventData.startEvent.end)}<br>
          End: ${formatDate(eventData.endEvent.start)}` :
          `${eventData.name || 'Unknown Vessel'}<br>
          Event: ${eventData.type}<br>
          ${eventData.speed ? `Speed: ${eventData.speed.toFixed(1)} knots<br>` : ''}
          Start: ${formatDate(eventData.start)}<br>
          End: ${formatDate(eventData.end)}`
      )
    };
    
    // Check if vesselPositions has at least one element
    const vesselData = props.vesselPositions.length > 0 
      ? props.vesselPositions[0]
      : {
          id: eventData.mmsi || 'Unknown',
          name: eventData.name || 'Unknown Vessel',
          position: {
            lat: eventData.position.lat,
            lon: eventData.position.lon
          },
          lastSeen: eventData.end || new Date().toISOString(),
          eventType: eventData.type || 'Unknown'
        };
    
    const marker = L.marker(markerData.position, {
      icon: createShipIcon('#636363', markerData.flag, vesselData),
    }).bindTooltip(markerData.tooltip).on('click', () => {
      marker.openTooltip();
    });
    
    hoveredMarker.value = marker;
  } else {
    hoveredMarker.value.setLatLng([eventData.position.lat, eventData.position.lon]);
  }
  
  emit('update:hoveredMarker', hoveredMarker.value);
};

// ==============================================
// UTILITY FUNCTIONS
// ==============================================

/**
Converts vessel IDs object to array
 */
function vesselIdsToArray(vessels) {
  let vesselIds = [];
  for (let key in vessels) {
    vesselIds.push(vessels[key].vesselId);
  }
  return vesselIds;
}

/**
Converts speed from m/s to knots
 */
function msToKnots(metersPerSecond) {
  return metersPerSecond * 1.94384;
}

/**
Function to stop default event propagation
 */
const stopEvent = (e) => {
  e.stopPropagation();
};

/**
Creates a ship icon with flag and color based on last seen date
 */
const createShipIcon = (baseColor = '#ff7800', countryCode = 'Unknown', vessel) => {
  const flagEmoji = countryCode !== 'Unknown' ? getFlagEmoji(countryCode) : '';
  const lastDateSeen = new Date(vessel.lastSeen);
  const currentDate = new Date();
  const daysSinceLastSeen = (currentDate - lastDateSeen) / (1000606024);

  const getColorComponent = (start, end, fraction) => {
    return Math.round(start + fraction * (end - start));
  };

  const calculateColor = (daysSinceLastSeen) => {
    // Use a non-linear function to make color change faster initially
    const fraction = Math.min(Math.pow(daysSinceLastSeen / 60, 1.5), 1);
    const startColor = hexToRgb(baseColor);
    const endColor = hexToRgb('#8B4513'); // Brown
    const r = getColorComponent(startColor.r, endColor.r, fraction);
    const g = getColorComponent(startColor.g, endColor.g, fraction);
    const b = getColorComponent(startColor.b, endColor.b, fraction);
    return rgbToHex(r, g, b);
  };

  const color = calculateColor(daysSinceLastSeen);

  const icon = L.divIcon({
    html: `
      <div class="ship-icon-container">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
          <path d="M20 21c-1.39 0-2.78-.47-4-1.32-2.44 1.71-5.56 1.71-8 0C6.78 20.53 5.39 21 4 21H2v2h2c1.38 0 2.74-.35 4-.99 2.52 1.29 5.48 1.29 8 0 1.26.65 2.62.99 4 .99h2v-2h-2zM3.95 19H4c1.6 0 3.02-.88 4-2 .98 1.12 2.4 2 4 2s3.02-.88 4-2c.98 1.12 2.4 2 4 2h.05l1.89-6.68c.08-.26.06-.54-.06-.78s-.34-.42-.6-.5L20 10.62V6c0-1.1-.9-2-2-2h-3V1H9v3H6c-1.1 0-2 .9-2 2v4.62l-1.29.42c-.26.08-.48.26-.6.5s-.15.52-.06.78L3.95 19zM6 6h12v3.97L12 8 6 9.97V6z" fill="${color}" stroke="white" stroke-width="1"/>
        </svg>
        <span class="ship-flag">${flagEmoji}</span>
      </div>
    `,
    className: 'ship-icon',
    iconSize: [24, 24],
    iconAnchor: [12, 12],
  });
  return icon;
};

/**
Helper function to convert hex to RGB
 */
function hexToRgb(hex) {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result ? {
    r: parseInt(result[1], 16),
    g: parseInt(result[2], 16),
    b: parseInt(result[3], 16)
  } : null;
}

/**
Helper function to convert RGB to hex
 */
function rgbToHex(r, g, b) {
  return `#${((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1)}`;
}

/**
Converts country code to flag emoji
 */
const getFlagEmoji = (countryCode) => {
  // Handle specific cases for country codes
  if (countryCode === 'KOR') { // South Korea
    countryCode = 'KR';
  } else if (countryCode === 'SWE') { // Sweden
    countryCode = 'SE';
  } else if (countryCode === 'ARE') { // United Arab Emirates
    countryCode = 'AE';
  } else if (countryCode === 'ATG') { // Antigua and Barbuda
    countryCode = 'AG';
  } else if (countryCode === 'BRB') { // Barbados
    countryCode = 'BB';
  }

  const codePoints = countryCode
    .toUpperCase()
    .slice(0, 2)
    .split('')
    .map(char => 127397 + char.charCodeAt());
  return String.fromCodePoint(...codePoints);
};

