<!--
CableAgeLayer.vue - Component for visualizing submarine cable ages through color coding

Features:
- Displays a legend showing cable age color coding
- Applies color coding to cables based on their Ready For Service (RFS) year
- Stores and restores original cable colors when toggling the layer
- Supports multiple languages through translations
- Provides activation/deactivation methods for parent components
-->

<template>
  <!-- Cable Age Layer Legend Popup -->
  <div v-if="isActive" class="map-popup">
    <h3>{{ currentTranslations.cableAgeTitle }}</h3>
    <div class="popup-content">
      <div class="legend-grid">
        <div class="legend-item">
          <span class="color-box" style="background-color: #de2512"></span>
          <span class="legend-text">{{ currentTranslations.cableAge20Plus }}</span>
        </div>
        <div class="legend-item">
          <span class="color-box" style="background-color: #ff8c26"></span>
          <span class="legend-text">{{ currentTranslations.cableAge16to20 }}</span>
        </div>
        <div class="legend-item">
          <span class="color-box" style="background-color: #f0cd41"></span>
          <span class="legend-text">{{ currentTranslations.cableAge11to15 }}</span>
        </div>
        <div class="legend-item">
          <span class="color-box" style="background-color: #188c49"></span>
          <span class="legend-text">{{ currentTranslations.cableAge6to10 }}</span>
        </div>
        <div class="legend-item">
          <span class="color-box" style="background-color: #40de83"></span>
          <span class="legend-text">{{ currentTranslations.cableAge0to5 }}</span>
        </div>
        <div class="legend-item">
          <span class="color-box" style="background-color: #808080"></span>
          <span class="legend-text">{{ currentTranslations.cableAgeFuture }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue';
import { useDataCablesStore } from '@/store/datacables';
import translations from "@/assets/translations/translations.json";

// ==============================================
// COMPONENT PROPS
// ==============================================

const props = defineProps({
  currentLanguage: {
    type: String,
    required: true
  }
});

// ==============================================
// STORE & STATE
// ==============================================

const cableStore = useDataCablesStore();
const isActive = ref(false);  // Tracks if the cable age layer is active
const originalCableColors = ref<Record<string, string>>({});  // Stores original cable colors

// ==============================================
// COMPUTED PROPERTIES
// ==============================================

/**
Gets translations for the current language
 */
const currentTranslations = computed(() => {
  return translations[props.currentLanguage] || translations.ENGLISH;
});

// ==============================================
// CABLE COLOR MANAGEMENT FUNCTIONS
// ==============================================

/**
Stores the original colors of all cables before applying age-based coloring
 */
function storeOriginalCableColors() {
  cableStore.dataCables.forEach((cable) => {
    if (!originalCableColors.value[cable.name]) {
      originalCableColors.value[cable.name] = cable.color || "#000000";
    }
  });
}

/**
Restores the original colors of all cables
 */
function restoreOriginalCableColors() {
  cableStore.dataCables.forEach((cable) => {
    cable.color = originalCableColors.value[cable.name] || "#000000";
  });
}

/**
Applies color coding to cables based on their age (RFS year)
 */
function applyCableColorsByAge() {
  const currentYear = new Date().getFullYear();

  cableStore.dataCables.forEach((cable) => {
    const rfsYear = new Date(cable.readyForService).getFullYear();

    if (isNaN(rfsYear)) {
      console.error(`Invalid RFS date: ${cable.readyForService}`);
      return;
    }

    const cableAge = currentYear - rfsYear;

    // Apply color based on cable age
    if (cableAge > 20) {
      cable.color = "#DE2512"; // Red (oldest cables)
    } else if (cableAge >= 16) {
      cable.color = "#FF8C26"; // Orange (16-20 years)
    } else if (cableAge >= 11) {
      cable.color = "#F0CD41"; // Yellow (11-15 years)
    } else if (cableAge >= 6) {
      cable.color = "#188c49"; // Darker green (6-10 years)
    } else if (cableAge >= 0) {
      cable.color = "#40DE83"; // Green (0-5 years)
    } else {
      cable.color = "#808080"; // Grey (future RFS)
    }
  });
}

// ==============================================
// LAYER CONTROL FUNCTIONS
// ==============================================

/**
Activates the cable age layer
- Stores original colors
- Applies age-based coloring
- Shows the legend
 */
function activateCableAgeLayer() {
  isActive.value = true;
  storeOriginalCableColors();
  applyCableColorsByAge();
}

/**
Deactivates the cable age layer
- Restores original colors
- Hides the legend
 */
function deactivateCableAgeLayer() {
  isActive.value = false;
  restoreOriginalCableColors();
}

// ==============================================
// COMPONENT EXPOSURES
// ==============================================

defineExpose({
  activateCableAgeLayer,
  deactivateCableAgeLayer
});
</script>