<!--
GlobalRiskMap.vue - Component for displaying various global risk layers on a map

This component provides interactive risk visualization layers including:
- Combined risk
- Fishing activity
- Anchorage density
- Seismic activity
- Volcanic activity
- Terrain ruggedness

Features include:
- Layer switching via radio buttons
- Color-coded risk visualization
- Interactive map layers
-->

<template>
  <!-- POPUP FOR GLOBAL RISK LAYER OVERLAY -->
  <div v-if="isCombinedRiskGroupActive" class="map-popup risk-popup">
    <h3>{{ currentTranslations.riskGlobalTitle }}</h3>
    <div class="popup-content">
      <div class="risk-controls">
        <div class="control-item">
          <input type="radio" id="combined-risk" name="risk-layer-global" :checked="activeRiskLayer === 'combined'" @change="setActiveRiskLayer('combined')" />
          <label for="combined-risk" class="legend-text">{{ currentTranslations.combinedRisk }} {{ currentTranslations.risk }}</label>
        </div>
        <div class="control-item">
          <input type="radio" id="fishing-risk" name="risk-layer-global" :checked="activeRiskLayer === 'fishing'" @change="setActiveRiskLayer('fishing')" />
          <label for="fishing-risk" class="legend-text">{{ currentTranslations.fishingRisk }} ({{ currentTranslations.percent50 }})</label>
        </div>
        <div class="control-item">
          <input type="radio" id="anchorage-risk" name="risk-layer-global" :checked="activeRiskLayer === 'anchorage'" @change="setActiveRiskLayer('anchorage')" />
          <label for="anchorage-risk" class="legend-text">{{ currentTranslations.anchorageRisk }} ({{ currentTranslations.percent30 }})</label>
        </div>
        <div class="control-item">
          <input type="radio" id="earthquakes-risk" name="risk-layer-global" :checked="activeRiskLayer === 'earthquakes'" @change="setActiveRiskLayer('earthquakes')" />
          <label for="earthquakes-risk" class="legend-text">{{ currentTranslations.earthquakesRisk }} ({{ currentTranslations.percent10 }})</label>
        </div>
        <div class="control-item">
          <input type="radio" id="volcanoes-risk" name="risk-layer-global" :checked="activeRiskLayer === 'volcanoes'" @change="setActiveRiskLayer('volcanoes')" />
          <label for="volcanoes-risk" class="legend-text">{{ currentTranslations.volcanoesRisk }} ({{ currentTranslations['percent7.5'] }})</label>
        </div>
        <div class="control-item">
          <input type="radio" id="terrain-risk" name="risk-layer-global" :checked="activeRiskLayer === 'terrain'" @change="setActiveRiskLayer('terrain')" />
          <label for="terrain-risk" class="legend-text">{{ currentTranslations.terrainRisk }} ({{ currentTranslations['percent2.5'] }})</label>
        </div>
      </div>
      
      <div class="risk-gradient">
        <div class="risk-label">{{ currentTranslations.riskLevel }}</div>
        <div class="color-gradient">
          <div style="background:#6DAED4;">1</div>
          <div style="background:#A3D39C;">2</div>
          <div style="background:#FFE08A;">3</div>
          <div style="background:#FDB366;">4</div>
          <div style="background:#F67E4F;">5</div>
          <div style="background:#E8453C;">6</div>
          <div style="background:#B50A2A;">7</div>
          <div style="background:#67001D;">8</div>
        </div>
        <div class="risk-range">
          <span>{{ currentTranslations.low }}</span>
          <span>{{ currentTranslations.high }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref } from 'vue';
import L from 'leaflet';
import 'leaflet.vectorgrid';

// Data imports
import combinedRiskData from '@/assets/riskGlobal/combined_risk_global_ocean.json';
import fishingData from '@/assets/riskGlobal/aggregated_fishing2normalised99.json';
import anchorageData from '@/assets/riskGlobal/aggregated_anchorages2normalised99.json';
import earthquakesData from '@/assets/riskGlobal/aggregated_seismic2normalised99.json';
import volcanoDataChoropleth from '@/assets/riskGlobal/aggregated_volcanoes2normalised99.json';
import terrainDataChoropleth from '@/assets/riskGlobal/aggregated_tri2normalised99.json';

// ==============================================
// PROPS AND REACTIVE STATE
// ==============================================

import { useDataCablesStore } from '@/store/datacables';
const cableStore = useDataCablesStore();
cableStore.setCombinedRiskData(combinedRiskData);

const props = defineProps({
  currentTranslations: {
    type: Object,
    required: false
  },
  mapInstance: {
    type: Object,
    required: false
  },
  riskLayersToggleRef: Object
});

// Reactive state
const isCombinedRiskGroupActive = ref(false);
const activeRiskLayer = ref('combined');

// Layer references
let combinedRiskLayer = null;
let fishingLayer = null;
let anchorageLayer = null;
let earthquakesLayer = null;
let volcanoLayerChoropleth = null;
let terrainLayerChoropleth = null;

// Workaround for click events
L.DomEvent.fakeStop = function () {
  return true;
}

// ==============================================
// LAYER MANAGEMENT FUNCTIONS
// ==============================================

/**
Deactivates all risk layers
 */
function deactivateAllLayers() {
  onCombinedRiskLayerDeactivated();
  onFishingLayerDeactivated();
  onAnchorageLayerDeactivated();
  onEarthquakesLayerDeactivated();
  onVolcanoLayerChoroplethDeactivated();
  onTerrainLayerChoroplethDeactivated();
}

/**
Sets the active risk layer
@param {string} layerType - The type of layer to activate
 */
function setActiveRiskLayer(layerType) {
  deactivateAllLayers();
  activeRiskLayer.value = layerType;
  
  switch(layerType) {
    case 'combined':
      onCombinedRiskLayerActivated();
      break;
    case 'fishing':
      onFishingLayerActivated();
      break;
    case 'anchorage':
      onAnchorageLayerActivated();
      break;
    case 'earthquakes':
      onEarthquakesLayerActivated();
      break;
    case 'volcanoes':
      onVolcanoLayerChoroplethActivated();
      break;
    case 'terrain':
      onTerrainLayerChoroplethActivated();
      break;
  }
}

// ==============================================
// COMBINED RISK LAYER FUNCTIONS
// ==============================================

/**
Gets color for combined risk value
 */
function getColor(rawRiskValue) {
  return rawRiskValue > 0.3 ? '#67001D' :
         rawRiskValue > 0.085 ? '#B50A2A' :
         rawRiskValue > 0.066 ? '#E8453C' :
         rawRiskValue > 0.02 ? '#F67E4F' :
         rawRiskValue > 0.003 ? '#FDB366' :
         rawRiskValue > 0.0007 ? '#FFE08A' :
         rawRiskValue > 0.0001 ? '#A3D39C' :
                                '#6DAED4';
}

/**
Style function for combined risk layer
 */
function combinedRiskStyleFunction(properties, zoom) {
  return {
    fill: true,
    fillColor: getColor(properties.risk),
    fillOpacity: 0.75,
    stroke: true,
    color: 'white',
    weight: 1,
    opacity: 1,
    dashArray: '3'
  };
}

/**
Creates features for combined risk layer
 */
function createCombinedRiskFeatures(data) {
  const cellSizeHalf = 0.5;
  
  return data.map(item => {
    const [lng, lat, risk] = item;
    
    return {
      type: 'Feature',
      properties: { risk },
      geometry: {
        type: 'Polygon',
        coordinates: [[
          [lng - cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat - cellSizeHalf]
        ]]
      }
    };
  });
}

/**
Activates combined risk layer
 */
function onCombinedRiskLayerActivated() {
  if (combinedRiskLayer) {
    combinedRiskLayer.addTo(props.mapInstance);
    return;
  }

  if (!combinedRiskData?.length) {
    console.error('Combined risk data is empty or invalid.');
    return;
  }

  const features = createCombinedRiskFeatures(combinedRiskData);
  const geojsonData = {
    type: 'FeatureCollection',
    features: features
  };

  combinedRiskLayer = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: {
      sliced: combinedRiskStyleFunction
    },
    interactive: true,
    maxZoom: 18
  });

  combinedRiskLayer.addTo(props.mapInstance);
}

/**
Deactivates combined risk layer
 */
function onCombinedRiskLayerDeactivated() {
  if (combinedRiskLayer) {
    props.mapInstance.removeLayer(combinedRiskLayer);
    combinedRiskLayer = null;
  }
}

// ==============================================
// FISHING LAYER FUNCTIONS
// ==============================================

/**
Gets color for fishing intensity
 */
function getColorFishing(intensity) {
  return intensity > 0.99 ? '#330000' :
         intensity > 0.7 ? '#67001D' :
         intensity > 0.12 ? '#B50A2A' :
         intensity > 0.05 ? '#E8453C' :
         intensity > 0.01 ? '#F67E4F' :
         intensity > 0.001 ? '#FDB366' :
                           '#FFE08A';
}

/**
Style function for fishing layer
 */
function styleFunction(properties, zoom) {
  return {
    fill: true,
    fillColor: getColorFishing(properties.intensity),
    fillOpacity: 0.5,
    stroke: true,
    color: 'white',
    weight: 1,
    opacity: 0.7
  };
}

/**
Activates fishing layer
 */
function onFishingLayerActivated() {
  if (fishingLayer) {
    fishingLayer.addTo(props.mapInstance);
    return;
  }
 
  const features = fishingData.map(item => {
    const [lng, lat, avgIntensity] = item;
    const cellSizeHalf = 0.5;
   
    return {
      type: 'Feature',
      properties: { intensity: avgIntensity },
      geometry: {
        type: 'Polygon',
        coordinates: [[
          [lng - cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat - cellSizeHalf]
        ]]
      }
    };
  });

  const geojsonData = {
    type: 'FeatureCollection',
    features: features
  };

  fishingLayer = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: {
      sliced: styleFunction
    },
    interactive: true,
    maxZoom: 18
  });

  fishingLayer.addTo(props.mapInstance);
}

/**
Deactivates fishing layer
 */
function onFishingLayerDeactivated() {
  if (fishingLayer) {
    props.mapInstance.removeLayer(fishingLayer);
    fishingLayer = null;
  }
}

// ==============================================
// ANCHORAGE LAYER FUNCTIONS
// ==============================================

/**
Gets color for anchorage density
 */
function getColorAnchorage(density) {
  return density > 0.5 ? '#67001D' :
         density > 0.1 ? '#B50A2A' :
         density > 0.05 ? '#E8453C' :
         density > 0.01 ? '#F67E4F' :
         density > 0.001 ? '#FDB366' :
                         '#FFE08A';
}

/**
Style function for anchorage layer
 */
function anchorageStyleFunction(properties, zoom) {
  return {
    fill: true,
    fillColor: getColorAnchorage(properties.density),
    fillOpacity: 0.5,
    stroke: true,
    color: 'white',
    weight: 1,
    opacity: 0.7
  };
}

/**
Activates anchorage layer
 */
function onAnchorageLayerActivated() {
  if (anchorageLayer) {
    anchorageLayer.addTo(props.mapInstance);
    return;
  }

  const features = anchorageData.map(item => {
    const [lng, lat, density] = item;
    const cellSizeHalf = 0.5;
    
    return {
      type: 'Feature',
      properties: { density },
      geometry: {
        type: 'Polygon',
        coordinates: [[
          [lng - cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat - cellSizeHalf]
        ]]
      }
    };
  });

  const geojsonData = {
    type: 'FeatureCollection',
    features: features
  };

  anchorageLayer = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: {
      sliced: anchorageStyleFunction
    },
    interactive: true,
    maxZoom: 18
  });

  anchorageLayer.addTo(props.mapInstance);
}

/**
Deactivates anchorage layer
 */
function onAnchorageLayerDeactivated() {
  if (anchorageLayer) {
    props.mapInstance.removeLayer(anchorageLayer);
    anchorageLayer = null;
  }
}

// ==============================================
// EARTHQUAKES LAYER FUNCTIONS
// ==============================================

/**
Gets color for earthquake magnitude
 */
function getColorEarthquakes(magnitude) {
  return magnitude > 0.778 ? '#67001D' :
         magnitude > 0.725 ? '#B50A2A' :
         magnitude > 0.635 ? '#E8453C' :
         magnitude > 0.56 ? '#F67E4F' :
         magnitude > 0.389 ? '#FDB366' :
                           '#FFE08A';
}

/**
Style function for earthquakes layer
 */
function earthquakesStyleFunction(properties, zoom) {
  return {
    fill: true,
    fillColor: getColorEarthquakes(properties.mag),
    fillOpacity: 0.5,
    stroke: true,
    color: 'white',
    weight: 1,
    opacity: 0.7
  };
}

/**
Activates earthquakes layer
 */
function onEarthquakesLayerActivated() {
  if (earthquakesLayer) {
    earthquakesLayer.addTo(props.mapInstance);
    return;
  }

  const features = earthquakesData.map(item => {
    const [lng, lat, mag] = item;
    const cellSizeHalf = 0.5;

    return {
      type: 'Feature',
      properties: { mag },
      geometry: {
        type: 'Polygon',
        coordinates: [[
          [lng - cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat - cellSizeHalf]
        ]]
      }
    };
  });

  const geojsonData = {
    type: 'FeatureCollection',
    features: features
  };

  earthquakesLayer = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: {
      sliced: earthquakesStyleFunction
    },
    interactive: true,
    maxZoom: 18
  });

  earthquakesLayer.addTo(props.mapInstance);
}

/**
Deactivates earthquakes layer
 */
function onEarthquakesLayerDeactivated() {
  if (earthquakesLayer) {
    props.mapInstance.removeLayer(earthquakesLayer);
    earthquakesLayer = null;
  }
}

// ==============================================
// VOLCANO LAYER FUNCTIONS
// ==============================================

/**
Gets color for volcano density
 */
function getColorVolcano(density) {
  return density > 0.75 ? '#67001D' :
         density > 0.5 ? '#B50A2A' :
         density > 0.25 ? '#E8453C' :
         density > 0.1 ? '#F67E4F' :
         density > 0.01 ? '#FDB366' :
                        '#FFE08A';
}

/**
Style function for volcano layer
 */
function volcanoStyleFunction(properties, zoom) {
  return {
    fill: true,
    fillColor: getColorVolcano(properties.density),
    fillOpacity: 0.5,
    stroke: true,
    color: 'white',
    weight: 1,
    opacity: 0.7
  };
}

/**
Activates volcano layer
 */
function onVolcanoLayerChoroplethActivated() {
  if (volcanoLayerChoropleth) {
    volcanoLayerChoropleth.addTo(props.mapInstance);
    return;
  }

  const features = volcanoDataChoropleth.map(item => {
    const [lng, lat, density] = item;
    const cellSizeHalf = 0.5;
    
    return {
      type: 'Feature',
      properties: { density },
      geometry: {
        type: 'Polygon',
        coordinates: [[
          [lng - cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat - cellSizeHalf]
        ]]
      }
    };
  });

  const geojsonData = {
    type: 'FeatureCollection',
    features: features
  };

  volcanoLayerChoropleth = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: {
      sliced: volcanoStyleFunction
    },
    interactive: true,
    maxZoom: 18
  });

  volcanoLayerChoropleth.addTo(props.mapInstance);
}

/**
Deactivates volcano layer
 */
function onVolcanoLayerChoroplethDeactivated() {
  if (volcanoLayerChoropleth) {
    props.mapInstance.removeLayer(volcanoLayerChoropleth);
    volcanoLayerChoropleth = null;
  }
}

// ==============================================
// TERRAIN LAYER FUNCTIONS
// ==============================================

/**
Gets color for terrain density
 */
function getColorTerrain(density) {
  return density > 0.5188 ? '#67001D' :
         density > 0.2495 ? '#B50A2A' :
         density > 0.1681 ? '#E8453C' :
         density > 0.1211 ? '#F67E4F' :
         density > 0.0635 ? '#FDB366' :
         density > 0.0 ? '#FFE08A' :
                        '#f0f9e7';
}

/**
Style function for terrain layer
 */
function terrainStyleFunction(properties, zoom) {
  return {
    fill: true,
    fillColor: getColorTerrain(properties.density),
    fillOpacity: 0.5,
    stroke: true,
    color: 'white',
    weight: 1,
    opacity: 0.7
  };
}

/**
Activates terrain layer
 */
function onTerrainLayerChoroplethActivated() {
  if (terrainLayerChoropleth) {
    terrainLayerChoropleth.addTo(props.mapInstance);
    return;
  }

  const features = terrainDataChoropleth.map(item => {
    const [lng, lat, density] = item;
    const cellSizeHalf = 0.5;
    
    return {
      type: 'Feature',
      properties: { density },
      geometry: {
        type: 'Polygon',
        coordinates: [[
          [lng - cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat - cellSizeHalf],
          [lng + cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat + cellSizeHalf],
          [lng - cellSizeHalf, lat - cellSizeHalf]
        ]]
      }
    };
  });

  const geojsonData = {
    type: 'FeatureCollection',
    features: features
  };

  terrainLayerChoropleth = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: {
      sliced: terrainStyleFunction
    },
    interactive: true,
    maxZoom: 18
  });

  terrainLayerChoropleth.addTo(props.mapInstance);
}

/**
Deactivates terrain layer
 */
function onTerrainLayerChoroplethDeactivated() {
  if (terrainLayerChoropleth) {
    props.mapInstance.removeLayer(terrainLayerChoropleth);
    terrainLayerChoropleth = null;
  }
}

// ==============================================
// GROUP HANDLERS
// ==============================================

/**
Activates the combined risk layer group
 */
function onCombinedRiskLayerGroupActivated() {
  requestAnimationFrame(() => {
  if (!props.riskLayersToggleRef?.isManualToggle) return;
  isCombinedRiskGroupActive.value = true;
  setActiveRiskLayer('combined');
});
}

/**
Deactivates the combined risk layer group
 */
function onCombinedRiskLayerGroupDeactivated() {
  isCombinedRiskGroupActive.value = false;
  deactivateAllLayers();
}

// ==============================================
// EXPORTS
// ==============================================

defineExpose({
  onCombinedRiskLayerGroupActivated,
  onCombinedRiskLayerGroupDeactivated,
  setActiveRiskLayer,
  deactivateAllLayers
});
</script>

<style>
.color-gradient div {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 12px;
  font-weight: bold;
  text-shadow: 0 0 2px rgba(0,0,0,0.5);
}
</style>