import { gql, useClientHandle } from "@urql/vue";
export async function loadCables() {
  const handle = useClientHandle();
  const result = await handle.useQuery({
    query: gql`
      query loadCables {
        CableSystems {
          id
          abbreviation
          name
          geoData
          readyForService
          outOfService
          color
          CableSystem_X_Landingsites {
            Landingsite {
              id
              name
              country
            }
          }
        }
      }
    `,
  });
  return result;
}

export async function loadLandingSites() {
  const handle = useClientHandle();
  const result = await handle.useQuery({
    query: gql`
      query loadLandingSites {
        Landingsites {
          country
          id
          name
          geoData
        }
      }
    `,
  });
  return result;
}
export const queryCableById = `
  query loadCableById ($id: String!) {
    CableSystems(where: {id: {_eq: $id}}) {
      id
      eu_relations
      endOfService
      constructionCost
      chineseInvolvement
      abbreviation
      length
      name
      outOfService
      readyForService
      seaRegion
      stateOwnership
      CableSystem_X_Landingsites {
        Landingsite {
          id
          name
        }
      }
      Ownerships {
        companyId
        Company {
              id
              name
              nationality  
        }
      }
    }
  }`;

export const queryLandingSiteById = `
  query loadLandingSiteById($id: String!) {
    Landingsites(where: {id: {_eq: $id}}) {
      country
      id
      name
      CableSystem_X_Landingsites {
        CableSystem {
          id
          name
        }
      }
    }
  }
`;
