<template>
  <div class="d-flex full-height resizable-container">
    <!-- SIDEBAR -->
    <div ref="sidebar" class="sidebar-nav" :style="{ width: sidebarWidth + 'px' }">
    <SidebarNav />
    </div>

    <div ref="resizer" class="resizer" @mousedown="startResizing"></div>

    
    <!-- CONTENT -->
    <div class="map-container">
      <router-view name="main"/>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onBeforeUnmount } from 'vue';
import SidebarNav from '@/components/sidebar/TheSidebar.vue';

const sidebarWidth = ref(250); // Initial sidebar width
let isResizing = false;

const startResizing = (event: MouseEvent) => {
  isResizing = true;
  document.addEventListener('mousemove', resizeSidebar);
  document.addEventListener('mouseup', stopResizing);
};

const resizeSidebar = (event: MouseEvent) => {
  if (isResizing) {
    sidebarWidth.value = event.clientX;
  }
};

const stopResizing = () => {
  isResizing = false;
  document.removeEventListener('mousemove', resizeSidebar);
  document.removeEventListener('mouseup', stopResizing);
};

onBeforeUnmount(() => {
  document.removeEventListener('mousemove', resizeSidebar);
  document.removeEventListener('mouseup', stopResizing);
});

</script>

<style scoped>
/* Ensure the entire layout takes full viewport height */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

/* Ensure the main container takes full height and uses flexbox */
.full-height {
  height: 100vh; /* Full viewport height */
}

.d-flex {
  display: flex; /* Flexbox layout for the sidebar and main content */
}

/* SidebarNav component should be styled to take up the necessary width */
.sidebar-nav {
  min-width: 320px; /* or any width you need for the sidebar */
  background-color: #f8f9fa; /* Background color for the sidebar */
}

/* The map container should take up all remaining space */
.map-container {
  flex-grow: 1; /* Take up the remaining space next to the sidebar */
  height: 100%; /* Ensure it takes full height */
  position: relative; /* Necessary for Leaflet or other map controls */
}
</style>
