<!--
ElevationProfile.vue - Displays an elevation profile for geographic data

Features:
- Shows elevation profile for cable routes
- Supports fullscreen mode
- Fetches ocean depth data from GEBCO WMS service
- Handles complex cable segments with interpolation
- Responsive design that adapts to other map layers
-->

<template>
  <div class="elevation-container">
    <div class="button-group" :class="{'has-two-buttons': showProfile}">
      <button @click="showElevationProfile" class="btn-elevation" :disabled="isLoading">
        <span v-if="!isLoading && !fetchingPoints">Elevation Profile</span>
        <span v-else-if="fetchingPoints">Fetching points: {{fetchedPoints}}/{{totalPoints}}</span>
        <span v-else>Loading...</span>
      </button>
      <button v-if="showProfile" @click="toggleFullscreen" class="btn-elevation" :class="{'active': isFullscreen}">
        <span v-if="!isFullscreen">Expand View</span>
        <span v-else>Minimize View</span>
      </button>
    </div>
    <div v-if="fetchingPoints" class="progress-bar-container">
      <div class="progress-bar" :style="{width: `${(fetchedPoints/totalPoints)*100}%`}"></div>
    </div>
    <div id="elevation-div" class="elevation-div" :class="oceanDepthFullscreenClass"></div>
  </div>
</template>

<script setup lang="ts">
import { ref, watch, onBeforeUnmount, computed, onMounted } from 'vue';
import L from 'leaflet';
import * as d3 from 'd3';
import axios from 'axios';
window.d3 = d3;
import '@raruto/leaflet-elevation';
import '@raruto/leaflet-elevation/dist/leaflet-elevation.css';
import 'leaflet/dist/leaflet.css';

// ==============================================
// Component Props
// ==============================================
const props = defineProps<{
  mapInstance: any;
  geoData: any;
}>();

// ==============================================
// Reactive State
// ==============================================
const showProfile = ref(false);
const isLoading = ref(false);
const isFullscreen = ref(false);
const fetchingPoints = ref(false);
const fetchedPoints = ref(0);
const totalPoints = ref(0);
const isComponentMounted = ref(true);
const resizeObserver = ref<ResizeObserver | null>(null);
const isOceanDepthActive = ref(false);
const isCableShipsActive = ref(false);
const isAnchorageActive = ref(false);

// ==============================================
// Leaflet Elevation Control Instances
// ==============================================
let currentElevationControl: any = null;
let currentElevationLayer: any = null;
let resizeTimeout: ReturnType<typeof setTimeout> | null = null;

// ==============================================
// Computed Properties
// ==============================================
const oceanDepthFullscreenClass = computed(() => {
  const baseClasses = {
    fullscreen: isFullscreen.value,
    "ocean-depth-fullscreen": isFullscreen.value && isOceanDepthActive.value,
    "anchorage-fullscreen": isFullscreen.value && isAnchorageActive.value,
    "cable-ships-fullscreen": isFullscreen.value && isCableShipsActive.value,
    "elevation-detached": true,
  };
  return baseClasses;
});

// ==============================================
// Elevation Control Configuration
// ==============================================
const elevationOptions = {
  theme: "lightblue-theme",
  detached: true,
  elevationDiv: "#elevation-div",
  followMarker: false,
  reverseCoords: false,
  autofitBounds: false,
  imperial: false,
  altitude: false,
  distance: false,
  summary: false,
  ruler: false,
  legend: false,
  edgeScale: true,
  hotline: true,
  preferCanvas: true,
  waypoints: false,
  onRemove: function(this: any) {
    if (resizeObserver.value) {
      resizeObserver.value.disconnect();
      resizeObserver.value = null;
    }
    if (this._onRemove) {
      this._onRemove();
    }
  }
};

// ==============================================
// Lifecycle Hooks
// ==============================================
onMounted(() => {
  if (props.mapInstance?._container) {
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.type === "attributes" && mutation.attributeName === "class") {
          const classList = props.mapInstance._container.classList;
          isOceanDepthActive.value = classList.contains("ocean-depth-active");
          isAnchorageActive.value = classList.contains("anchorage-active");
          isCableShipsActive.value = classList.contains("cable-ships-active");
        }
      });
    });

    observer.observe(props.mapInstance._container, {
      attributes: true,
      attributeFilter: ["class"],
    });

    const classList = props.mapInstance._container.classList;
    isOceanDepthActive.value = classList.contains("ocean-depth-active");
    isAnchorageActive.value = classList.contains("anchorage-active");
    isCableShipsActive.value = classList.contains("cable-ships-active");

    isComponentMounted.value = true;

    onBeforeUnmount(() => {
      observer.disconnect();
    });
  }
});

onBeforeUnmount(() => {
  isComponentMounted.value = false;
  cleanupElevationProfile(true);
});

// ==============================================
// Watchers
// ==============================================
watch(oceanDepthFullscreenClass, () => {
  if (currentElevationControl) {
    cleanupElevationProfile();
    showElevationProfile();
  }
});

watch(() => props.geoData, () => {
  if (showProfile.value) {
    cleanupElevationProfile(true);
  }
});

// ==============================================
// Helper Types
// ==============================================
interface Point {
  lat: number;
  lng: number;
}

interface Segment {
  points: Point[];
  used?: boolean;
}

// ==============================================
// Safe Elevation Control Wrapper
// ==============================================
class SafeElevationControl extends (L.Control as any).Elevation {
  constructor(options: any) {
    super(options);
    this._originalResizeChart = this._resizeChart;
    this._resizeChart = this._safeResizeChart.bind(this);
  }

  _safeResizeChart() {
    try {
      if (!this._container || !this._map || !this._data || !this._data.length) return;
      const chart = this._chart;
      const area = this._area;
      if (!chart || !area) return;
      if (typeof chart.select !== 'function' || typeof area.select !== 'function') return;
      if (!document.body.contains(this._container)) return;
      if (!this._map._container || !document.body.contains(this._map._container)) return;
      if (typeof this._originalResizeChart === 'function') this._originalResizeChart();
    } catch (error) {
      console.warn('Safe resize handling prevented error:', error);
    }
  }

  onAdd(map: L.Map) {
    try {
      if (!map || !map._container) return this;
      const result = super.onAdd(map);
      if (!this._container) return result;
      if (typeof this._initEvents === 'function') this._initEvents();
      return result;
    } catch (error) {
      console.warn('Error during elevation control addition:', error);
      return this;
    }
  }

  onRemove(map: L.Map) {
    try {
      if (resizeTimeout) {
        clearTimeout(resizeTimeout);
        resizeTimeout = null;
      }

      if (this._container) {
        const listeners = ['mousemove', 'mouseout', 'mouseover', 'click'];
        listeners.forEach(event => this._container.removeEventListener(event, this._mousemoveHandler));
      }

      this._chart = null;
      this._area = null;
      this._data = null;
      super.onRemove(map);
    } catch (error) {
      console.warn('Error during elevation control removal:', error);
    }
  }
}

// ==============================================
// Core Functions
// ==============================================
const toggleFullscreen = () => {
  isFullscreen.value = !isFullscreen.value;
  if (currentElevationControl) {
    cleanupElevationProfile();
    elevationOptions.height = isFullscreen.value ? 400 : 300;
    showElevationProfile();
  }
};

const showElevationProfile = async () => {
  if (!props.geoData || !isComponentMounted.value) return;
  isLoading.value = true;
  showProfile.value = true;

  try {
    removeCurrentElevationControl();
    const geoJson = await convertToGeoJSON(props.geoData);
    
    if (!isComponentMounted.value) return;
    elevationOptions.height = isFullscreen.value ? 600 : 300;
    
    if (!window.d3) window.d3 = d3;
    currentElevationControl = new SafeElevationControl(elevationOptions).addTo(props.mapInstance);

    if (currentElevationControl) {
      currentElevationControl.load(geoJson);
      setupResizeHandling();
    }
  } catch (error) {
    console.error('Error loading elevation profile:', error);
  } finally {
    isLoading.value = false;
  }
};

// ==============================================
// Data Processing Functions
// ==============================================
async function fetchOceanDepth(lat: number, lng: number): Promise<number> {
  try {
    const minx = lng - 0.0001;
    const miny = lat - 0.0001;
    const maxx = lng + 0.0001;
    const maxy = lat + 0.0001;
    const url = `https://wms.gebco.net/mapserv?service=WMS&version=1.3.0&request=GetFeatureInfo&layers=GEBCO_LATEST_2&styles=&bbox=${minx},${miny},${maxx},${maxy}&width=101&height=101&crs=EPSG:4326&query_layers=GEBCO_LATEST_2&info_format=text/plain&i=50&j=50`;
    const response = await axios.get(url);
    const searchString = "value_list";
    const searchIndex = response.data.indexOf(searchString);
    const extractedText = response.data.substring(searchIndex + searchString.length + 4, response.data.length - 2);
    fetchedPoints.value++;
    return parseFloat(extractedText);
  } catch (error) {
    fetchedPoints.value++;
    return 0;
  }
}

const convertToGeoJSON = async (coordinates: any) => {
  const rawCoordinates = JSON.parse(JSON.stringify(coordinates));
  const unsortedSegments = [...rawCoordinates[0]].filter(Array.isArray);
  const sortedSegments = sortCableSegments(unsortedSegments);
  const maxDistanceThreshold = 0.50;
  const minPointDensity = 10;

  totalPoints.value = calculateTotalPoints(sortedSegments, minPointDensity, maxDistanceThreshold);
  fetchedPoints.value = 0;
  fetchingPoints.value = true;

  const processedCoordinates: ([number, number, number] | null)[] = [];

  for (const segment of sortedSegments) {
    if (Array.isArray(segment) && segment.length > 0) {
      if (processedCoordinates.length > 0) processedCoordinates.push(null);

      const segmentToProcess = shouldInterpolateSegment(segment, minPointDensity)
        ? interpolateSegment(segment, maxDistanceThreshold)
        : segment;

      for (const [lat, lng] of segmentToProcess) {
        const depth = await fetchOceanDepth(lng, lat);
        processedCoordinates.push([lng, lat, depth]);
      }
    }
  }

  fetchingPoints.value = false;

  const multiLineString = processedCoordinates.reduce((acc: number[][][], coord: [number, number, number] | null) => {
    if (coord === null) {
      acc.push([]);
    } else {
      if (acc.length === 0) acc.push([]);
      acc[acc.length - 1].push(coord);
    }
    return acc;
  }, []);

  return {
    type: "Feature",
    properties: {},
    geometry: {
      type: "MultiLineString",
      coordinates: multiLineString,
    },
  };
};

// ==============================================
// Segment Processing Functions
// ==============================================
function sortCableSegments(rawSegments: number[][][]): number[][][] {
  const segments: Segment[] = rawSegments.map(segment => ({
    points: segment.map(([lat, lng]) => ({ lat, lng })),
    used: false
  }));

  const sortedSegments: number[][][] = [];
  if (segments.length === 0) return [];
  
  segments[0].used = true;
  sortedSegments.push(rawSegments[0]);
  let currentEndPoint = segments[0].points[segments[0].points.length - 1];

  while (segments.some(segment => !segment.used)) {
    const nextSegment = findClosestSegment(currentEndPoint, segments);
    if (!nextSegment) break;
    
    const { segmentIndex, shouldReverse } = nextSegment;
    segments[segmentIndex].used = true;
    const segmentToAdd = shouldReverse 
      ? [...rawSegments[segmentIndex]].reverse()
      : rawSegments[segmentIndex];
    
    sortedSegments.push(segmentToAdd);
    currentEndPoint = shouldReverse 
      ? segments[segmentIndex].points[0]
      : segments[segmentIndex].points[segments[segmentIndex].points.length - 1];
  }

  return sortedSegments;
}

function findClosestSegment(
  currentEndPoint: Point,
  segments: Segment[]
): { segmentIndex: number; shouldReverse: boolean } | null {
  let minDistance = Infinity;
  let closestSegmentIndex = -1;
  let shouldReverse = false;

  segments.forEach((segment, index) => {
    if (segment.used) return;

    const distanceToStart = calculateDistance(currentEndPoint, segment.points[0]);
    if (distanceToStart < minDistance) {
      minDistance = distanceToStart;
      closestSegmentIndex = index;
      shouldReverse = false;
    }

    const distanceToEnd = calculateDistance(
      currentEndPoint,
      segment.points[segment.points.length - 1]
    );
    if (distanceToEnd < minDistance) {
      minDistance = distanceToEnd;
      closestSegmentIndex = index;
      shouldReverse = true;
    }
  });

  if (closestSegmentIndex === -1) return null;
  return { segmentIndex: closestSegmentIndex, shouldReverse };
}

function calculateDistance(p1: Point, p2: Point): number {
  return Math.sqrt(Math.pow(p2.lat - p1.lat, 2) + Math.pow(p2.lng - p1.lng, 2));
}

const calculateSegmentLength = (segment: [number, number][]): number => {
  let length = 0;
  for (let i = 0; i < segment.length - 1; i++) {
    const [lat1, lng1] = segment[i];
    const [lat2, lng2] = segment[i + 1];
    length += Math.sqrt((lat2 - lat1) ** 2 + (lng2 - lng1) ** 2);
  }
  return length;
};

const shouldInterpolateSegment = (segment: [number, number][], minPointDensity: number): boolean => {
  const segmentLength = calculateSegmentLength(segment);
  const SHORT_SEGMENT_THRESHOLD = 2.0;
  const SHORT_SEGMENT_MIN_DENSITY = 20;
  const NORMAL_SEGMENT_MIN_DENSITY = 10;
  
  const pointDensity = segment.length / segmentLength;
  const requiredDensity = segmentLength < SHORT_SEGMENT_THRESHOLD 
    ? SHORT_SEGMENT_MIN_DENSITY 
    : NORMAL_SEGMENT_MIN_DENSITY;
    
  return pointDensity < requiredDensity;
};

const interpolateSegment = (segment: [number, number][], maxDistanceThreshold: number): [number, number][] => {
  const interpolatedSegment: [number, number][] = [];
  const segmentLength = calculateSegmentLength(segment);
  const SHORT_SEGMENT_THRESHOLD = 2.0;
  const SHORT_SEGMENT_DISTANCE = 0.25;
  const NORMAL_SEGMENT_DISTANCE = 0.50;
  
  const actualThreshold = segmentLength < SHORT_SEGMENT_THRESHOLD 
    ? SHORT_SEGMENT_DISTANCE 
    : NORMAL_SEGMENT_DISTANCE;
  
  for (let i = 0; i < segment.length - 1; i++) {
    const [lat1, lng1] = segment[i];
    const [lat2, lng2] = segment[i + 1];
    interpolatedSegment.push([lat1, lng1]);
    
    const distance = Math.sqrt((lat2 - lat1) ** 2 + (lng2 - lng1) ** 2);
    if (distance > actualThreshold) {
      const numExtraPoints = Math.ceil(distance / actualThreshold) - 1;
      for (let j = 1; j <= numExtraPoints; j++) {
        const fraction = j / (numExtraPoints + 1);
        interpolatedSegment.push([
          lat1 + fraction * (lat2 - lat1),
          lng1 + fraction * (lng2 - lng1),
        ]);
      }
    }
  }
  
  interpolatedSegment.push(segment[segment.length - 1]);
  return interpolatedSegment;
};

function calculateTotalPoints(segments: number[][][], minPointDensity: number, maxDistanceThreshold: number): number {
  let total = 0;
  for (const segment of segments) {
    if (Array.isArray(segment) && segment.length > 0) {
      const segmentToProcess = shouldInterpolateSegment(segment, minPointDensity)
        ? interpolateSegment(segment, maxDistanceThreshold)
        : segment;
      total += segmentToProcess.length;
    }
  }
  return total;
}

// ==============================================
// Cleanup Functions
// ==============================================
const removeCurrentElevationControl = () => {
  if (currentElevationControl && props.mapInstance) {
    props.mapInstance.removeControl(currentElevationControl);
    currentElevationControl = null;
  }
  if (currentElevationLayer && props.mapInstance) {
    props.mapInstance.removeLayer(currentElevationLayer);
    currentElevationLayer = null;
  }
  
  props.mapInstance.eachLayer((layer: any) => {
    if (layer.options && (
      layer.options.className === 'elevation-profile' ||
      (layer.options.style && layer.options.style.className === 'elevation-profile')
    )) {
      props.mapInstance.removeLayer(layer);
    }
  });
};

const cleanupElevationProfile = (resetFullscreen = false) => {
  if (resizeObserver.value) {
    resizeObserver.value.disconnect();
    resizeObserver.value = null;
  }
  
  if (currentElevationControl) {
    try {
      if (typeof currentElevationControl.clear === 'function') {
        const originalRemoveClass = L.DomUtil.removeClass;
        L.DomUtil.removeClass = function(el: HTMLElement, name: string) {
          safeRemoveClasses(el, name);
        };
        currentElevationControl.clear();
        L.DomUtil.removeClass = originalRemoveClass;
      }
      
      const elevationDiv = document.getElementById('elevation-div');
      if (elevationDiv) elevationDiv.innerHTML = '';
      if (props.mapInstance) props.mapInstance.removeControl(currentElevationControl);
    } catch (error) {
      console.error('Error during elevation control cleanup:', error);
    }
    currentElevationControl = null;
  }

  const selectors = [
    '.elevation-polyline',
    '.elevation-profile',
    '.elevation-line',
    '.elevation-point',
    '.elevation-marker',
    '.elevation-summary',
    '.elevation-tooltip',
    '.leaflet-elevation'
  ];

  selectors.forEach(selector => {
    document.querySelectorAll(selector).forEach(element => {
      if (element instanceof HTMLElement) {
        try {
          if (element.className) safeRemoveClasses(element, element.className);
          element.remove();
        } catch (error) {
          console.error(`Error removing element with selector ${selector}:`, error);
        }
      }
    });
  });

  if (props.mapInstance) {
    props.mapInstance.eachLayer((layer: any) => {
      try {
        if (layer.options && (
          layer.options.className === 'elevation-profile' ||
          (layer.options.style && layer.options.style.className === 'elevation-profile') ||
          layer._path?.classList?.contains('elevation-polyline') ||
          layer.options.className?.includes('elevation-') ||
          layer._container?.classList?.contains('leaflet-elevation')
        )) {
          props.mapInstance.removeLayer(layer);
        }
      } catch (error) {
        console.error('Error removing map layer:', error);
      }
    });
  }

  if (currentElevationLayer && props.mapInstance) {
    try {
      props.mapInstance.removeLayer(currentElevationLayer);
    } catch (error) {
      console.error('Error removing current elevation layer:', error);
    }
    currentElevationLayer = null;
  }

  showProfile.value = false;
  if (resetFullscreen) isFullscreen.value = false;
};

// ==============================================
// Utility Functions
// ==============================================
const safeRemoveClasses = (element: HTMLElement, classNames: string) => {
  if (!element) return;
  const classes = classNames.split(' ').filter(className => className.trim());
  classes.forEach(className => {
    try {
      element.classList.remove(className);
    } catch (error) {
      console.warn(`Failed to remove class: ${className}`, error);
    }
  });
};

const debounce = <T extends (...args: any[]) => any>(
  func: T,
  wait: number
) => {
  let timeout: ReturnType<typeof setTimeout>;
  
  return function executedFunction(
    this: any,
    ...args: Parameters<T>
  ) {
    const later = () => {
      clearTimeout(timeout);
      func.apply(this, args);
    };
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
  };
};

const setupResizeHandling = () => {
  const elevationDiv = document.getElementById('elevation-div');
  if (elevationDiv && isComponentMounted.value) {
    if (resizeObserver.value) resizeObserver.value.disconnect();

    resizeObserver.value = new ResizeObserver(debounce(() => {
      if (currentElevationControl && isComponentMounted.value) {
        try {
          if (currentElevationControl._container && 
              currentElevationControl._map && 
              document.body.contains(currentElevationControl._container) &&
              typeof currentElevationControl._resizeChart === 'function') {
            currentElevationControl._resizeChart();
          }
        } catch (error) {
          console.warn('Safe resize handling:', error);
        }
      }
    }, 250));

    resizeObserver.value.observe(elevationDiv);
  }
};
</script>

<style scoped>
