  background: rgba(99, 179, 237, 0.2);
  color: white;
  border: 1px solid rgba(99, 179, 237, 0.3);
  padding: 0.5rem 1rem;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: all 0.2s ease;
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
}

.btn-elevation:hover:not(:disabled) {
  background: rgba(99, 179, 237, 0.3);
  transform: translateY(-1px);
}

.btn-elevation:disabled {
  background: rgba(74, 85, 104, 0.3);
  cursor: not-allowed;
  opacity: 0.7;
}

.btn-elevation.active {
  background: rgba(128, 90, 213, 0.3);
  border-color: rgba(128, 90, 213, 0.4);
}

.progress-bar-container {
  width: 100%;
  height: 4px;
  background-color: rgba(75, 86, 115, 0.3);
  border-radius: 2px;
  overflow: hidden;
  margin-top: -0.5rem;
}

.progress-bar {
  height: 100%;
  background-color: #63b3ed;
}

.elevation-div {
  height: 300px;
  width: 100%;
  font: 12px/1.5 "Helvetica Neue", Arial, Helvetica, sans-serif;
  background-color: rgba(75, 86, 115, 0.5);
  border-radius: 4px;
  padding: 0.75rem;
}

.elevation-div.fullscreen {
  position: fixed;
  bottom: 1rem;
  left: min(310px, 30%);
  right: 1rem;
  width:  auto !important; 
  height: 300px;
  z-index: 1000;
  background-color: rgba(46, 53, 71, 0.95);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  margin-left: 1.5rem;
}

.elevation-div.fullscreen.ocean-depth-fullscreen {
  width: 60vw !important;
  right: 11vw !important;
}

.elevation-div.fullscreen.anchorage-fullscreen {
  width: 72vw !important;
  right: 11vw !important;
}

.elevation-div.fullscreen.cable-ships-fullscreen {
  width: 70vw !important;
  right: 13vw !important;
}

@keyframes pulse {
  0% { opacity: 0.6; }
  50% { opacity: 1; }
  100% { opacity: 0.6; }
}

.btn-elevation:disabled span {
  animation: pulse 1.5s infinite ease-in-out;
}
</style>

<style>
.leaflet-elevation-toggle {
  background-color: rgba(99, 179, 237, 0.8) !important;
}

.leaflet-elevation-control .area {
  fill: rgba(99, 179, 237, 0.2) !important;
}

.leaflet-elevation-control .line {
  stroke: #63b3ed !important;
}
</style>