<!--
  MaintenanceLayerToggle.vue
  
  A Vue component that provides a toggle interface for cable ship maintenance zone layers on a Leaflet map.
  Displays public and private maintenance zones with color-coded styling, interactive highlighting,
  and zone labels. Supports dynamic positioning based on other active UI elements.
  
  Features:
  - Toggle visibility of public and private maintenance zones
  - Color-coded zones based on abbreviation
  - Interactive zone highlighting on click
  - Popup information display
  - Dynamic positioning for different UI states
  - Zone labels with custom styling
-->

<template>
  <div 
    v-if="isVisible" 
    class="maintenance-layer-toggle-popup"
    :class="{ 'viewing-vessel-track': props.viewingVesselTrack }"
    @click.stop
  >
    <h3 class="maintenance-layer-title">
      {{ props.currentTranslations.maintenanceZonesLayer }}
    </h3>
    
    <div class="layers-container">
      <div 
        v-for="layer in layers" 
        :key="layer.name" 
        class="layer-toggle"
      >
        <input 
          :id="layer.name" 
          type="checkbox" 
          v-model="layer.visible" 
          @change.stop="toggleLayer(layer)" 
          class="layer-checkbox"
        />
        <label 
          :for="layer.name" 
          class="layer-label"
        >
          {{ props.currentTranslations[layer.translatedKey] }}
        </label>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import L from 'leaflet';
import maintenanceZonesJson from '@/assets/cableMaintenanceZones.json';
import privateMaintenanceZonesJson from '@/assets/privateMaintenanceAgreementZones.json';

// ==========================================
// COMPONENT PROPS
// ==========================================

const props = defineProps({
  mapInstance: { type: Object, required: false },
  controlInstance: { type: Object, required: false },
  currentTranslations: { type: Object, required: false },
  viewingVesselTrack: { type: Boolean, required: false },
});

// ==========================================
// REACTIVE VARIABLES
// ==========================================

// Controls component visibility
const isVisible = ref(false);

// Storage for zone labels to manage their lifecycle
const zoneLabels = new Map();

// Array to track currently highlighted layers for cleanup
const highlightedLayers = [];

// ==========================================
// CONFIGURATION DATA
// ==========================================

// Color mapping for different maintenance zone abbreviations
const zoneColors = {
  ACMA: '#FF6B6B',
  NAZ: '#19422a',
  MECMA: '#2cd42c',
  '20CMA': '#FDCB6E',
  SEAIOCMA: '#6C5CE7',
  YZ: '#FF8A5B',
  APMA: '#FFB900',
  AAPMMSAA: '#FF6D9A',
  SPZ: '#A569BD',
  'E-MARINE': '#34D399',
};

// Layer configuration with GeoJSON data and display settings
const layers = ref([
  {
    name: 'PublicMaintenanceZones',
    translatedKey: 'publicMaintenanceZonesLayer',
    defaultName: 'PublicMaintenanceZones',
    visible: false,
    geoJsonLayer: null,
    features: maintenanceZonesJson.features,
  },
  {
    name: 'PrivateMaintenanceZones',
    translatedKey: 'privateMaintenanceZonesLayer',
    defaultName: 'PrivateMaintenanceZones',
    visible: false,
    geoJsonLayer: null,
    features: privateMaintenanceZonesJson.features,
  },
]);

// ==========================================
// STYLING FUNCTIONS
// ==========================================

/**
 * Generates styling for maintenance zones based on their abbreviation
 * @param {Object} feature - GeoJSON feature object
 * @returns {Object} Leaflet style object
 */
const getZoneStyle = (feature) => {
  const abbreviation = feature.properties.abbreviation;
  const defaultStyle = { 
    color: '#000000', 
    weight: 2, 
    opacity: 0.7, 
    fillOpacity: 0.3 
  };
  
  if (zoneColors[abbreviation]) {
    return {
      ...defaultStyle,
      color: zoneColors[abbreviation],
      fillColor: zoneColors[abbreviation],
    };
  }
  
  return defaultStyle;
};

/**
 * Creates a label marker for a maintenance zone feature
 * @param {Object} feature - GeoJSON feature object
 * @returns {L.Marker} Leaflet marker with custom div icon
 */
const createLabel = (feature) => {
  const geoJson = L.geoJSON(feature);
  const bounds = geoJson.getBounds();
  const center = bounds.getCenter();
  const abbreviation = feature.properties.abbreviation;
  const labelColor = zoneColors[abbreviation] || '#000000';
  
  return L.marker(center, {
    icon: L.divIcon({
      className: 'maintenance-zone-label',
      html: `<div style="color:${labelColor};">${feature.properties.abbreviation}</div>`,
      iconSize: null,
    }),
    interactive: false,
  });
};

// ==========================================
// INTERACTION HANDLERS
// ==========================================

/**
 * Highlights all zones with the same abbreviation and shows popup information
 * @param {Object} feature - GeoJSON feature object that was clicked
 * @param {Event} event - Click event object
 */
const highlightZone = (feature, event) => {
  if (!props.mapInstance) return;

  // Clear any previously highlighted layers
  clearHighlightedZones();

  const abbreviation = feature.properties.abbreviation;

  // Highlight all features with the same abbreviation across all layers
  layers.value.forEach((layerConfig) => {
    layerConfig.features.forEach((featureItem) => {
      if (featureItem.properties.abbreviation === abbreviation) {
        const highlightStyle = {
          color: zoneColors[abbreviation] || '#FFD700',
          weight: 4,
          opacity: 1,
          fillOpacity: 0.6,
        };
        const highlightedLayer = L.geoJSON(featureItem, { 
          style: highlightStyle 
        }).addTo(props.mapInstance);
        highlightedLayers.push(highlightedLayer);
      }
    });
  });

  // Create and display popup with zone information
  const popupContent = `
    <strong>${props.currentTranslations.abbreviation}:</strong>${feature.properties.abbreviation}<br/>
    <strong>${props.currentTranslations.name}:</strong>${feature.properties.name}<br/>
  `;
  const popup = L.popup()
    .setLatLng(L.geoJSON(feature).getBounds().getCenter())
    .setContent(popupContent)
    .openOn(props.mapInstance);

};

/**
 * Removes all highlighted zone layers from the map
 */
const clearHighlightedZones = () => {
  highlightedLayers.forEach((layer) => {
    props.mapInstance.removeLayer(layer);
  });
  highlightedLayers.length = 0;
};

// ==========================================
// LAYER MANAGEMENT
// ==========================================

/**
 * Toggles the visibility of a maintenance zone layer and its labels
 * @param {Object} layerConfig - Layer configuration object
 */
const toggleLayer = (layerConfig) => {
  if (!props.mapInstance) return;

  if (layerConfig.visible) {
    // Show layer: create GeoJSON layer if it doesn't exist, or add existing one
    if (!layerConfig.geoJsonLayer) {
      layerConfig.geoJsonLayer = L.geoJSON(layerConfig.features, {
        style: getZoneStyle,
        onEachFeature: (feature, layer) => {
          layer.on('click', (event) => {
            L.DomEvent.stop(event);
            highlightZone(feature, event);
          });
        },
      }).addTo(props.mapInstance);
    } else {
      props.mapInstance.addLayer(layerConfig.geoJsonLayer);
    }

    // Add labels for each feature in the layer
    layerConfig.features.forEach((feature) => {
      const labelKey = `${layerConfig.name}-${feature.properties.abbreviation}`;
      let label = zoneLabels.get(labelKey);
      if (!label) {
        label = createLabel(feature);
        zoneLabels.set(labelKey, label);
      }
      label.addTo(props.mapInstance);
    });
  } else {
    // Hide layer: remove GeoJSON layer from map
    if (layerConfig.geoJsonLayer) {
      props.mapInstance.removeLayer(layerConfig.geoJsonLayer);
    }
    
    // Remove all labels for this layer
    layerConfig.features.forEach((feature) => {
      const labelKey = `${layerConfig.name}-${feature.properties.abbreviation}`;
      const label = zoneLabels.get(labelKey);
      if (label) {
        props.mapInstance.removeLayer(label);
      }
    });
  }
};

/**
 * Resets all layers to hidden state and cleans up map elements
 */
const resetLayers = () => {
  layers.value.forEach((layer) => {
    layer.visible = false;
    
    // Remove GeoJSON layer from map
    if (layer.geoJsonLayer) {
      props.mapInstance.removeLayer(layer.geoJsonLayer);
    }
    
    // Remove all labels for this layer
    layer.features.forEach((feature) => {
      const labelKey = `${layer.name}-${feature.properties.abbreviation}`;
      const label = zoneLabels.get(labelKey);
      if (label) {
        props.mapInstance.removeLayer(label);
      }
    });
  });
  
  // Clear any highlighted zones
  clearHighlightedZones();
};

// ==========================================
// COMPONENT INTERFACE
// ==========================================

// Expose methods for parent component interaction
defineExpose({
  show: () => {
    isVisible.value = true;
  },
  hide: () => {
    isVisible.value = false;
    resetLayers();
  },
  clearHighlightedZones,
});
</script>

