<!--
  RiskLayersToggle.vue
  
  A Vue component that provides a collapsible sidebar panel for toggling visibility of various 
  risk and geographic layers on a map. The component organizes layers into categorized groups 
  (Risk Assessment Maps, Maritime Activity, Natural Features, Cable Infrastructure) and provides 
  an expandable interface for users to control layer visibility with loading states and smooth animations.
  
  Features:
  - Collapsible toggle panel with arrow indicator
  - Grouped layer organization with individual group expansion
  - Layer visibility controls with loading states
  - Translation support for internationalization
  - Responsive design with custom scrollbars
  - Integration with Leaflet map and layer control instances
-->

<template>
  <div v-if="isVisible" class="risk-layers-toggle-popup" :class="{'collapsed':!isExpanded}" @click.stop @mousedown.stop @mouseup.stop>
    <!-- Toggle Tab -->
    <div class="toggle-tab" @click.stop="toggleExpanded">
      <div class="arrow-container">
        <svg class="arrow-icon" viewBox="0 0 24 80" width="10" height="70">
          <path d="M16 10 L8 40 L16 70" stroke="white" stroke-width="2" fill="none" :transform="isExpanded ? 'rotate(180 12 40)' : ''"/>
        </svg>
      </div>
      <span class="tab-text" v-if="!isExpanded">{{props.currentTranslations.riskLayersToggles}}</span>
    </div>
    
    <!-- Panel Content -->
    <div class="panel-content" v-show="isExpanded">
      <h3 class="risk-layers-title">{{props.currentTranslations.riskLayersTitle || 'Risk Layers'}}</h3>
      
      <!-- Clustered Layer Groups -->
      <div class="layers-container">
        <!-- Risk Assessment Maps Group -->
        <div class="layer-group">
          <div class="group-header" @click.stop="toggleGroup('riskMaps')">
            <div class="group-title-container">
              <svg class="group-icon" viewBox="0 0 24 24" width="18" height="18">
                <path d="M20.5 3l-.16.03L15 5.1 9 3.33l-6.64.9c-.21.07-.36.25-.36.48V20.5c0 .28.22.5.5.5l.16-.03L9 18.9l6 2.1 5.64-.9c.21-.07.36-.25.36-.48V3.5c0-.28-.22-.5-.5-.5zM15 19l-6-2.11V5l6 2.11V19z" fill="white"/>
              </svg>
              <div class="group-title">{{getGroupTitle('riskMaps')}}</div>
            </div>
            <svg class="group-arrow" viewBox="0 0 24 24" width="16" height="16">
              <path d="M7 10l5 5 5-5" stroke="white" stroke-width="2" fill="none" :transform="expandedGroups.riskMaps ? 'rotate(180 12 12)' : ''"/>
            </svg>
          </div>
          <div class="group-content" v-show="expandedGroups.riskMaps">
            <div v-for="layer in layerGroups.riskMaps" :key="layer.name" class="layer-toggle" @click.stop>
              <input :id="layer.name" type="checkbox" :checked="layer.visible" @change.stop="toggleLayer(layer)" @click.stop class="layer-checkbox" :disabled="loadingLayers[layer.name]"/>
              <label :for="layer.name" class="layer-label" @click.stop="toggleLayer(layer)">{{getTranslatedName(layer)}}</label>
              <div v-if="loadingLayers[layer.name]" class="layer-loading" @click.stop>
                <div class="loading-spinner"></div>
              </div>
            </div>
          </div>
        </div>
        
        <!-- Maritime Activity Group -->
        <div class="layer-group">
          <div class="group-header" @click.stop="toggleGroup('maritimeActivity')">
            <div class="group-title-container">
              <svg class="group-icon" viewBox="0 0 24 24" width="18" height="18">
                <path d="M20 21c-1.39 0-2.78-.47-4-1.32-2.44 1.71-5.56 1.71-8 0C6.78 20.53 5.39 21 4 21H2v-2h2c1.38 0 2.74-.35 4-.99 2.52 1.29 5.48 1.29 8 0 1.26.65 2.62.99 4 .99h2v2h-2zM3.95 19H4c1.6 0 3.02-.88 4-2 .98 1.12 2.4 2 4 2s3.02-.88 4-2c.98 1.12 2.4 2 4 2h.05l1.89-6.68c.08-.26.06-.54-.06-.78s-.34-.42-.6-.5L20 10.62V6c0-1.1-.9-2-2-2h-3V1H9v3H6c-1.1 0-2 .9-2 2v4.62l-1.29.42c-.26.08-.48.26-.6.5s-.15.52-.06.78L3.95 19zM6 6h12v3.97L12 8 6 9.97V6z" fill="white"/>
              </svg>
              <div class="group-title">{{getGroupTitle('maritimeActivity')}}</div>
            </div>
            <svg class="group-arrow" viewBox="0 0 24 24" width="16" height="16">
              <path d="M7 10l5 5 5-5" stroke="white" stroke-width="2" fill="none" :transform="expandedGroups.maritimeActivity ? 'rotate(180 12 12)' : ''"/>
            </svg>
          </div>
          <div class="group-content" v-show="expandedGroups.maritimeActivity">
            <div v-for="layer in layerGroups.maritimeActivity" :key="layer.name" class="layer-toggle">
              <input :id="layer.name" type="checkbox" :checked="layer.visible" @change.stop="toggleLayer(layer)" class="layer-checkbox" :disabled="loadingLayers[layer.name]"/>
              <label :for="layer.name" class="layer-label">{{getTranslatedName(layer)}}</label>
              <div v-if="loadingLayers[layer.name]" class="layer-loading" @click.stop>
                <div class="loading-spinner"></div>
              </div>
            </div>
          </div>
        </div>
        
        <!-- Natural Features Group -->
        <div class="layer-group">
          <div class="group-header" @click.stop="toggleGroup('naturalFeatures')">
            <div class="group-title-container">
              <svg class="group-icon" viewBox="0 0 24 24" width="18" height="18">
                <path d="M14 6l-3.75 5 2.85 3.8-1.6 1.2C9.81 13.75 7 10 7 10l-6 8h22L14 6z" fill="white"/>
              </svg>
              <div class="group-title">{{getGroupTitle('naturalFeatures')}}</div>
            </div>
            <svg class="group-arrow" viewBox="0 0 24 24" width="16" height="16">
              <path d="M7 10l5 5 5-5" stroke="white" stroke-width="2" fill="none" :transform="expandedGroups.naturalFeatures ? 'rotate(180 12 12)' : ''"/>
            </svg>
          </div>
          <div class="group-content" v-show="expandedGroups.naturalFeatures">
            <div v-for="layer in layerGroups.naturalFeatures" :key="layer.name" class="layer-toggle">
              <input :id="layer.name" type="checkbox" :checked="layer.visible" @change.stop="toggleLayer(layer)" class="layer-checkbox" :disabled="loadingLayers[layer.name]"/>
              <label :for="layer.name" class="layer-label">{{getTranslatedName(layer)}}</label>
              <div v-if="loadingLayers[layer.name]" class="layer-loading">
                <div class="loading-spinner"></div>
              </div>
            </div>
          </div>
        </div>
        
        <!-- Cable Infrastructure Group -->
        <div class="layer-group">
          <div class="group-header" @click.stop="toggleGroup('cableInfo')">
            <div class="group-title-container">
              <svg class="group-icon" viewBox="0 0 24 24" width="18" height="18">
                <path d="M20 5V4c0-.55-.45-1-1-1h-2c-.55 0-1 .45-1 1v1h-1v4c0 .55.45 1 1 1h1v7c0 1.1-.9 2-2 2s-2-.9-2-2V7c0-2.21-1.79-4-4-4S5 4.79 5 7v7H4c-.55 0-1 .45-1 1v4h1v1c0 .55.45 1 1 1h2c.55 0 1-.45 1-1v-1h1v-4c0-.55-.45-1-1-1H7V7c0-1.1.9-2 2-2s2 .9 2 2v10c0 2.21 1.79 4 4 4s4-1.79 4-4v-7h1c.55 0 1-.45 1-1V5h-1z" fill="white"/>
              </svg>
              <div class="group-title">{{getGroupTitle('cableInfo')}}</div>
            </div>
            <svg class="group-arrow" viewBox="0 0 24 24" width="16" height="16">
              <path d="M7 10l5 5 5-5" stroke="white" stroke-width="2" fill="none" :transform="expandedGroups.cableInfo ? 'rotate(180 12 12)' : ''"/>
            </svg>
          </div>
          <div class="group-content" v-show="expandedGroups.cableInfo">
            <div v-for="layer in layerGroups.cableInfo" :key="layer.name" class="layer-toggle">
              <input :id="layer.name" type="checkbox" :checked="layer.visible" @change.stop="toggleLayer(layer)" class="layer-checkbox" :disabled="loadingLayers[layer.name]"/>
              <label :for="layer.name" class="layer-label">{{getTranslatedName(layer)}}</label>
              <div v-if="loadingLayers[layer.name]" class="layer-loading">
                <div class="loading-spinner"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, watch, onMounted, computed } from 'vue';

// ================================
// PROPS DEFINITION
// ================================
const props = defineProps({
  mapInstance: {
    type: Object,
    required: false
  },
  controlInstance: {
    type: Object,
    required: false
  },
  currentTranslations: {
    type: Object,
    required: false
  },
  currentLanguage: {
    type: String,
    required: false
  }
});

// ================================
// REACTIVE STATE
// ================================
// Component visibility and expansion states
const isVisible = ref(false);
const isManualToggle = ref(false);
const isExpanded = ref(false);

// Loading states for individual layers
const loadingLayers = reactive({});

// Group expansion states for collapsible categories
const expandedGroups = reactive({
  riskMaps: false,
  maritimeActivity: false,
  naturalFeatures: false,
  cableInfo: false
});

// ================================
// LAYER DEFINITIONS AND CONFIGURATION
// ================================
// Define the layers with predefined names to match map layers
const layerDefinitions = [
  // Risk Assessment Maps
  {
    name: 'EuropeanRiskMap',
    translatedKey: 'europeanRiskMap',
    defaultName: 'European Risk Map',
    popupSelector: '.custom-popup',
    group: 'riskMaps'
  },
  {
    name: 'GlobalRiskMap',
    translatedKey: 'globalRiskMap',
    defaultName: 'Global Risk Map',
    popupSelector: '.custom-popup',
    group: 'riskMaps'
  },
  // Maritime Activity
  {
    name: 'AggregatedFishingActivity',
    translatedKey: 'fishingActivityLayer',
    defaultName: 'Fishing Activity',
    popupSelector: '.custom-popup',
    group: 'maritimeActivity'
  },
  {
    name: 'AggregatedAnchorage',
    translatedKey: 'anchorageActivityLayer',
    defaultName: 'Anchorage Activity',
    popupSelector: '.custom-popup',
    group: 'maritimeActivity'
  },
  {
    name: 'AggregatedDredging(Europe)',
    translatedKey: 'dredgingActivityLayer',
    defaultName: 'Dredging Activity',
    popupSelector: '.custom-popup',
    group: 'maritimeActivity'
  },
  {
    name: 'MaritimeTrafficDensity(Sep.2024)',
    translatedKey: 'maritimeTrafficDensityLayer',
    defaultName: 'Maritime Traffic Density',
    popupSelector: '.custom-popup',
    group: 'maritimeActivity'
  },
  // Natural Features
  {
    name: 'AggregatedSeismicActivity',
    translatedKey: 'seismicActivityLayer',
    defaultName: 'Seismic Activity',
    popupSelector: '.custom-popup',
    group: 'naturalFeatures'
  },
  {
    name: 'SeabedSubstrates(Europe)',
    translatedKey: 'seabedSubstratesLayer',
    defaultName: 'Seabed Substrates',
    popupSelector: '.custom-popup',
    group: 'naturalFeatures'
  },
  {
    name: 'Volcanoes',
    translatedKey: 'volcanoesLayer',
    defaultName: 'Volcanoes',
    popupSelector: '.custom-popup',
    group: 'naturalFeatures'
  },
  {
    name: 'ShallowWaters(-1to-200m)',
    translatedKey: 'shallowWatersLayer',
    defaultName: 'Shallow Waters',
    popupSelector: '.custom-popup',
    group: 'naturalFeatures'
  },
  // Cable Infrastructure
  {
    name: 'CableAge',
    translatedKey: 'cableAgeLayer',
    defaultName: 'Cable Age',
    popupSelector: '.custom-age-legend',
    group: 'cableInfo'
  },
  {
    name: 'DistancetoCableShips',
    translatedKey: 'distanceToCableShipsLayer',
    defaultName: 'Distance to Cable Ships',
    popupSelector: 'custom-popup',
    group: 'cableInfo'
  }
];

// Group translation keys and default names for internationalization
const groupTranslations = {
  riskMaps: {
    translationKey: 'riskMapsGroupTitle',
    defaultName: 'Risk Assessment Maps'
  },
  maritimeActivity: {
    translationKey: 'maritimeActivityGroupTitle',
    defaultName: 'Maritime Activity'
  },
  naturalFeatures: {
    translationKey: 'naturalFeaturesGroupTitle',
    defaultName: 'Natural Features'
  },
  cableInfo: {
    translationKey: 'cableInfrastructureGroupTitle',
    defaultName: 'Cable Infrastructure'
  }
};

// ================================
// COMPUTED AND REACTIVE DATA
// ================================
// Create layers ref with basic information from layer definitions
const layers = ref(layerDefinitions.map(def => {
  return {
    name: def.name,
    translatedKey: def.translatedKey,
    defaultName: def.defaultName,
    layer: null,
    visible: false,
    popupSelector: def.popupSelector,
    popupElement: null,
    group: def.group
  };
}));

// Organize layers into groups for the UI display
const layerGroups = reactive({
  riskMaps: layers.value.filter(layer => layer.group === 'riskMaps'),
  maritimeActivity: layers.value.filter(layer => layer.group === 'maritimeActivity'),
  naturalFeatures: layers.value.filter(layer => layer.group === 'naturalFeatures'),
  cableInfo: layers.value.filter(layer => layer.group === 'cableInfo')
});

// ================================
// UTILITY FUNCTIONS
// ================================
/**
 * Get translated name for a layer, fallback to default name if translation not available
 * @param {Object} layer - Layer object containing translation key and default name
 * @returns {String} Translated or default layer name
 */
const getTranslatedName = (layer) => {
  const translation = props.currentTranslations[layer.translatedKey];
  return translation || layer.defaultName;
};

/**
 * Get translated group title, fallback to default name if translation not available
 * @param {String} groupName - Name of the layer group
 * @returns {String} Translated or default group title
 */
const getGroupTitle = (groupName) => {
  const groupInfo = groupTranslations[groupName];
  if (!groupInfo) return groupName;
  
  const translation = props.currentTranslations[groupInfo.translationKey];
  return translation || groupInfo.defaultName;
};

// ================================
// UI INTERACTION METHODS
// ================================
/**
 * Toggle expansion state of a specific layer group
 * @param {String} groupName - Name of the group to toggle
 */
const toggleGroup = (groupName) => {
  expandedGroups[groupName] = !expandedGroups[groupName];
};

/**
 * Toggle main panel expansion state
 */
const toggleExpanded = () => {
  isExpanded.value = !isExpanded.value;
};

// ================================
// LAYER MANAGEMENT
// ================================
/**
 * Initialize layers when map and control instances are ready
 * Matches layer definitions with actual map layers from the control instance
 */
const initializeLayers = () => {
  // Check if required instances are available
  if (!props.controlInstance || !props.mapInstance) {
    return;
  }

  // Wait for map to be fully loaded
  if (!props.mapInstance._loaded) {
    setTimeout(initializeLayers, 100);
    return;
  }

  // Find the Risk Layers group in the control instance
  const groupedLayerObj = props.controlInstance._layers.find(layerObj => layerObj.originalName === "Risk Layers");
  if (!groupedLayerObj) {
    console.error("RiskLayers group not found");
    return;
  }

  // Get the grouped layer and validate it
  const groupedLayer = groupedLayerObj.layer;
  if (!groupedLayer || typeof groupedLayer.getLayers !== 'function') {
    console.error("Invalid grouped layer or getLayers method not available");
    return;
  }

  // Match layer definitions with actual map layers
  const allGroupLayers = groupedLayer.getLayers();
  layers.value.forEach((layerConfig, index) => {
    const matchedLayer = allGroupLayers.find(layer => 
      layer.options?.name?.toLowerCase().includes(layerConfig.name.toLowerCase())
    ) || allGroupLayers[index];

    if (matchedLayer) {
      layerConfig.layer = matchedLayer;
      layerConfig.visible = false;
      // Remove layer from map initially (layers start hidden)
      if (props.mapInstance.hasLayer(matchedLayer)) {
        props.mapInstance.removeLayer(matchedLayer);
      }
    }
  });
};

/**
 * Toggle individual layer visibility with loading state management
 * @param {Object} layerConfig - Configuration object for the layer to toggle
 */
const toggleLayer = async (layerConfig) => {
  // Validate layer and map availability
  if (!layerConfig.layer || !props.mapInstance) {
    console.error("Layer or map not available:", layerConfig.name);
    return;
  }
  
  // Set loading state
  loadingLayers[layerConfig.name] = true;
  isManualToggle.value = true;
  
  try {
    await new Promise((resolve) => {
      requestAnimationFrame(() => {
        if (layerConfig.visible) {
          props.mapInstance.removeLayer(layerConfig.layer);
        } else {
          props.mapInstance.addLayer(layerConfig.layer);
        }
        layerConfig.visible = !layerConfig.visible;
        resolve();
      });
    });
  } catch (error) {
    console.error("Error toggling layer:", error);
  } finally {
    setTimeout(() => {
      loadingLayers[layerConfig.name] = false;
      isManualToggle.value = false;
    }, 300);
  }
};

// ================================
// WATCHERS AND LIFECYCLE
// ================================
// Watch for changes in control instance and reinitialize layers
watch(() => props.controlInstance, () => {
  initializeLayers();
}, { immediate: true });

// Watch for changes in map instance and reinitialize if both instances available
watch(() => props.mapInstance, () => {
  if (props.mapInstance && props.controlInstance) {
    initializeLayers();
  }
}, { immediate: true });

// Watch for changes in translations to refresh display
watch(() => props.currentTranslations, () => {
  // Translations updated, component will reactively update display names
}, { deep: true });

// Initialize layers when component is mounted if instances are available
onMounted(() => {
  if (props.mapInstance && props.controlInstance) {
    initializeLayers();
  }
});

// ================================
// COMPONENT EXPOSURE
// ================================
// Expose methods to parent component for external control
defineExpose({
  show: () => {
    isVisible.value = true;
    initializeLayers();
  },
  hide: () => {
    isVisible.value = false;
  },
  isManualToggle: isManualToggle,
  expand: () => {
    isExpanded.value = true;
  },
  collapse: () => {
    isExpanded.value = false;
  }
});
</script>

