<!--
  FishingDatePicker Component
  
  A date range picker component specifically designed for selecting fishing data date ranges.
  Integrates with the Global Fishing Watch API to fetch and display fishing effort data.
  
  Features:
  - Date range selection with validation (max date is today)
  - Automatic API calls when date range changes
  - Custom date formatting (dd/MM/yyyy)
  - API URL modification for proxy routing
  - Fishing layer ready state management
  - Enhanced error handling and retry logic
-->

<template>
  <div v-if="visible" class="date-popup">
    <VueDatePicker
      v-model="internalDate"
      :model-value="internalDate"
      @update:model-value="handleDatePicked"
      :placeholder="placeholder"
      :preview-format="format"
      :format="format"
      :enable-time-picker="false"
      :range="{ partialRange: false }"
      dark="true"
      class="custom-datepicker"
      no-today
      :max-date="new Date()"
    />
  </div>
</template>

<script setup lang="ts">
import { ref, computed, watch } from 'vue';
import axios from 'axios';
import VueDatePicker from "@vuepic/vue-datepicker";
import '@vuepic/vue-datepicker/dist/main.css';

// ========================
// TYPE DEFINITIONS
// ========================

interface Props {
  visible: boolean;
  currentTranslations: Record<string, any>;
}

interface Emits {
  (e: 'update:fishingUrl', url: string): void;
  (e: 'fishing-layer-ready', ready: boolean): void;
}

// ========================
// PROPS & EMITS
// ========================

const props = defineProps<Props>();
const emit = defineEmits<Emits>();

// ========================
// COMPONENT STATE
// ========================

// Date picker state
const internalDate = ref();
const format = "dd/MM/yyyy";
const fishLayerReady = ref(false);

// Updated API configuration - use your proxy endpoint
const apiUrl = 'https://sdcs-api.dev.peasec.de/fishing/v3/4wings/generate-png';
const params = ref({
  color: '#48ba0f',
  interval: 'DAY',
  'datasets[0]': 'public-global-fishing-effort:latest',
  'date-range': '2023-11-01,2023-12-24' // Default date range
});

// ========================
// COMPUTED PROPERTIES
// ========================

/**
 * Dynamic placeholder text based on current translations
 */
const placeholder = computed(() => {
  return props.currentTranslations?.datePlaceholderFishing || 'Select date range';
});

// ========================
// UTILITY METHODS
// ========================

/**
 * Formats a date part (day/month) to ensure two-digit format
 * @param {number} part - The date part to format
 * @returns {string} Formatted date part with leading zero if needed
 */
const formatDatePart = (part: number): string => {
  return part < 10 ? '0' + part : part.toString();
};

/**
 * Enhanced URL replacement to handle different API endpoint variations
 * @param {string} originalUrl - The original API URL
 * @returns {string} Modified URL using the proxy
 */
const modifyApiUrl = (originalUrl: string): string => {
  return originalUrl
    .replace("gateway.api.globalfishingwatch.org", "sdcs-api.dev.peasec.de/fishing")
    .replace("gateway.api.prod-v2.globalfishingwatch.org", "sdcs-api.dev.peasec.de/fishing");
};

// ========================
// API METHODS
// ========================

/**
 * Initializes the fishing watch layer with default parameters
 * Called on component mount and when visibility changes
 */
const initializeFishingWatch = async () => {
  try {
    console.log('Initializing fishing watch with params:', params.value);
    
    const response = await axios.post(apiUrl, null, { 
      params: params.value,
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      timeout: 30000 // 30 second timeout
    });
    
    console.log('API Response:', response.data);
    
    if (response.data && response.data.url) {
      const modifiedUrl = modifyApiUrl(response.data.url);
      console.log('Modified URL:', modifiedUrl);
      
      emit('update:fishingUrl', modifiedUrl);
      fishLayerReady.value = true;
      emit('fishing-layer-ready', true);
    } else {
      throw new Error('Invalid response structure');
    }
  } catch (error) {
    console.error('Failed to initialize fishing watch:', error);
    fishLayerReady.value = false;
    emit('fishing-layer-ready', false);
    
    // Optional: Retry logic
    setTimeout(() => {
      if (!fishLayerReady.value) {
        console.log('Retrying fishing watch initialization...');
        initializeFishingWatch();
      }
    }, 5000);
  }
};

/**
 * Updates fishing data with new date range parameters
 * Called when user selects a new date range
 */
const updateFishingData = async () => {
  try {
    console.log('Updating fishing data with params:', params.value);
    
    const response = await axios.post(apiUrl, null, { 
      params: params.value,
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      timeout: 30000
    });
    
    console.log('Update API Response:', response.data);
    
    if (response.data && response.data.url) {
      const modifiedUrl = modifyApiUrl(response.data.url);
      console.log('Updated Modified URL:', modifiedUrl);
      
      emit('update:fishingUrl', modifiedUrl);
    } else {
      throw new Error('Invalid response structure');
    }
  } catch (error) {
    console.error('Failed to update fishing data:', error);
    
    // Don't set fishLayerReady to false on update errors
    // Just log the error for debugging
  }
};

// ========================
// EVENT HANDLERS
// ========================

/**
 * Handles date picker value changes
 * Formats the selected date range and updates API parameters
 * @param {Date[] | null} dateRange - Selected date range from the picker
 */
const handleDatePicked = (dateRange: Date[] | null) => {
  if (dateRange && dateRange.length === 2) {
    // Format start date
    const firstDay = formatDatePart(dateRange[0].getDate());
    const firstMonth = formatDatePart(dateRange[0].getMonth() + 1);
    
    // Format end date
    const secondDay = formatDatePart(dateRange[1].getDate());
    const secondMonth = formatDatePart(dateRange[1].getMonth() + 1);
    
    // Update API parameters with formatted date range
    const newDateRange = `${dateRange[0].getFullYear()}-${firstMonth}-${firstDay},${dateRange[1].getFullYear()}-${secondMonth}-${secondDay}`;
    params.value['date-range'] = newDateRange;
    
    console.log('Date range updated to:', newDateRange);
    
    // Fetch new fishing data with updated date range
    updateFishingData();
  }
};

// ========================
// LIFECYCLE & WATCHERS
// ========================

// Initialize fishing watch when component is created
initializeFishingWatch();

/**
 * Watch for visibility changes to refresh data if needed
 * Reinitializes fishing watch if component becomes visible and layer isn't ready
 */
watch(() => props.visible, (newVisible) => {
  if (newVisible && !fishLayerReady.value) {
    console.log('Component became visible, reinitializing...');
    initializeFishingWatch();
  }
});
</script>