<!--
  SourcesModal.vue
  
  A modal component that displays data source attribution and licensing information.
  Features an expandable sources section with detailed information about each data provider.
  Supports keyboard navigation (ESC to close) and responsive design.
-->

<template>
  <div>
    <!-- Attribution Footer -->
    <p class="text-[#a0aec0] text-xs">
      Cable data by 
      <a href="https://www2.telegeography.com/" 
         target="_blank" 
         class="text-blue-400 hover:text-blue-300 transition-colors">
        Telegeography
      </a>
      (<a href="https://www.submarinecablemap.com/" 
          target="_blank" 
          class="text-blue-400 hover:text-blue-300 transition-colors">
        cable map
      </a>),
      licensed under 
      <a href="https://creativecommons.org/licenses/by-sa/4.0/" 
         target="_blank" 
         class="text-blue-400 hover:text-blue-300 transition-colors">
        Creative Commons Attribution-ShareAlike 4.0 International
      </a>,
      merged with own data and data from 
      <a href="https://subtelforum.com/" 
         target="_blank" 
         class="text-blue-400 hover:text-blue-300 transition-colors">
        Submarine Telecoms Forum
      </a>
      (<a href="https://subtelforum.com/online-map/" 
          target="_blank" 
          class="text-blue-400 hover:text-blue-300 transition-colors">
        cable map
      </a>) used under fair use.
      
      <!-- Toggle Button for Sources -->
      <button 
        v-if="!showSources"
        @click="showSources = true" 
        class="toggle-button"
      >
        Data Sources
        <svg class="toggle-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 9l-7 7-7-7" />
        </svg>
      </button>
    </p>

    <!-- Expandable Sources Modal -->
    <div v-if="showSources" class="sources-container">
      <div class="sources-content">
        <!-- Modal Header -->
        <div class="sources-header">
          <h3 class="sources-title">Data Sources</h3>
          <button @click="showSources = false" class="close-button">
            <span class="sr-only">Close</span>
            <svg class="close-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2.5" d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>
        
        <!-- Sources List -->
        <div class="sources-list">
          <div v-for="(source, index) in sources" 
               :key="index" 
               class="source-item">
            <h4 class="source-title">{{ source.title }}</h4>
            <div class="source-details">
              <p v-if="source.sourceName" class="source-name">
                Source: {{ source.sourceName }}
              </p>
              <a :href="source.url" 
                 target="_blank" 
                 class="source-url">
                {{ source.url }}
              </a>
              <p v-if="source.description" 
                 class="source-description">
                {{ source.description }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
// Vue composition API imports
import { ref, onMounted, onUnmounted } from 'vue';

// Reactive state
const showSources = ref(false);

// Data sources configuration
const sources = [
  {
    title: 'Cable Source "Telegeography"',
    url: 'https://www2.telegeography.com/',
    description: 'Mixed with own data'
  },
  {
    title: 'Cable Source "Mixed Sources"',
    url: 'https://kis-orca.org/map/',
    description: 'Mixed with telegeography and own data'
  },
  {
    title: 'Seismic Activity',
    url: 'https://earthquake.usgs.gov/earthquakes/map/',
    sourceName: 'U.S. Geological Survey (USGS)'
  },
  {
    title: 'Fishing Activity',
    url: 'https://globalfishingwatch.org/our-apis/documentation#4wings-introduction',
    sourceName: 'Global Fishing Watch'
  },
  {
    title: 'Ocean Depth',
    url: 'https://www.gebco.net/data_and_products/gridded_bathymetry_data/',
    sourceName: 'General Bathymetric Chart of the Oceans (GEBCO)'
  },
  {
    title: 'Anchorage',
    url: 'https://globalfishingwatch.org/datasets-and-code-anchorages/',
    sourceName: 'Global Fishing Watch'
  },
  {
    title: 'Fixed Infrastructure',
    url: 'https://globalfishingwatch.org/our-apis/documentation#get-fixed-infrastructure-in-mvt-http-request',
    sourceName: 'Global Fishing Watch'
  },
  {
    title: 'Seamarks',
    url: 'https://wiki.openstreetmap.org/wiki/Seamarks/Seamark_Objects',
    sourceName: 'OpenStreetMap'
  },
  {
    title: 'EEZ, Territorial Waters',
    url: 'https://marineregions.org/downloads.php',
    sourceName: 'Marine Regions'
  },
  {
    title: 'Gas Pipelines',
    url: 'https://globalenergymonitor.org/projects/global-gas-infrastructure-tracker/',
    sourceName: 'Global Energy Monitor'
  },
  {
    title: 'Cable Ships',
    url: 'https://globalfishingwatch.org/our-apis/documentation#vessels-api',
    sourceName: 'Global Fishing Watch'
  },
  {
    title: 'ITU Broadband Networks',
    url: 'https://bbmaps.itu.int/geonetwork/srv/api/records/f9af598b-da16-4a7a-a757-6cffc02e9565',
    sourceName: 'ITU'
  },
  {
    title: 'Data Centers',
    url: 'https://wiki.openstreetmap.org/wiki/Tag:telecom=data_center',
    sourceName: 'OpenStreetMap' 
  },
  {
    title: 'Map',
    url: 'https://www.maptiler.com/',
    sourceName: 'MapTiler'
  }
];

// Event handlers
const handleKeydown = (event) => {
  if (event.key === 'Escape' && showSources.value) {
    showSources.value = false;
  }
};

// Lifecycle hooks
onMounted(() => {
  document.addEventListener('keydown', handleKeydown);
});

onUnmounted(() => {
  document.removeEventListener('keydown', handleKeydown);
});
</script>

