<!--
CableViewer.vue - Component for displaying detailed information about a submarine cable

Features:
- Displays cable information, landing sites, and ownership details
- Provides ocean depth analysis capabilities
- Includes elevation profile and risk analysis visualizations
- Responsive design for various screen sizes

Dependencies:
- Vue Router for navigation
- Pinia stores for state management
- Leaflet for map interactions
- Urql for GraphQL queries
-->

<template>
  <div class="cable-details-container">
    <!-- Header Section -->
    <div class="header-section">
      <router-link to="/" @click="dataCableStore.deselectCables()" class="back-button">
        <i class="bi bi-arrow-left"></i>
        <span>Back to Cables</span>
      </router-link>
      
      <div class="cable-title">
        <h1>{{ cable?.name || 'Loading...' }}</h1>
        <div v-if="cable" class="status-badge" :class="cable.outOfService ? 'status-offline' : 'status-online'">
          <i :class="['bi', !cable.outOfService ? 'bi-check-circle-fill' : 'bi-x-circle-fill']"></i>
          <span>{{ cable.outOfService ? 'Out of Service' : 'Operational' }}</span>
        </div>
      </div>
    </div>

    <!-- Main Content Grid -->
    <div v-if="cable" class="content-grid">
      <!-- Basic Information Card -->
      <div class="info-card">
        <div class="card-header">
          <i class="bi bi-info-circle"></i>
          <h3>Cable Information</h3>
        </div>
        <div class="card-content">
          <div class="info-row">
            <div class="info-item">
              <span class="label">Total Length</span>
              <span class="value">{{ cable.length?.toLocaleString() }} km</span>
            </div>
            <div class="info-item">
              <span class="label">Service Date</span>
              <span class="value">{{ formatDate(cable.readyForService) }}</span>
            </div>
          </div>
        </div>
      </div>

      <!-- Landing Sites Card -->
      <div v-if="cable.CableSystem_X_Landingsites?.length" class="info-card">
        <div class="card-header">
          <i class="bi bi-geo-alt"></i>
          <h3>Landing Sites</h3>
          <span class="count-badge">{{ cable.CableSystem_X_Landingsites.length }}</span>
        </div>
        <div class="card-content">
          <div class="landing-sites-grid">
            <div 
              v-for="point in cable.CableSystem_X_Landingsites" 
              :key="point.Landingsite.id" 
              class="landing-site-chip"
              @click="landingSitesStore.setSelectLandingSiteById(point.Landingsite.id)"
            >
              <span>{{ point.Landingsite.name }}</span>
            </div>
          </div>
        </div>
      </div>

      <!-- Ownership Card -->
      <div v-if="cable.Ownerships?.length" class="info-card">
        <div class="card-header">
          <i class="bi bi-building"></i>
          <h3>Ownership</h3>
          <span class="count-badge">{{ cable.Ownerships.length }}</span>
        </div>
        <div class="card-content">
          <div class="ownership-list">
            <div v-for="entry in cable.Ownerships" :key="entry.Company.id" class="ownership-item">
              <div class="company-info">
                <span class="company-name">{{ entry.Company.name }}</span>
                <span v-if="entry.Company.nationality" class="nationality">{{ entry.Company.nationality }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Ocean Depth Analysis Card -->
      <div class="info-card analysis-card">
        <div class="card-header">
          <i class="bi bi-water"></i>
          <h3>Ocean Depth Analysis</h3>
        </div>
        <div class="card-content">
          <div class="analysis-controls">
            <div class="control-group">
              <label for="samplePointCount">Sample Points</label>
              <select id="samplePointCount" v-model="samplePointCount" class="select-input">
                <option v-for="count in [100, 200, 500, 1000, 2000, 5000]" :key="count" :value="count">
                  {{ count.toLocaleString() }} points
                </option>
              </select>
            </div>
            
            <button @click="extractDepthInfo" class="primary-button" :disabled="isAnalyzing">
              <i v-if="!isAnalyzing" class="bi bi-search"></i>
              <i v-else class="bi bi-arrow-clockwise spin"></i>
              <span>{{ isAnalyzing ? 'Analyzing...' : 'Analyze Depth' }}</span>
            </button>
          </div>

          <div v-if="filteredDepthAnalysis" class="depth-results">
            <div class="result-item">
              <span class="result-label">Average Depth</span>
              <span class="result-value">{{ Math.round(filteredDepthAnalysis.averageDepth).toLocaleString() }}m</span>
            </div>
            <div class="result-item">
              <span class="result-label">Maximum Depth</span>
              <span class="result-value">{{ Math.round(filteredDepthAnalysis.deepestPoint).toLocaleString() }}m</span>
            </div>
          </div>
        </div>
      </div>

      <!-- Cable Analysis Card -->
      <div class="info-card analysis-card">
        <div class="card-header">
          <i class="bi bi-graph-up"></i>
          <h3>Cable Analysis</h3>
        </div>
        <div class="card-content">
          <div class="analysis-buttons">
            <button 
              @click="toggleElevationProfile" 
              class="analysis-button" 
              :class="{ active: activeAnalysis === 'elevation' }"
              :disabled="isAnalyzing"
            >
              <i class="bi bi-bar-chart"></i>
              <span>{{ activeAnalysis === 'elevation' ? 'Hide' : 'Show' }} Elevation Profile</span>
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Analysis Visualization Components -->
    <div v-if="activeAnalysis" class="analysis-container">
      <ElevationProfile 
        v-if="activeAnalysis === 'elevation'"
        ref="elevationProfileRef"
        :mapInstance="mapInstance" 
        :geoData="geoData"
        :autoStart="true"
      />
      
    </div>

    <!-- Hidden Analysis Utility -->
    <OceanDepthAnalyzer 
      v-if="drawnGeometry"
      ref="oceanDepthAnalyzerRef"
      :mapInstance="mapInstance"
      :drawnGeometry="drawnGeometry"
      :samplePointCount="samplePointCount"
      @depth-analysis-complete="filterDepthAnalysis"
      style="display: none;"
    />
  </div>
</template>

<script setup lang="ts">
import { ref, watch, nextTick } from "vue";
import { onBeforeRouteUpdate, useRoute } from "vue-router";
import { useClientHandle } from "@urql/vue";
import L from 'leaflet';

// Store imports
import { useDataCablesStore } from "@/store/datacables";
import { useLandingSitesStore } from "@/store/landingSites";

// Component imports
import OceanDepthAnalyzer from "@/components/layers/natural/OceanDepthAnalyzer.vue";
import ElevationProfile from '@/components/sidebar/ElevationProfile.vue';

// Service imports
import { queryCableById } from "@/service/hasura";

// Initialize stores and router
const dataCableStore = useDataCablesStore();
const landingSitesStore = useLandingSitesStore();
const route = useRoute();
const handle = useClientHandle();

// Component state
const geoData = ref(null);
const cable = ref();
const drawnGeometry = ref(null);
const isAnalyzing = ref(false);
const filteredDepthAnalysis = ref(null);
const samplePointCount = ref(100);
const activeAnalysis = ref(null); // 'elevation' | 'risk' | null

// Template refs
const oceanDepthAnalyzerRef = ref(null);
const elevationProfileRef = ref(null);

// Map instance from store
const mapInstance = dataCableStore.getMapInstance;

// Watch for cable selection changes
watch(() => dataCableStore.selectCable, () => {
  geoData.value = dataCableStore.cableDetails.geoData;
});

// Watch for cable data changes
watch(() => cable.value, async () => {
  await clearAnalysis();
  drawnGeometry.value = null;
  filteredDepthAnalysis.value = null;
  isAnalyzing.value = false;
  geoData.value = dataCableStore.cableDetails.geoData;
});

// Load initial cable data
let id = route.params.cableId.toString();
if (!dataCableStore.isCableSelected(id)) {
  dataCableStore.setSelectCable(id, true);
}

// Handle route updates
onBeforeRouteUpdate((to) => {
  loadCable(to.params.cableId.toString());
});


/**
Fetches cable data by ID from the GraphQL API
@param id - The cable system ID to load
 */
const loadCable = async (id: string) => {
  const result = await handle.client.query(queryCableById, { id: id }).toPromise();
  if (result.error) return;
  cable.value = result.data.CableSystems[0];
};

/**
Formats a date string to a human-readable format
@param dateString - The date string to format
@returns Formatted date string
 */
function formatDate(dateString: string): string {
  return new Date(dateString).toLocaleDateString('en-US', {
    year: 'numeric', 
    month: 'long', 
    day: 'numeric'
  });
}

/**
Cleans up active analysis components
 */
const cleanupAnalysis = async () => {
  if (activeAnalysis.value === 'elevation' && elevationProfileRef.value) {
    if (typeof elevationProfileRef.value.cleanup === 'function') {
      await elevationProfileRef.value.cleanup();
    }
  } else if (activeAnalysis.value === 'risk' && cableRiskAnalysisRef.value) {
    if (typeof cableRiskAnalysisRef.value.cleanup === 'function') {
      await cableRiskAnalysisRef.value.cleanup();
    }
  }
};

/**
Toggles the elevation profile visualization
 */
const toggleElevationProfile = async () => {
  if (activeAnalysis.value === 'elevation') {
    await clearAnalysis();
  } else {
    await cleanupAnalysis();
    activeAnalysis.value = 'elevation';
    
    await nextTick();
    
    if (elevationProfileRef.value?.showElevationProfile) {
      elevationProfileRef.value.showElevationProfile();
    }
  }
};

/**
Clears all active analysis visualizations
 */
const clearAnalysis = async () => {
  await cleanupAnalysis();
  activeAnalysis.value = null;
};

/**
Extracts depth information for the cable
 */
const extractDepthInfo = async () => {
  let cableGeo = dataCableStore.cableDetails;
  if (!cableGeo?.geoData) return;
  
  isAnalyzing.value = true;
  try {
    const flattenedCoordinates = cableGeo.geoData.flatMap(segment => 
      segment[0].map(coord => [coord[1], coord[0]])
    );

    const geoData = {
      type: 'LineString',
      coordinates: flattenedCoordinates
    };

    const latlngs = geoData.coordinates.map((coord: number[]) => 
      L.latLng(coord[1], coord[0])
    );
    const geometry = L.polyline(latlngs);

    drawnGeometry.value = geometry;
  } finally {
    isAnalyzing.value = false;
  }
};

/**
Filters and stores depth analysis results
@param fullAnalysis - The complete depth analysis results
 */
const filterDepthAnalysis = (fullAnalysis) => {
  filteredDepthAnalysis.value = {
    averageDepth: fullAnalysis.averageDepth,
    deepestPoint: fullAnalysis.deepestPoint,
    samplePoints: fullAnalysis.samplePoints
  };
};

loadCable(id);
</script>

