  filter: brightness(1.8) contrast(1.4) drop-shadow(0 0 2px rgba(255, 255, 255, 0.5));
  box-shadow: 
    0 0 6px rgba(49, 130, 206, 0.6), 
    inset 0 0 3px rgba(255, 255, 255, 0.2);
  transform: scale(1.08);
  z-index: 1000;
  transition: all 0.2s ease-in-out;
}




</style>

