import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'
import SidebarList from "@/components/sidebar/SideBarList.vue";
import CableViewer from '@/components/sidebar/CableViewer.vue';
import LeafletMap from '@/components/LeafletMap.vue';
import LandingsiteViewer from '@/components/sidebar/LandingSiteViewer.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    components: {
      main: LeafletMap,
      sidebar: SidebarList,
    },
  },
  {
    path: '/cables/:cableId',
    name: 'cables',
    components: {
      main: LeafletMap,
      sidebar: CableViewer,
    },
  },
  {
    path: '/landingSites/:landingSiteId',
    name: 'landingSites',
    components: {
      main: LeafletMap,
      sidebar: LandingsiteViewer,
    },
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
