<!--
  TimelineSlider Component
  
  A Vue 3 component that provides an interactive timeline with draggable sliders for selecting date ranges.
  Features include:
  - Dual-handle range slider for date selection
  - Event bars visualization on the timeline
  - Inter-event connection lines with hover interpolation
  - Dynamic timeline markers and tooltips
  - Auto-extending visible range when dragging near edges
  - Hover effects and event tooltips
-->

<template>
  <div class="timeline-slider">
    <!-- Main timeline container -->
    <div 
      class="timeline"
      ref="timeline"
      @mousemove="updateHoverPosition"
      @mouseleave="hideHoverEffect"
    >
      <!-- Hover effect indicator -->
      <div class="hover-effect" :style="hoverEffectStyle">
        <div class="hover-line"></div>
      </div>
      
      <!-- Left slider handle -->
      <div 
        class="slider left" 
        :style="{ left: leftPosition + '%' }" 
        @mousedown="startDrag('left', $event)" 
        @mouseover="showTooltip('left')" 
        @mouseout="hideTooltip('left')"
      >
        <div v-if="showLeftTooltip" class="slider-tooltip">
          {{ formatDate(currentStartDate) }}
        </div>
      </div>
      
      <!-- Right slider handle -->
      <div 
        class="slider right" 
        :style="{ left: rightPosition + '%' }" 
        @mousedown="startDrag('right', $event)" 
        @mouseover="showTooltip('right')" 
        @mouseout="hideTooltip('right')"
      >
        <div v-if="showRightTooltip" class="slider-tooltip">
          {{ formatDate(currentEndDate) }}
        </div>
      </div>
      
      <!-- Selected range highlight -->
      <div 
        class="selected-range" 
        :style="{ 
          left: leftPosition + '%', 
          width: (rightPosition - leftPosition) + '%' 
        }"
      ></div>
      
      <!-- Timeline markers (date labels) -->
      <div 
        v-for="(marker, index) in timelineMarkers" 
        :key="index" 
        class="timeline-marker" 
        :style="{ left: marker.position + '%' }"
      >
        {{ marker.label }}
      </div>
      
      <!-- Event bars -->
      <div 
        v-for="(event, index) in visibleEvents" 
        :key="'event-' + index" 
        class="event-bar" 
        :style="getEventBarStyle(event)"
        @mouseover="showEventTooltip(event, $event)"
        @mouseout="hideEventTooltip"
        @mouseenter="emitEventHover(event)"
      ></div>
      
      <!-- Inter-event connection lines -->
      <div 
        v-for="(line, index) in interEventLines" 
        :key="'line-' + index"
        class="inter-event-line"
        :style="getInterEventLineStyle(line)"
        @mousemove="handleLineHover(line, $event)"
      ></div>
    </div>
    
    <!-- Event tooltip -->
    <div 
      v-if="isEventTooltipVisible" 
      class="event-tooltip" 
      :style="eventTooltipStyle"
    >
      {{ eventTooltipContent }}
    </div>
    
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue';

// ============================================================================
// PROPS AND EMITS
// ============================================================================

const props = defineProps({
  events: { type: Array, default: () => [] },
  startDate: Date,
  endDate: Date,
});

const emit = defineEmits([
  'update:startDate', 
  'update:endDate', 
  'eventHover', 
  'eventLeave', 
  'interEventHover'
]);

// ============================================================================
// REACTIVE REFERENCES
// ============================================================================

// DOM reference
const timeline = ref(null);

// Slider positions (percentages)
const leftPosition = ref(10);
const rightPosition = ref(90);

// Hover effect
const hoverPosition = ref(null);

// Date ranges
const currentStartDate = ref(new Date());
const currentEndDate = ref(new Date());
const minDate = ref(new Date('2000-01-01'));
const maxDate = ref(new Date(new Date().setMonth(new Date().getMonth() + 6)));
const visibleRangeStart = ref(new Date());
const visibleRangeEnd = ref(new Date());

// Tooltip states
const showLeftTooltip = ref(false);
const showRightTooltip = ref(false);
const isEventTooltipVisible = ref(false);
const eventTooltipContent = ref('');
const eventTooltipStyle = ref({});

// ============================================================================
// COMPUTED PROPERTIES
// ============================================================================

// Hover effect styling
const hoverEffectStyle = computed(() => {
  if (hoverPosition.value === null) {
    return { display: 'none' };
  }
  
  // Check if hover position overlaps with any event or inter-event line
  const hoveredEvent = visibleEvents.value.find(event => {
    return hoverPosition.value >= event.startPosition && hoverPosition.value <= event.endPosition;
  });
  
  const hoveredLine = interEventLines.value.find(line => {
    return hoverPosition.value >= line.start && hoverPosition.value <= line.end;
  });
  
  return {
    left: `${hoverPosition.value}%`,
    display: 'block',
    backgroundColor: (hoveredEvent || hoveredLine) ? 'rgba(255, 255, 255, 0.5)' : 'rgba(255, 255, 255, 0.2)'
  };
});


// Visible events within the timeline range
const visibleEvents = computed(() => {
  const { start, end, timeRange } = extendedVisibleRangeForEvents.value;
  return props.events.map(event => {
    const startDate = new Date(event.start);
    const endDate = new Date(event.end);
    const visibleTimeRange = visibleRangeEnd.value.getTime() - visibleRangeStart.value.getTime();
    let startPosition = ((startDate.getTime() - visibleRangeStart.value.getTime()) / visibleTimeRange) * 100;
    let endPosition = ((endDate.getTime() - visibleRangeStart.value.getTime()) / visibleTimeRange) * 100;
    startPosition = Math.max(0, Math.min(startPosition, 100));
    endPosition = Math.max(0, Math.min(endPosition, 100));
    return { ...event, startPosition, endPosition };
  }).filter(event => {
    const eventStart = new Date(event.start);
    const eventEnd = new Date(event.end);
    return eventStart <= end && eventEnd >= start;
  });
});


// Lines connecting non-overlapping events
const interEventLines = computed(() => {
  const lines = [];
  const sortedEvents = [...visibleEvents.value].sort((a, b) => new Date(a.start) - new Date(b.start));
  for (let i = 0; i < sortedEvents.length - 1; i++) {
    const currentEvent = sortedEvents[i];
    const nextEvent = sortedEvents[i + 1];
    if (new Date(nextEvent.start) > new Date(currentEvent.end)) {
      lines.push({ start: currentEvent.endPosition, end: nextEvent.startPosition, startEvent: currentEvent, endEvent: nextEvent });
    }
  }
  return lines;
});


// Timeline markers (date labels along the timeline)
const timelineMarkers = computed(() => {
  const visibleTimeRange = visibleRangeEnd.value.getTime() - visibleRangeStart.value.getTime();
  const leftSliderTime = currentStartDate.value.getTime();
  const rightSliderTime = currentEndDate.value.getTime();
  const selectedTimeRange = rightSliderTime - leftSliderTime;
  const dayInMs = 24 * 60 * 60 * 1000;
  const monthInMs = 30 * dayInMs;
  const yearInMs = 365 * dayInMs;

  let interval, format;
  if (selectedTimeRange <= 30 * dayInMs) {
    interval = dayInMs;
    format = 'day';
  } else if (selectedTimeRange <= 1.5 * yearInMs) {
    interval = monthInMs;
    format = 'month';
  } else {
    interval = yearInMs;
    format = 'year';
  }

  const markers = [];
  // Subtract 10 days from the visible range start
  let current = new Date(visibleRangeStart.value.getTime() - 10 * dayInMs);

  while (current.getTime() <= visibleRangeEnd.value.getTime()) {
    const currentTime = current.getTime();
    const position = ((currentTime - visibleRangeStart.value.getTime()) / visibleTimeRange) * 100;
    const closeToSlider = Math.abs(position - leftPosition.value) < 5 || Math.abs(position - rightPosition.value) < 5;
    if (position >= 0 && position <= 100) {
      let label;
      switch (format) {
        case 'day':
          label = current.getDate().toString();
          break;
        case 'month':
          label = current.toLocaleString('default', { month: 'short' });
          if (current.getMonth() === 0) {
            label = `${label}\n${current.getFullYear()}`;
          }
          break;
        case 'year':
          label = current.getFullYear().toString();
          break;
      }
      markers.push({ position, label, isFixed: false });
    }
    current = new Date(current.getTime() + interval);
  }

  //markers.push({ position: leftPosition.value, label: formatDate(currentStartDate.value), isFixed: true });
  //markers.push({ position: rightPosition.value, label: formatDate(currentEndDate.value), isFixed: true });

  return markers.sort((a, b) => a.position - b.position);
});

// ============================================================================
// INITIALIZATION FUNCTIONS
// ============================================================================

// Initialize date ranges and visible timeline bounds
const initializeDates = () => {
  const now = new Date();
  const oneYearAgo = new Date(now.getFullYear() - 1, now.getMonth(), now.getDate());
  
  // Set initial visible range (14 months total)
  visibleRangeEnd.value = new Date(now.getFullYear(), now.getMonth() + 2, now.getDate());
  visibleRangeStart.value = new Date(oneYearAgo.getFullYear(), oneYearAgo.getMonth() - 2, oneYearAgo.getDate());
  
  // Set initial slider positions
  leftPosition.value = 10;
  rightPosition.value = 90;
  
  // Calculate initial dates based on slider positions
  updateDates('left', true);
  updateDates('right', true);
};

// Initialize slider positions
const initializePositions = () => {
  leftPosition.value = 0;
  rightPosition.value = 100;
};

// ============================================================================
// DATE AND POSITIONING FUNCTIONS
// ============================================================================

// Format date for display
const formatDate = (date) => {
  return date.toLocaleDateString('en-US', { year: 'numeric', month: 'short', day: 'numeric' });
};

// Update date values based on slider positions
const updateDates = (slider, emitValue) => {
  const visibleTimeRange = visibleRangeEnd.value.getTime() - visibleRangeStart.value.getTime();
  if (slider === 'left') {
    const leftTime = visibleRangeStart.value.getTime() + (leftPosition.value / 100) * visibleTimeRange *0.835;
    currentStartDate.value = new Date(Math.max(leftTime, minDate.value.getTime()));
    if (emitValue) emit('update:startDate', currentStartDate.value);
  } else {
    const rightTime = visibleRangeStart.value.getTime() + (rightPosition.value / 100) * visibleTimeRange * 1.019;
    currentEndDate.value = new Date(Math.min(rightTime, maxDate.value.getTime()));
    if (emitValue) emit('update:endDate', currentEndDate.value);
  }
};

const adjustVisibleRange = () => {
  const newVisibleStart = new Date(currentStartDate.value);
  const newVisibleEnd = new Date(currentEndDate.value);
  const visibleTimeRange = newVisibleEnd.getTime() - newVisibleStart.getTime();
  newVisibleStart.setTime(newVisibleStart.getTime() - visibleTimeRange * 0.1);
  newVisibleEnd.setTime(newVisibleEnd.getTime() + visibleTimeRange * 0.1);
  visibleRangeStart.value = new Date(Math.max(newVisibleStart, minDate.value));
  visibleRangeEnd.value = new Date(Math.min(newVisibleEnd, maxDate.value));
  leftPosition.value = 10;
  rightPosition.value = 90;
};

const resetSliderPositions = () => {
  leftPosition.value = 10;
  rightPosition.value = 90;
};

const extendedVisibleRangeForEvents = computed(() => {
  const start = new Date(visibleRangeStart.value);
  const end = new Date(visibleRangeEnd.value);
  start.setMonth(start.getMonth() - 4);
  end.setMonth(end.getMonth() + 4);
  return { start, end, timeRange: end.getTime() - start.getTime() };
});


// Extend visible range when dragging near edges
const extendVisibleRange = (direction, distanceDragged) => {
  const visibleTimeRange = visibleRangeEnd.value.getTime() - visibleRangeStart.value.getTime();
  const expansionFactor = 0.0001;
  if (direction === 'left') {
    const newVisibleStart = new Date(visibleRangeStart.value.getTime() - visibleTimeRange * Math.abs(distanceDragged) * expansionFactor);
    visibleRangeStart.value = new Date(Math.max(newVisibleStart, minDate.value));
    leftPosition.value = 10;
    const leftTime = visibleRangeStart.value.getTime() + (leftPosition.value / 100) * visibleTimeRange;
    currentStartDate.value = new Date(Math.max(leftTime, minDate.value.getTime()));
  } else {
    const newVisibleEnd = new Date(visibleRangeEnd.value.getTime() + visibleTimeRange * Math.abs(distanceDragged) * expansionFactor);
    visibleRangeEnd.value = new Date(Math.min(newVisibleEnd, maxDate.value));
    rightPosition.value = 90;
    const rightTime = visibleRangeEnd.value.getTime() + (rightPosition.value / 100) * visibleTimeRange;
    currentEndDate.value = new Date(Math.min(rightTime, maxDate.value.getTime()));
  }
  updateDates(direction, false);
};


// ============================================================================
// EVENT HANDLING FUNCTIONS
// ============================================================================

// Handle slider drag interactions
const startDrag = (slider, event) => {
  event.preventDefault();
  const initialX = event.clientX;
  const initialLeft = leftPosition.value;
  const initialRight = rightPosition.value;
  const timelineRect = timeline.value.getBoundingClientRect();

  const move = (e) => {
    const deltaX = e.clientX - initialX;
    const deltaPct = (deltaX / timelineRect.width) * 100;
    if (slider === 'left') {
      if (leftPosition.value <= 10) {
        extendVisibleRange('left', deltaX);
      }
      leftPosition.value = Math.max(0, Math.min(initialLeft + deltaPct, rightPosition.value - 1));
    } else {
      if (rightPosition.value >= 90) {
        extendVisibleRange('right', deltaX);
      }
      rightPosition.value = Math.max(leftPosition.value + 1, Math.min(initialRight + deltaPct, 100));
    }
  };

  const stop = () => {
    window.removeEventListener('mousemove', move);
    window.removeEventListener('mouseup', stop);
    updateDates(slider, true);
    adjustVisibleRange();
    resetSliderPositions();
  };

  window.addEventListener('mousemove', move);
  window.addEventListener('mouseup', stop);
};


// Update hover position for visual feedback
const updateHoverPosition = (event) => {
  const timelineRect = timeline.value.getBoundingClientRect();
  hoverPosition.value = ((event.clientX - timelineRect.left) / timelineRect.width) * 100;
  
  // Find any event at this hover position
  const hoveredEvent = visibleEvents.value.find(event => {
    return hoverPosition.value >= event.startPosition && hoverPosition.value <= event.endPosition;
  });
  
  if (hoveredEvent) {
    // Use the existing emitEventHover function
    emitEventHover(hoveredEvent);
    showEventTooltip(hoveredEvent, event);
  } else {
    hideEventTooltip();
  }
  
  // Find any inter-event line at this hover position
  const hoveredLine = interEventLines.value.find(line => {
    return hoverPosition.value >= line.start && hoverPosition.value <= line.end;
  });
  
  if (hoveredLine) {
    handleLineHover(hoveredLine, event);
  }
};

// Hide hover effect
const hideHoverEffect = () => {
  hoverPosition.value = null;
  hideEventTooltip();
};


// Handle inter-event line hover for interpolation
const handleLineHover = (line, event) => {
  try {
    const timelineRect = timeline.value.getBoundingClientRect();
    
    // Calculate position along the line
    const lineStart = line.start;
    const lineEnd = line.end;
    const lineWidth = lineEnd - lineStart;
    const position = (hoverPosition.value - lineStart) / lineWidth;
    const normalizedPosition = Math.max(0, Math.min(1, position));
    
    const startTime = new Date(line.startEvent.end).getTime();
    const endTime = new Date(line.endEvent.start).getTime();
    const interpolatedTime = startTime + (endTime - startTime) * normalizedPosition;
    
    const startLat = line.startEvent.position?.lat ?? 0;
    const endLat = line.endEvent.position?.lat ?? 0;
    const interpolatedLat = startLat + (endLat - startLat) * normalizedPosition;
    
    const startLon = line.startEvent.position?.lon ?? 0;
    const endLon = line.endEvent.position?.lon ?? 0;
    const interpolatedLon = startLon + (endLon - startLon) * normalizedPosition;
    
    emit('interEventHover', {
      time: new Date(interpolatedTime),
      position: {
        lat: interpolatedLat,
        lon: interpolatedLon
      },
      startEvent: line.startEvent,
      endEvent: line.endEvent
    });
  } catch (error) {
    console.error('Error in handleLineHover:', error);
  }
};

// ============================================================================
// TOOLTIP FUNCTIONS
// ============================================================================

// Show slider tooltips
const showTooltip = (slider) => {
  if (slider === 'left') {
    showLeftTooltip.value = true;
  } else {
    showRightTooltip.value = true;
  }
};

// Hide slider tooltips
const hideTooltip = (slider) => {
  if (slider === 'left') {
    showLeftTooltip.value = false;
  } else {
    showRightTooltip.value = false;
  }
};

// Show event tooltip on hover
const showEventTooltip = (event, e) => {
  const startDate = new Date(event.start);
  const endDate = new Date(event.end);
  eventTooltipContent.value = `${formatDate(startDate)} - ${formatDate(endDate)}`;
  
  if (e) {
    eventTooltipStyle.value = {
      left: `${e.clientX}px`,
      top: `${e.clientY - 30}px`
    };
  }
  
  isEventTooltipVisible.value = true;
};


// Hide event tooltip
const hideEventTooltip = () => {
  isEventTooltipVisible.value = false;
};

// ============================================================================
// EVENT BAR STYLING FUNCTIONS
// ============================================================================

// Get styling for event bars
const getEventBarStyle = (event) => {
  return {
    left: `${event.startPosition}%`,
    width: `${event.endPosition - event.startPosition}%`,
    backgroundColor: '#48bb78'
  };
};

// Get styling for inter-event lines
const getInterEventLineStyle = (line) => {
  const start = Math.min(line.start, line.end);
  const end = Math.max(line.start, line.end);
  
  return {
    left: `${start}%`,
    width: `${end - start}%`,
    backgroundColor: '#718096',
    height: '3px',
    top: 'calc(50% - 1.5px)',
  };
};

// ============================================================================
// EVENT EMISSION FUNCTIONS
// ============================================================================

// Emit event hover for external handling
const emitEventHover = (event) => {
  emit('eventHover', event);
};

// ============================================================================
// WATCHERS
// ============================================================================

// Watch for external startDate changes
watch(() => props.startDate, (startDate) => {
  currentStartDate.value = startDate;
  adjustVisibleRange();
  emit('update:startDate', currentStartDate.value);
});

// Watch for external endDate changes
watch(() => props.endDate, (endDate) => {
  currentEndDate.value = endDate;
  adjustVisibleRange();
  emit('update:endDate', currentEndDate.value);
});

// Watch for dataPoints changes (if prop exists)
watch(() => props.dataPoints, () => {
  initializeDates();
}, { deep: true });

// ============================================================================
// LIFECYCLE HOOKS
// ============================================================================

onMounted(() => {
  initializeDates();
  initializePositions();
  adjustVisibleRange();
});
</script>

