<!--
  AnchorageLayerToggle.vue
  
  A popup component that allows users to toggle the visibility of individual layers
  within the "Anchorage and seabed use" grouped layer on a Leaflet map.
  
  Features:
  - Toggle individual layers (Seamarks, Fixed Infrastructure, Anchorage)
  - Multi-language support through translation props
  - Dynamic layer matching and visibility management
  - Responsive positioning with transition effects
-->

<template>
  <div v-if="isVisible" class="anchorage-layer-toggle-popup" @click.stop>
    <h3 class="anchorage-layer-title">{{ props.currentTranslations.anchorageLayerToggles }}</h3>
    
    <div class="layers-container" @click.stop>
      <div v-for="layer in layers" :key="layer.name" class="layer-toggle" @click.stop>
        <input 
          :id="layer.name" 
          type="checkbox" 
          :checked="layer.visible" 
          @change.stop="toggleLayer(layer)" 
          class="layer-checkbox"
        />
        <label :for="layer.name" class="layer-label">{{ getLayerTranslation(layer) }}</label>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, watch, onMounted, computed } from 'vue';

// ============================================================================
// COMPONENT PROPS
// ============================================================================

const props = defineProps({
  mapInstance: {
    type: Object,
    required: false
  },
  controlInstance: {
    type: Object,
    required: false
  },
  currentTranslations: {
    type: Object,
    required: false
  }
});

// ============================================================================
// REACTIVE DATA
// ============================================================================

// Controls the visibility of the popup
const isVisible = ref(false);

// Define the layers with predefined names to match
const layerDefinitions = [
  { name: 'Seamarks', translatedKey: 'seamarksLayer', defaultName: 'Seamarks' },
  { name: 'FixedInfrastructure', translatedKey: 'fixedInfrastructureLayer', defaultName: 'Fixed Infrastructure' },
  { name: 'Anchorage', translatedKey: 'anchorageLayer', defaultName: 'Anchorage' }
];

// Reactive layers array with only static data
const layers = ref(layerDefinitions.map(def => ({
  name: def.name,
  translatedKey: def.translatedKey,
  defaultName: def.defaultName,
  layer: null,
  visible: false
})));

// ============================================================================
// COMPUTED PROPERTIES & HELPER FUNCTIONS
// ============================================================================

/**
 * Get the current translation for a layer, falling back to default name
 * @param {Object} layer - The layer object containing translation keys
 * @returns {string} - The translated layer name or default name
 */
const getLayerTranslation = (layer) => {
  return props.currentTranslations[layer.translatedKey] || layer.defaultName;
};

// ============================================================================
// LAYER MANAGEMENT FUNCTIONS
// ============================================================================

/**
 * Initialize layers when map is ready by finding and matching layers
 * from the grouped "Anchorage and seabed use" layer
 */
const initializeLayers = () => {
  if (!props.controlInstance) return;

  // Find the grouped layer
  const groupedLayerObj = props.controlInstance._layers.find(
    layerObj => layerObj.originalName === "Anchorage and seabed use"
  );
  
  if (!groupedLayerObj) {
    return;
  }
  
  const groupedLayer = groupedLayerObj.layer;
  
  // Get all layers in the group
  const allGroupLayers = groupedLayer.getLayers();
  
  // Match layers dynamically
  layers.value.forEach((layerConfig, index) => {
    // Try to match layer by name similarity or use the layer by index
    const matchedLayer = allGroupLayers.find(
      layer => layer.options?.name?.toLowerCase().includes(layerConfig.name.toLowerCase())
    ) || allGroupLayers[index];
    
    if (matchedLayer) {
      layerConfig.layer = matchedLayer;
      
      // Check if the layer is currently on the map
      layerConfig.visible = props.mapInstance.hasLayer(matchedLayer);
      
      // If layer should be visible but isn't, add it
      if (layerConfig.visible) {
        props.mapInstance.addLayer(matchedLayer);
      }
    }
  });
};

/**
 * Toggle individual layer visibility on the map
 * @param {Object} layerConfig - The layer configuration object
 */
const toggleLayer = (layerConfig) => {
  if (!layerConfig.layer) return;
  
  layerConfig.visible = !layerConfig.visible;
  
  if (layerConfig.visible) {
    props.mapInstance.addLayer(layerConfig.layer);
  } else {
    props.mapInstance.removeLayer(layerConfig.layer);
  }
};

// ============================================================================
// WATCHERS
// ============================================================================

// Watch for changes in control instance and reinitialize layers
watch(() => props.controlInstance, () => {
  initializeLayers();
}, { immediate: true });

// Watch for changes in translations (reactive updates handled by template)
watch(() => props.currentTranslations, () => {
  // No need to update anything here as we're now getting translations directly in the template
  // through the getLayerTranslation function
}, { deep: true });

// ============================================================================
// EXPOSED METHODS
// ============================================================================

defineExpose({
  /**
   * Show the popup and initialize layers
   */
  show: () => {
    isVisible.value = true;
    initializeLayers();
  },
  
  /**
   * Hide the popup
   */
  hide: () => {
    isVisible.value = false;
  }
});
</script>

