import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import { createPinia } from 'pinia';
import urql, { Client, cacheExchange, fetchExchange } from '@urql/vue';

import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'bootstrap';
// commented out because opening the app in a browser would not show the most recent state, 
// could be reintegrated in the future to improve loading time
// import './registerServiceWorker';

const client = new Client({
    url: process.env.VUE_APP_GRAPHQL_SERVER_URL,
    exchanges: [cacheExchange, fetchExchange],
    fetchOptions: {
      headers: {
        'content-type': 'application/json',
        'x-hasura-admin-secret': process.env.VUE_APP_HASURA_ADMIN_SECRET,
      },
    },
  });

createApp(App).use(createPinia()).use(router).use(urql, client).mount('#app');
//creates app instance of vue App and mounts it at ID app