<!--
LandingSiteViewer.vue - Landing Site Details Page Component

This component displays comprehensive information about a specific submarine cable landing site.
It shows the landing site's basic information, connected cables, and other connected landing sites
in an organized card-based layout.

Features:
- Dynamic route-based landing site loading
- Interactive cable and landing site navigation
- Real-time connected sites count filtering
- Responsive card-based layout with hover effects
- Automatic store synchronization for selected cables and sites
-->

<template>
  <div class="landing-site-details-container">
    <!-- Header Section -->
    <div class="header-section">
      <!-- Back Navigation -->
      <router-link
        to="/"
        @click="dataCableStore.deselectCables(), landingSitesStore.deselectLandingSite()"
        class="back-button"
      >
        <i class="bi bi-arrow-left"></i>
        <span>Back to Map</span>
      </router-link>
      
      <!-- Landing Site Title -->
      <div class="landing-site-title">
        <h1>{{ landingSite?.name || 'Loading...' }}</h1>
      </div>
    </div>

    <!-- Main Content Grid -->
    <div v-if="landingSite" class="content-grid">
      <!-- Basic Information Card -->
      <div class="info-card">
        <div class="card-header">
          <i class="bi bi-info-circle"></i>
          <h3>Landing Site Information</h3>
        </div>
        <div class="card-content">
          <div class="info-row">
            <div class="info-item">
              <span class="label">Country</span>
              <span class="value">{{ landingSite.country }}</span>
            </div>
          </div>
        </div>
      </div>

      <!-- Connected Cables Card -->
      <div v-if="landingSite.CableSystem_X_Landingsites?.length" class="info-card">
        <div class="card-header">
          <i class="bi bi-ethernet"></i>
          <h3>Connected Cables</h3>
          <span class="count-badge">{{ landingSite.CableSystem_X_Landingsites.length }}</span>
        </div>
        <div class="card-content">
          <div class="cables-grid">
            <div
              v-for="cable in landingSite.CableSystem_X_Landingsites"
              :key="cable.CableSystem.id"
              class="cable-chip"
              @click="navigateToCable(cable.CableSystem.id)"
            >
              <span>{{ cable.CableSystem.name }}</span>
            </div>
          </div>
        </div>
      </div>

      <!-- Connected Landing Sites Card -->
      <div v-if="landingSite.CableSystem_X_Landingsites_t?.length" class="info-card">
        <div class="card-header">
          <i class="bi bi-geo-alt"></i>
          <h3>Connected Landing Sites</h3>
          <span class="count-badge">{{ getConnectedSitesCount() }}</span>
        </div>
        <div class="card-content">
          <div class="connected-sites-grid">
            <div
              v-for="connectedSite in landingSite.CableSystem_X_Landingsites_t"
              :key="connectedSite"
              v-show="!landingSitesStore.isLandingSiteSelectedById(connectedSite)"
              class="connected-site-chip"
              @click="navigateToLandingSite(connectedSite)"
            >
              <span>{{ connectedSite }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref } from "vue";
import { onBeforeRouteUpdate, useRoute } from "vue-router";
import { useClientHandle } from "@urql/vue";
import { useDataCablesStore } from "@/store/datacables";
import { useLandingSitesStore } from "@/store/landingSites";
import { queryLandingSiteById } from "@/service/hasura";

// ============================================================================
// REACTIVE STATE
// ============================================================================

// Main landing site data
const landingSite = ref();

// Store instances
const dataCableStore = useDataCablesStore();
const landingSitesStore = useLandingSitesStore();

// GraphQL client handle
const handle = useClientHandle();

// Current route information
const route = useRoute();

// ============================================================================
// DATA LOADING FUNCTIONS
// ============================================================================

/**
 * Loads landing site data from the API based on the provided ID
 * Also initializes the data cable store and sets selected cables
 * @param {string} id - The landing site ID to load
 */
const loadLandingSite = async (id: string) => {
  try {
    const result = await handle.client
      .query(queryLandingSiteById, { id: id })
      .toPromise();

    if (result.error) {
      return;
    }

    const data = result.data.Landingsites[0];
    landingSite.value = data;

    // Initialize cable store and select related cables
    await dataCableStore.initialize();
    dataCableStore.setManySelectedCables(
      data.CableSystem_X_Landingsites.map((cable: any) => cable.CableSystem.id)
    );
  } catch (error) {
    // Error handling could be added here if needed
  }
};

/**
 * Sets up the landing site selection in the store
 * Ensures the current landing site is properly selected
 */
async function setLandingSite() {
  await dataCableStore.initialize();
  const currentId = route.params.landingSiteId.toString();
  
  if (!landingSitesStore.isLandingSiteSelectedById(currentId)) {
    landingSitesStore.setSelectLandingSiteById(currentId);
  }
}

// ============================================================================
// UTILITY FUNCTIONS
// ============================================================================

/**
 * Calculates the number of connected landing sites that are not currently selected
 * Used for displaying the count badge in the connected sites card
 * @returns {number} Count of unselected connected sites
 */
const getConnectedSitesCount = () => {
  if (!landingSite.value?.CableSystem_X_Landingsites_t) return 0;
  
  return landingSite.value.CableSystem_X_Landingsites_t.filter(
    (site: string) => !landingSitesStore.isLandingSiteSelectedById(site)
  ).length;
};

// ============================================================================
// NAVIGATION FUNCTIONS
// ============================================================================

/**
 * Handles navigation to a specific cable
 * Deselects current landing site and selects the target cable
 * @param {string} cableId - The ID of the cable to navigate to
 */
const navigateToCable = (cableId: string) => {
  landingSitesStore.deselectLandingSite();
  dataCableStore.setSelectCable(cableId);
};

/**
 * Handles navigation to a connected landing site
 * Selects the target landing site
 * @param {string} siteId - The ID of the landing site to navigate to
 */
const navigateToLandingSite = (siteId: string) => {
  landingSitesStore.setSelectLandingSiteById(siteId);
};

// ============================================================================
// COMPONENT INITIALIZATION
// ============================================================================

// Load initial landing site data based on route parameter
const initialId = route.params.landingSiteId.toString();
loadLandingSite(initialId);
setLandingSite();

// ============================================================================
// ROUTE WATCHERS
// ============================================================================

/**
 * Handles route updates when navigating between different landing sites
 * Reloads landing site data when the route parameter changes
 */
onBeforeRouteUpdate((to) => {
  const newId = to.params.landingSiteId.toString();
  loadLandingSite(newId);
});
</script>

