<!--
SeismicActivity.vue - Component for displaying earthquake/Seismic activity data

Features:
- Displays seismic activity data from USGS API
- Date range selection for historical data
- Visual representation of earthquakes with magnitude-based styling
- Detailed earthquake information on selection
- Supports multiple languages through translations
-->

<template>
  <!-- Seismic Activity Layer Group -->
  <l-layer-group :visible="false" name="Seismic Activity" layer-type="base">
    <!-- GeoJSON layer for earthquake data -->
    <l-geo-json
      v-if="seismicData"
      :geojson="seismicData"
      :options="{ pointToLayer: reactivePointToLayer }"
    ></l-geo-json>
    
    <!-- Dummy Earthquake for Usage Study -->
    <l-circle-marker
      v-if="showSeismicOverlay"
      :lat-lng="[5.7, 97.4]"
      :radius="15"
      fillColor="red"
      color="black"
      :weight="1"
      :opacity="1"
      :fillOpacity="0.8"
    >
      <l-tooltip>Dummy Earthquake</l-tooltip>
    </l-circle-marker>
  </l-layer-group>

  <!-- Date Picker for Seismic Data -->
  <div v-if="showSeismicOverlay" class="date-popup">
    <VueDatePicker
      v-model="seismicDate"
      :model-value="seismicDate"
      @update:model-value="seismicDatePicked"
      :placeholder="currentTranslations.datePlaceholderSeismic"
      :preview-format="format"
      :format="format"
      :enable-time-picker="false"
      :range="{ partialRange: false }"
      dark=true
      class="custom-datepicker"
      no-today
      :max-date="new Date()"
    />
  </div>

  <!-- Earthquake Details Panel -->
  <div v-if="selectedEarthquake" class="earthquake-details">
    <h2>{{ currentTranslations.selectedEarthquakeDetails }}:</h2>
    <p><strong>{{ currentTranslations.value.magnitude }}:</strong> {{ selectedEarthquake.mag }}</p>
    <p><strong>{{ currentTranslations.value.location }}:</strong> {{ selectedEarthquake.place }}</p>
    <p><strong>{{ currentTranslations.value.time }}:</strong> {{ new Date(selectedEarthquake.time).toLocaleString() }}</p>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, watch } from 'vue';
import {
  LLayerGroup,
  LGeoJson,
  LCircleMarker,
  LTooltip
} from '@vue-leaflet/vue-leaflet';
import VueDatePicker from "@vuepic/vue-datepicker";
import '@vuepic/vue-datepicker/dist/main.css';
import axios from 'axios';

// ==============================================
// COMPONENT PROPS & EMITS
// ==============================================

const props = defineProps({
  currentTranslations: {
    type: Object,
    required: true
  },
  showSeismicOverlay: {
    type: Boolean,
    required: true
  },
  currentLanguage: {
    type: String,
    required: true
  }
});

const emit = defineEmits(['update:showSeismicOverlay']);

// ==============================================
// REACTIVE STATE
// ==============================================

const seismicData = ref(null);          // Stores GeoJSON earthquake data
const selectedEarthquake = ref(null);   // Currently selected earthquake
const seismicDate = ref(null);          // Selected date range for filtering
const format = "dd/MM/yyyy";           // Date format for display

// ==============================================
// WATCHERS
// ==============================================

// Update seismic layer when language changes
watch(() => props.currentLanguage, () => {
  if (props.showSeismicOverlay) {
    updateSeismicLayer();
  }
});

// Handle seismic overlay toggle
watch(() => props.showSeismicOverlay, (newValue) => {
  if (newValue) {
    updateSeismicLayer();
  }
});

// ==============================================
// COMPUTED PROPERTIES
// ==============================================

/**
Computed property for styling earthquake markers based on magnitude
 */
const reactivePointToLayer = computed(() => (feature, latlng) => {
  const magnitude = feature.properties.mag;
  const radius = magnitude * 3;
  const fillColor = magnitude > 5 ? 'red' : magnitude > 3 ? 'orange' : 'yellow';
  
  return L.circleMarker(latlng, {
    radius: radius,
    fillColor: fillColor,
    color: 'black',
    weight: 1,
    opacity: 1,
    fillOpacity: 0.8
  }).bindTooltip(`
    <strong>${props.currentTranslations.magnitude}:</strong> ${magnitude}<br>
    <strong>${props.currentTranslations.location}:</strong> ${feature.properties.place}<br>
    <strong>${props.currentTranslations.time}:</strong> ${new Date(feature.properties.time).toLocaleString()}
  `);
});

// ==============================================
// DATA FETCHING FUNCTIONS
// ==============================================

/**
Fetches seismic data from USGS API
@param {string|null} startTime - Start date in YYYY-MM-DD format
@param {string|null} endTime - End date in YYYY-MM-DD format
 */
const fetchSeismicData = async (startTime = null, endTime = null) => {
  try {
    let url;
    if (startTime && endTime) {
      url = `https://earthquake.usgs.gov/fdsnws/event/1/query?format=geojson&starttime=${startTime}&endtime=${endTime}`;
    } else {
      // Default to recent significant earthquakes if no date range specified
      url = 'https://earthquake.usgs.gov/earthquakes/feed/v1.0/summary/2.5_week.geojson';
    }
    const response = await axios.get(url);
    seismicData.value = response.data;
  } catch (error) {
    console.error('Error fetching seismic data:', error);
  }
};

// ==============================================
// DATE HANDLING FUNCTIONS
// ==============================================

/**
Handles date selection from the date picker
@param {Array<Date>} e - Selected date range
 */
const seismicDatePicked = (e) => {
  if (e != null) {
    let startDate = formatDatePart(e[0].getDate());
    let startMonth = formatDatePart(e[0].getMonth() + 1);
    let endDate = formatDatePart(e[1].getDate());
    let endMonth = formatDatePart(e[1].getMonth() + 1);
    const startTime = `${e[0].getFullYear()}-${startMonth}-${startDate}`;
    const endTime = `${e[1].getFullYear()}-${endMonth}-${endDate}`;
    fetchSeismicData(startTime, endTime);
  } else {
    // If date selection is cleared, fetch default data
    fetchSeismicData();
  }
};

/**
Formats date parts to ensure two-digit format
@param {number} part - Date part (day or month)
@returns {string} Formatted two-digit string
 */
const formatDatePart = (part) => {
  return part < 10 ? '0' + part : part;
};

// ==============================================
// LAYER MANAGEMENT FUNCTIONS
// ==============================================

/**
Updates the seismic layer (placeholder for future functionality)
 */
 const updateSeismicLayer = () => {
  if (props.showSeismicOverlay && seismicData.value) {
    const cloned = JSON.parse(JSON.stringify(seismicData.value));
    seismicData.value = null; 
    setTimeout(() => {
      seismicData.value = cloned;
    }, 0);
  }
};

// ==============================================
// INITIAL DATA LOAD
// ==============================================

// Fetch initial seismic data when component mounts
fetchSeismicData();
</script>

<style scoped>
/* Earthquake Details Panel */
.earthquake-details {
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 15px;
  border-radius: 8px;
  max-width: 300px;
}

.earthquake-details h2 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.earthquake-details p {
  margin: 5px 0;
}

/* Custom DatePicker Styles */
.custom-datepicker {
  --dp-background-color: #2d3748;
  --dp-text-color: #ffffff;
  --dp-hover-color: #4a5568;
  --dp-hover-text-color: #ffffff;
  --dp-primary-color: #4299e1;
}
</style>