<!--
  SideBarList.vue
  
  A sidebar component that displays a list of data cables.
  Each cable can be selected by clicking on it, which updates the store
  and applies visual styling to indicate the active selection.
-->

<template>
  <div
    class="item-container"
    v-for="listItems in dataCableStore.getDataCables"
    :key="listItems.id"
  >
    <!-- Clickable list item with conditional active styling -->
    <a
      @click="dataCableStore.setSelectCable(listItems.id, true)"
      class="list-item"
      :class="{ 'active': dataCableStore.selectedCableId === listItems.id }"
    >
      <div class="item-content">
        <!-- Cable name display -->
        <strong class="item-name">{{ listItems.name }}</strong>
      </div>
    </a>
  </div>
</template>

<script setup lang="ts">
// Store imports
import { useDataCablesStore } from "@/store/datacables";

// Store initialization
const dataCableStore = useDataCablesStore();
</script>

