<!--
  ClusteredDataCenters.vue
  
  A Vue component that displays data centers on a Leaflet map with clustering functionality.
  Features:
  - Clusters nearby data center markers for better performance and visibility
  - Supports both point and polygon geometries for data centers
  - Interactive tooltips with formatted property information
  - Responsive cluster sizing based on point count
  - Separate handling of polygons to prevent animation issues
  - Layer visibility control through map overlay system
-->

<template>
</template>

<script setup>
  import { ref, onUnmounted, watch } from 'vue'
  import L from 'leaflet'
  import Supercluster from 'supercluster'
  import datacenters from '@/assets/datacenters.json'

  // =============================================================================
  // PROPS DEFINITION
  // =============================================================================
  
  const props = defineProps({
    mapInstance: {
      type: Object,
      required: false
    },
    controlInstance: {
      type: Object,
      required: false
    },
    mapReady: {
      type: Boolean,
      required: false
    },
    currentTranslations: {
      type: Object,
      required: false
    },
  })

  // =============================================================================
  // REACTIVE REFERENCES
  // =============================================================================
  
  const supercluster = ref(null)
  const dataCenterLayer = ref(null)
  const polygonLayer = ref(null) // Separate layer for polygons
  const isLayerVisible = ref(false)

  // =============================================================================
  // UTILITY FUNCTIONS
  // =============================================================================
  
  /**
   * Formats property keys for display by removing @ symbols, capitalizing words,
   * and handling underscores and colons appropriately
   */
  const formatPropertyKey = (key) => {
    // Handle special cases for better display names
    const keyMappings = {
      'addrcity': 'City',
      'addrcitysimc': 'City Code',
      'addrhousenumber': 'House Number',
      'addrpostcode': 'Postcode',
      'addrstreet': 'Street',
      'building': 'Building',
      'sourceaddr': 'Source'
    }
    
    const lowerKey = key.toLowerCase()
    if (keyMappings[lowerKey]) {
      return keyMappings[lowerKey]
    }
    
    return key.replace('@', '')
      .split(':')
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join('')
      .split('_')
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ')
  }

  /**
   * Creates HTML content for tooltips from feature properties
   * Filters out unwanted properties and formats links with improved styling
   */
  const createTooltipContent = (properties) => {
    const excludeKeys = [
      'originalGeometry', 
      'cluster', 
      'cluster_id', 
      'point_count', 
      'point_count_abbreviated', 
      'id',
      'telecom' // Remove the telecom field as it's always 'data_center'
    ]
    
    const content = Object.entries(properties)
      .filter(([key, value]) => 
        !excludeKeys.includes(key.toLowerCase()) && 
        value !== null && 
        value !== undefined && 
        value !== '' && 
        value !==
        value !== 'data_center' && // Extra safety to filter out data_center values
        !key.toLowerCase().includes('id')
      )
      .map(([key, value]) => {
        const formattedKey = formatPropertyKey(key)
        let formattedValue = value
        
        // Format specific values
        if (key.toLowerCase() === 'building' && value === 'yes') {
          formattedValue = 'Yes'
        }
        
        // Handle URLs with better formatting
        if (value.toString().startsWith('http')) {
          const domain = new URL(value).hostname
          formattedValue = `<a href="${value}" target="_blank" rel="noopener noreferrer" class="tooltip-link">${domain}</a>`
        }
        
        return `
          <div class="tooltip-row">
            <span class="tooltip-label">${formattedKey}:</span>
            <span class="tooltip-value">${formattedValue}</span>
          </div>
        `
      })
      .join('')
    
      return `
  <div class="datacenter-tooltip">
    <div class="tooltip-header">
      <div class="tooltip-icon"></div>
      <div class="tooltip-title">Data Center</div>
    </div>
    <div class="tooltip-content">
      ${content}
    </div>
  </div>
`
  }

  /**
   * Creates a custom icon for data center markers
   */
  const createDataCenterIcon = () => {
    return L.divIcon({
      html: `<div class="data-center-marker"><svg viewBox="0 0 24 24" width="24" height="24"><rect x="4" y="2" width="16" height="20" fill="#4a90e2" rx="2"/><rect x="6" y="4" width="12" height="3" fill="#214d7c" rx="1"/><rect x="6" y="8" width="12" height="3" fill="#214d7c" rx="1"/><rect x="6" y="12" width="12" height="3" fill="#214d7c" rx="1"/><rect x="6" y="16" width="12" height="3" fill="#214d7c" rx="1"/><circle cx="7" cy="5.5" r="0.5" fill="#00ff00"/><circle cx="7" cy="9.5" r="0.5" fill="#00ff00"/><circle cx="7" cy="13.5" r="0.5" fill="#00ff00"/><circle cx="7" cy="17.5" r="0.5" fill="#00ff00"/></svg></div>`,
      className: 'data-center-icon',
      iconSize: [24, 24],
      iconAnchor: [12, 12]
    })
  }

  /**
   * Binds an interactive tooltip to a layer with proper styling and pointer events
   */
  const bindInteractiveTooltip = (layer, content) => {
    const tooltip = layer.bindTooltip(content, {
      permanent: false,
      direction: 'top',
      className: 'datacenter-tooltip-container',
      opacity: 1,
      offset: [0, -10]
    }).getTooltip()
    
    // Make tooltip interactive
    if (tooltip) {
      const tooltipElement = tooltip.getElement()
      if (tooltipElement) {
        tooltipElement.style.pointerEvents = 'auto'
      }
    }
  }

  // =============================================================================
  // SUPERCLUSTER INITIALIZATION
  // =============================================================================
  
  try {
    // Initialize supercluster with clustering configuration
    supercluster.value = new Supercluster({
      radius: 40,
      maxZoom: 16,
      map: (props) => ({
        ...props.properties,
        count: 1,
        originalGeometry: props.originalGeometry
      }),
      reduce: (accumulated, props) => {
        accumulated.count += props.count
      }
    })

    // Transform features to point geometry for clustering while preserving original geometry
    const formattedFeatures = datacenters.features.map(feature => ({
      type: 'Feature',
      properties: {
        ...feature.properties, 
        originalGeometry: feature.geometry.type === 'Polygon' ? feature.geometry : null
      },
      geometry: {
        type: 'Point',
        coordinates: feature.geometry.type === 'Polygon' ? 
          [feature.geometry.coordinates[0][0][0], feature.geometry.coordinates[0][0][1]] : 
          feature.geometry.coordinates
      }
    }))
    
    supercluster.value.load(formattedFeatures)
  } catch (error) {
    // Silent handling - functionality will be degraded but component won't crash
  }

  // =============================================================================
  // CORE FUNCTIONALITY
  // =============================================================================
  
  /**
   * Updates clusters based on current map view and zoom level
   * Handles both individual markers and cluster markers
   */
  const updateClusters = () => {
    if (!props.mapInstance || !dataCenterLayer.value || !isLayerVisible.value) {
      return
    }
    
    try {
      const bounds = props.mapInstance.getBounds()
      const zoom = Math.floor(props.mapInstance.getZoom())
      const bbox = [bounds.getWest(), bounds.getSouth(), bounds.getEast(), bounds.getNorth()]
      const clusters = supercluster.value.getClusters(bbox, zoom)
      
      // Clear existing layers
      dataCenterLayer.value.clearLayers()
      if (polygonLayer.value) {
        polygonLayer.value.clearLayers()
      }
      
      clusters.forEach((cluster) => {
        const [lng, lat] = cluster.geometry.coordinates
        const latlng = L.latLng(lat, lng)
        
        if (cluster.properties.cluster) {
          // Handle cluster markers
          const count = cluster.properties.point_count
          const size = count < 10 ? 'small' : count < 50 ? 'medium' : 'large'
          const marker = L.marker(latlng, {
            icon: L.divIcon({
              html: `<div class="cluster-marker ${size}"><div class="cluster-count">${count}</div></div>`,
              className: 'custom-cluster-icon',
              iconSize: [40, 40],
              iconAnchor: [20, 20]
            })
          })
          
          // Add click handler to expand cluster
          marker.on('click', () => {
            const expansionZoom = supercluster.value.getClusterExpansionZoom(cluster.properties.cluster_id)
            props.mapInstance.setView(latlng, expansionZoom)
          })
          
          dataCenterLayer.value.addLayer(marker)
        } else {
          // Handle individual markers and polygons
          
          // Add polygon if original geometry exists
          if (cluster.properties.originalGeometry && polygonLayer.value) {
            try {
              const polygon = L.polygon(
                cluster.properties.originalGeometry.coordinates[0].map(coord => [coord[1], coord[0]]),
                {
                  color: '#4a90e2',
                  weight: 2,
                  fillColor: '#4a90e2',
                  fillOpacity: 0.2,
                  // Disable animation for polygons to prevent errors
                  pane: 'overlayPane'
                }
              )
              const tooltipContent = createTooltipContent(cluster.properties)
              bindInteractiveTooltip(polygon, tooltipContent)
              polygonLayer.value.addLayer(polygon)
            } catch (polygonErr) {
              // Silent handling - polygon creation failed but marker will still be added
            }
          }
          
          // Add point marker
          const marker = L.marker(latlng, {
            icon: createDataCenterIcon()
          })
          
          const tooltipContent = createTooltipContent(cluster.properties)
          bindInteractiveTooltip(marker, tooltipContent)
          dataCenterLayer.value.addLayer(marker)
        }
      })
    } catch (error) {
      // Silent handling - cluster update failed
    }
  }

  /**
   * Initializes the layer groups and sets up event listeners
   */
  const initializeLayer = () => {
    if (!props.mapInstance || !props.controlInstance) {
      return
    }
    
    try {
      // Create separate layers for better management
      polygonLayer.value = L.layerGroup()
      dataCenterLayer.value = L.layerGroup()
      
      // Add layers to map and control
      props.mapInstance.addLayer(polygonLayer.value)
      props.controlInstance.addOverlay(dataCenterLayer.value, 'Data Centers', false)
      
      // Set up animation handling for polygons
      if (props.mapInstance.options) {
        // Hide polygons during zoom animation to prevent errors
        props.mapInstance.on('zoomanim', (e) => {
          if (isLayerVisible.value && polygonLayer.value) {
            polygonLayer.value.eachLayer((layer) => {
              if (layer._path) {
                layer._path.style.display = 'none'
              }
            })
          }
        })
        
        // Update clusters and show polygons after zoom completes
        props.mapInstance.on('zoomend', () => {
          updateClusters()
          
          if (isLayerVisible.value && polygonLayer.value) {
            polygonLayer.value.eachLayer((layer) => {
              if (layer._path) {
                layer._path.style.display = ''
              }
            })
          }
        })
      }
      
      // Set up other event listeners
      props.mapInstance.on('moveend', updateClusters)
      
      props.mapInstance.on('overlayadd', (e) => {
        if (e.originalName === 'Data Centers' || e.name === 'Data Centers' ) {
          isLayerVisible.value = true
          if (!props.mapInstance.hasLayer(polygonLayer.value)) {
            props.mapInstance.addLayer(polygonLayer.value)
          }
          updateClusters()
        }
      })
      
      props.mapInstance.on('overlayremove', (e) => {
        if (e.originalName === 'Data Centers' || e.name === 'Data Centers') {
          isLayerVisible.value = false
          if (props.mapInstance.hasLayer(polygonLayer.value)) {
            props.mapInstance.removeLayer(polygonLayer.value)
          }
        }
      })
      
      isLayerVisible.value = false
    } catch (error) {
      // Silent handling - initialization failed
    }
  }

  // =============================================================================
  // LIFECYCLE HOOKS
  // =============================================================================
  
  /**
   * Watch for map readiness and control instance to initialize layers
   */
  watch(
    [() => props.mapReady, () => props.controlInstance],
    ([mapReady, controlInst]) => {
      if (mapReady && controlInst) {
        initializeLayer()
      }
    },
    { immediate: true }
  )
  /**
   * Cleanup event listeners and layers on component unmount
   */
  onUnmounted(() => {
    if (props.mapInstance) {
      props.mapInstance.off('moveend', updateClusters)
      props.mapInstance.off('zoomend', updateClusters)
      props.mapInstance.off('zoomanim')
      
      if (dataCenterLayer.value) {
        props.mapInstance.removeLayer(dataCenterLayer.value)
      }
      
      if (polygonLayer.value) {
        props.mapInstance.removeLayer(polygonLayer.value)
      }
    }
  })

  // =============================================================================
  // COMPONENT EXPORTS
  // =============================================================================
  
  defineExpose({
    dataCenterLayer,
  })
</script>

<style>
  /* =============================================================================
   * CLUSTER MARKER STYLES
   * ============================================================================= */
  
