    background-color: #4a90e2;
    border: 2px solid #214d7c;
    border-radius: 50%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
  }
  
  .cluster-marker.small {
    width: 30px;
    height: 30px;
    font-size: 12px;
  }
  
  .cluster-marker.medium {
    width: 35px;
    height: 35px;
    font-size: 14px;
  }
  
  .cluster-marker.large {
    width: 40px;
    height: 40px;
    font-size: 16px;
  }
  
  .cluster-count {
    width: 100%;
    text-align: center;
  }

  /* =============================================================================
   * DATA CENTER MARKER STYLES
   * ============================================================================= */
  
  .data-center-marker {
    width: 24px;
    height: 24px;
  }
  
  .data-center-icon {
    background: none;
    border: none;
  }

  /* =============================================================================
   * MODERN DARK TOOLTIP STYLES
   * ============================================================================= */
  
  .datacenter-tooltip-container {
    pointer-events: auto !important;
  }
  
  .leaflet-tooltip.datacenter-tooltip-container {
    pointer-events: auto !important;
    background: transparent;
    border: none;
    box-shadow: none;
    padding: 0;
  }
  
  .datacenter-tooltip {
    background: rgba(26, 32, 44, 0.9);
    border-radius: 10px;
    box-shadow: 0 4px 14px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.1);
    min-width: 250px;
    max-width: 600px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    overflow: hidden;
    z-index: 1000;
    backdrop-filter: saturate(180%) blur(8px);
    user-select: none;
    color: #e2e8f0;
    font-size: 13px;
    line-height: 1.4;
  }
  
  .tooltip-header {
    display: flex;
    align-items: center;
    margin-bottom: 2px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);
    padding: 16px 20px 12px 20px;
    gap: 12px;
  }
  
  .tooltip-icon {
    font-size: 20px !important;
    color: #63b3ed;
    filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.1));
  }
  
  .tooltip-title {
    font-weight: 700;
    font-size: 0.85rem;
    color: #90cdf4;
    margin: 0;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  }
  
  .tooltip-content {
    padding: 16px 20px 20px 20px;
  }
  
  .tooltip-info-grid {
    display: flex;
    flex-direction: column;
    gap: 6px;
  }
  
  .tooltip-row {
    display: flex;
    justify-content: space-between;
    padding: 8px 12px;
    background: rgba(255, 255, 255, 0.07);
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    transition: all 0.2s ease;
    align-items: flex-start;
    min-height: 32px;
    margin: 5px;
  }
  
  .tooltip-row:hover {
    background: rgba(99, 179, 237, 0.15);
    border-color: rgba(99, 179, 237, 0.25);
  }
  
  .tooltip-label {
    font-size: 0.75rem !important;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.2px !important;
