import { defineStore } from "pinia"; 
import router from "@/router/index"; 
import { loadLandingSites } from "@/service/hasura"; 
import { toRaw } from "vue"; 

export const useLandingSitesStore = defineStore("landingSites", { 
  state: () => ({ 
    landingSites: [] as any[], 
    selectLandingSites: [] as any[], 
    isLoading: true, 
    selectedFromSidebar: false, 
  }), 
  
  getters: { 
    getLandingSites: (state) => { 
      return state.landingSites; 
    }, 
  }, 
  
  actions: { 
    async fetchLandingSites() { 
      const landingSites = ((await loadLandingSites()) as any).data._rawValue.Landingsites; 
      landingSites.forEach((e: any) => (e.geoData = JSON.parse(e.geoData))); 
      this.landingSites = landingSites; 
    }, 
    
    setSelectLandingSiteById(landingSiteId: string, fromSidebar = false) { 
      const landingSite = this.landingSites.find( 
        (site: any) => landingSiteId === site.id 
      ); 
      
      if (landingSite) { 
        this.setSelectLandingSite(toRaw(landingSite), fromSidebar); 
      } else { 
        console.error("No landing site found!"); 
      } 
    }, 
    
    setSelectLandingSite(landingSite: any, fromSidebar = false) {
      console.log("STORE: SELECT LANDING SITE: ", landingSite);
      console.log("FROM SIDEBAR: ", fromSidebar);
      this.selectLandingSites = [landingSite]; 
      this.selectedFromSidebar = fromSidebar; 
      // you could set the selected cables here. You need to change the request loadLandingSites and copy the code from LandingViewer in this method! 
      router.push({ path: `/landingSites/${landingSite.id}` }); 
    }, 
    
    deselectLandingSite() {
      if (this.selectLandingSites.length) { 
        this.selectLandingSites = []; 
        this.selectedFromSidebar = false; 
        router.push({ path: "/" }); 
      } 
    }, 
    
    isOneSelected() { 
      return !!this.landingSites.length; 
    }, 
    
    isLandingSiteSelectedById(landingSiteId: string): boolean { 
      return this.selectLandingSites.some( 
        (site: any) => landingSiteId === site.id 
      ); 
    }, 
    
    isLandingSiteSelected(landingSite: any): boolean {
      return this.selectLandingSites.some(
        (site: any) => landingSite.id === site.id
      );
    }, 
  }, 
});