function processHeatmapInChunks(data, layerKey, options) {
  return new Promise((resolve) => {
    const CHUNK_SIZE = 1000;
    const SAMPLE_RATE = 2; // Adjust this to control memory usage and rendering time

    let processedData = [];
    let currentIndex = 0;

    function processChunk() {
      const endIndex = Math.min(currentIndex + CHUNK_SIZE, data.length);

      for (let i = currentIndex; i < endIndex; i++) {
        // Sample every Nth item to reduce final size
        if (i % SAMPLE_RATE === 0) {
          processedData.push(data[i]);
        }
      }

      currentIndex = endIndex;
      if(currentIndex == 800000 || currentIndex == 1800000){
        console.log("CURRENT INDEX: ", currentIndex);
      }
