    volcanoLayerRef.value.onVolcanoLayerDeactivated();
  }
  
  if (distanceToShipsRef.value?.distanceGridActive) {
    distanceToShipsRef.value.deactivateDistancePolygonGridLayer();
  }
  
  // Deactivate Cable Age layer through component if active
  if (cableAgeLayerRef.value) {
    cableAgeLayerRef.value.deactivateCableAgeLayer();
