const imageLoadPromises = new Map();

function preloadShallowWatersImages() {
  const shallowWatersImages = [
    { url: require('@/assets/shallowWaters/1.webp'), bounds: [[0.0, -180.0], [90.0, -90.0]] },
    { url: require('@/assets/shallowWaters/2.webp'), bounds: [[0.0, 90.0], [90.0, 180.0]] },
    { url: require('@/assets/shallowWaters/3.webp'), bounds: [[0.0, -90.0], [90.0, 0.0]] },
    { url: require('@/assets/shallowWaters/4.webp'), bounds: [[0.0, 0.0], [90.0, 90.0]] },
    { url: require('@/assets/shallowWaters/5.webp'), bounds: [[-90.0, 90.0], [0.0, 180.0]] },
    { url: require('@/assets/shallowWaters/6.webp'), bounds: [[-90.0, -90.0], [0.0, 0.0]] },
    { url: require('@/assets/shallowWaters/7.webp'), bounds: [[-90.0, 0.0], [0.0, 90.0]] }
  ];

  // Load images progressively
  shallowWatersImages.forEach((img, index) => {
    if (!imageLoadPromises.has(img.url)) {
      const promise = new Promise((resolve) => {
        const imgObj = new Image();
        imgObj.onload = () => {
          imageCache[img.url] = imgObj;
          resolve(imgObj);
        };
        imgObj.onerror = () => resolve(null); // Handle errors gracefully
        // Delay loading to prevent blocking initial render
        setTimeout(() => {
          imgObj.src = img.url;
        }, index * 100); // Stagger loading
      });
      imageLoadPromises.set(img.url, promise);
    }
  });
}

function monitorLayerPerformance(layerName, operation) {
  const startTime = performance.now();
  
  return operation().finally(() => {
    const endTime = performance.now();
    const duration = endTime - startTime;
    
    if (duration > 100) { // Log slow operations
      console.warn(`Layer ${layerName} took ${duration.toFixed(2)}ms to load`);
    }
  });
}

function onShallowWatersLayerActivated() {
  if (!props.riskLayersToggleRef?.isManualToggle) return;
  if (shallowWatersLayers.length === 0) {
    const shallowWatersImages = [
      { url: require('@/assets/shallowWaters/1.webp'), bounds: [[0.0, -180.0], [90.0, -90.0]] },
      { url: require('@/assets/shallowWaters/2.webp'), bounds: [[0.0, 90.0], [90.0, 180.0]] },
      { url: require('@/assets/shallowWaters/3.webp'), bounds: [[0.0, -90.0], [90.0, 0.0]] },
      { url: require('@/assets/shallowWaters/4.webp'), bounds: [[0.0, 0.0], [90.0, 90.0]] },
      { url: require('@/assets/shallowWaters/5.webp'), bounds: [[-90.0, 90.0], [0.0, 180.0]] },
      { url: require('@/assets/shallowWaters/6.webp'), bounds: [[-90.0, -90.0], [0.0, 0.0]] },
      { url: require('@/assets/shallowWaters/7.webp'), bounds: [[-90.0, 0.0], [0.0, 90.0]] }
    ];

    shallowWatersLayers = shallowWatersImages.map(img => 
      L.imageOverlay(img.url, img.bounds, { opacity: 0.7 })
    );
  }

  shallowWatersLayers.forEach(layer => props.mapInstance.addLayer(layer));
  isShallowWatersLayerActive.value = true;
}

function onShallowWatersLayerDeactivated() {
  shallowWatersLayers.forEach(layer => props.mapInstance.removeLayer(layer));
  isShallowWatersLayerActive.value = false;
}

