// ==============================================
// COMBINED RISK LAYER FUNCTIONS
// ==============================================

/**
Gets color for combined risk value
 */
function getColorEurope(rawRiskValue) {
  return rawRiskValue > 0.437336 ? '#67001D' :
         rawRiskValue > 0.100002 ? '#B50A2A' :
         rawRiskValue > 0.048397 ? '#E8453C' :
         rawRiskValue > 0.010739 ? '#F67E4F' :
         rawRiskValue > 0.001021 ? '#FDB366' :
         rawRiskValue > 0.000156 ? '#FFE08A' :
         rawRiskValue > 0.0001   ? '#A3D39C' :
                                  '#6DAED4';
}

/**
Style function for combined risk layer
 */
function combinedRiskStyleFunctionEurope(properties, zoom) {
  return {
    fill: true,
    fillColor: getColorEurope(properties.risk),
    fillOpacity: 0.7,
    stroke: zoom >= 8,
    color: 'white',
    weight: zoom >= 8 ? 0.5 : 0,
    opacity: 0.7,
    dashArray: zoom >= 8 ? '3' : '0'
  };
}

/**
Creates features for combined risk layer
 */
function createCombinedRiskFeatures(data, zoom) {
  const minRisk = zoom < 3 ? 0.1 :
                  zoom < 5 ? 0.01 :
                  zoom < 7 ? 0.001 :
                  0;

  const cellSizeHalf = 0.05;

  return data
    .filter(item => item[2] >= minRisk)
    .map(item => {
      const [lng, lat, risk] = item;
      
      return {
        type: 'Feature',
        properties: { risk },
        geometry: {
          type: 'Polygon',
          coordinates: [[
            [lng - cellSizeHalf, lat - cellSizeHalf],
            [lng + cellSizeHalf, lat - cellSizeHalf],
            [lng + cellSizeHalf, lat + cellSizeHalf],
            [lng - cellSizeHalf, lat + cellSizeHalf],
            [lng - cellSizeHalf, lat - cellSizeHalf]
          ]]
        }
      };
    });
}

/**
Gets percentile info for combined risk value
 */
function getCombinedRiskPercentileInfo(riskValue) {
  if (riskValue > 0.437336) return "Top 1% (Extreme risk)";
  if (riskValue > 0.100002) return "95th-99th percentile (Severe risk)";
  if (riskValue > 0.048397) return "90th-95th percentile (High risk)";
  if (riskValue > 0.010739) return "75th-90th percentile (Elevated risk)";
  if (riskValue > 0.001021) return "50th-75th percentile (Moderate risk)";
  if (riskValue > 0.000156) return "25th-50th percentile (Low risk)";
  if (riskValue > 0.0001) return "Minimum-25th percentile (Baseline)";
  return "Below minimum detectable risk";
}

/**
Applies interactivity to combined risk layer
 */
function applyCombinedRiskInteractivity(layer) {
  if (!window.combinedRiskTooltip) {
    window.combinedRiskTooltip = L.tooltip();
  }
  if (!window.combinedRiskPopup) {
    window.combinedRiskPopup = L.popup();
  }

  const tooltip = window.combinedRiskTooltip;
  const popup = window.combinedRiskPopup;

  layer.on('mouseover', function(e) {
    const risk = e.layer.properties.risk;
    tooltip
      .setContent(`${props.currentTranslations.combinedRisk}: ${risk.toFixed(6)}`)
      .setLatLng(e.latlng)
      .addTo(props.mapInstance);
  });

  layer.on('mouseout', () => props.mapInstance.closeTooltip(tooltip));

  layer.on('click', function(e) {
    const risk = e.layer.properties.risk;
    popup
      .setContent(`
        <div class="risk-popup">
          <div style="font-weight: bold;">${props.currentTranslations.combinedRisk} ${props.currentTranslations.details}</div>
          <div><strong>${props.currentTranslations.value}:</strong> ${risk.toFixed(6)}</div>
          <div><strong>${props.currentTranslations.classification}:</strong> ${getCombinedRiskPercentileInfo(risk)}</div>
        </div>
      `)
      .setLatLng(e.latlng)
      .openOn(props.mapInstance);
  });
}

/**
Activates combined risk layer
 */
function onCombinedRiskEuropeLayerActivated() {
  if (combinedRiskEuropeLayer) {
    combinedRiskEuropeLayer.addTo(props.mapInstance);
    attachZoomHandler();
    return;
  }

  if (!combinedRiskDataEurope?.length) {
    console.error("[CombinedRisk] Error: combinedRiskDataEurope is empty or invalid.");
    return;
  }

  const currentZoom = props.mapInstance.getZoom();
  const features = createCombinedRiskFeatures(combinedRiskDataEurope, currentZoom);
  const geojsonData = { type: 'FeatureCollection', features: features };

  combinedRiskEuropeLayer = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: { sliced: combinedRiskStyleFunctionEurope },
    interactive: true,
    maxZoom: 16,
    buffer: 256,
    updateWhenIdle: true,
    tolerance: 3
  });

  applyCombinedRiskInteractivity(combinedRiskEuropeLayer);
  combinedRiskEuropeLayer.addTo(props.mapInstance);
  attachZoomHandler();
}

/**
Deactivates combined risk layer
 */
function onCombinedRiskEuropeLayerDeactivated() {
  if (combinedRiskEuropeLayer) {
    props.mapInstance.removeLayer(combinedRiskEuropeLayer);
    combinedRiskEuropeLayer = null;
  }
}

// ==============================================
// FISHING LAYER FUNCTIONS
// ==============================================

/**
Gets color for fishing intensity
 */
function getColorFishingEurope(intensity) {
  return intensity > 0.99       ? '#330000' :
         intensity > 0.247068   ? '#67001D' :
         intensity > 0.102776   ? '#B50A2A' :
         intensity > 0.015986   ? '#E8453C' :
         intensity > 0.002485   ? '#F67E4F' :
         intensity > 0.000502   ? '#FDB366' :
                                 '#FFE08A';
}

/**
Gets percentile info for fishing intensity
 */
function getPercentileInfo(intensity) {
  if (intensity > 0.99) return "Top 1% (Extreme risk)";
  if (intensity > 0.247068) return "Above 95th percentile (Severe risk)";
  if (intensity > 0.102776) return "Above 90th percentile (High risk)";
  if (intensity > 0.015986) return "Above 75th percentile (Elevated risk)";
  if (intensity > 0.002485) return "Above 50th percentile (Moderate risk)";
  if (intensity > 0.000502) return "Above 25th percentile (Low risk)";
  return "Below 25th percentile (Baseline risk)";
}

/**
Style function for fishing layer
 */
function styleFunctionEurope(properties, zoom) {
  return {
    fill: true,
    fillColor: getColorFishingEurope(properties.intensity),
    fillOpacity: 0.5,
    stroke: zoom >= 8,
    color: 'white',
    weight: 0.5,
    opacity: 0.7
  };
}

/**
Creates features for fishing layer
 */
function createGeoJsonFeatures(data, zoom) {
  const minIntensity = zoom < 3 ? 0.1 :
                      zoom < 4 ? 0.05 :
                      zoom < 6 ? 0.01 :
                      zoom < 8 ? 0.001 : 0;
  
  const cellSizeHalf = 0.05;
  
  return data
    .filter(item => item[2] >= minIntensity)
    .map(item => {
      const [lng, lat, avgIntensity] = item;
      
      return {
        type: 'Feature',
        properties: { intensity: avgIntensity },
        geometry: {
          type: 'Polygon',
          coordinates: [[
            [lng - cellSizeHalf, lat - cellSizeHalf],
            [lng + cellSizeHalf, lat - cellSizeHalf],
            [lng + cellSizeHalf, lat + cellSizeHalf],
            [lng - cellSizeHalf, lat + cellSizeHalf],
            [lng - cellSizeHalf, lat - cellSizeHalf]
          ]]
        }
      };
    });
}

/**
Applies interactivity to fishing layer
 */
function applyInteractivityToLayer(layer) {
  if (!window.fishingTooltip) {
    window.fishingTooltip = L.tooltip();
  }
  
  let tooltip = window.fishingTooltip;

  if (!window.fishingPopup) {
    window.fishingPopup = L.popup();
  }
  
  let popup = window.fishingPopup;

  layer.on('mouseover', function(e) {
    const intensity = e.layer.properties.intensity;
    const formattedIntensity = intensity.toFixed(6);
    
    tooltip.setContent(`Fishing Intensity: ${formattedIntensity}`)
      .setLatLng(e.latlng);
      
    if (!tooltip.isOpen()) {
      tooltip.addTo(props.mapInstance);
    }
  });

  layer.on('mouseout', function() {
    props.mapInstance.closeTooltip(tooltip);
  });

  layer.on('click', function(e) {
    const intensity = e.layer.properties.intensity;
    const formattedIntensity = intensity.toFixed(6);
    const percentileInfo = getPercentileInfo(intensity);
    
    popup.setContent(`
      <div style="font-weight: bold;">Fishing Intensity Details</div>
      <div>Intensity: ${formattedIntensity}</div>
      <div>Percentile: ${percentileInfo}</div>
    `)
    .setLatLng(e.latlng)
    .openOn(props.mapInstance);
  });
}

/**
Activates fishing layer
 */
function onFishingLayerActivatedEurope() {
  if (!props.fishingDataEurope || props.fishingDataEurope.length === 0) {
    console.error('Fishing data Europe is empty or not yet loaded.');
    return;
  }

  if (fishingLayerEurope) {
    fishingLayerEurope.addTo(props.mapInstance);
    attachZoomHandler();
    return;
  }

  const currentZoom = props.mapInstance.getZoom();
  const features = createGeoJsonFeatures(props.fishingDataEurope, currentZoom);
  const geojsonData = { type: 'FeatureCollection', features };

  fishingLayerEurope = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: { sliced: styleFunctionEurope },
    interactive: true,
    maxZoom: 16,
    buffer: 256,
    updateWhenIdle: true,
  });

  applyInteractivityToLayer(fishingLayerEurope);
  fishingLayerEurope.addTo(props.mapInstance);
  attachZoomHandler();
}

/**
Deactivates fishing layer
 */
function onFishingLayerDeactivatedEurope() {
  if (fishingLayerEurope) {
    props.mapInstance.removeLayer(fishingLayerEurope);
    fishingLayerEurope = null;
  }
}

