// ==============================================
// DREDGING LAYER FUNCTIONS
// ==============================================

/**
Gets color for dredging density
 */
function getColorDredgingEurope(density) {
  return density > 0.991064 ? '#67001D' :
         density > 0.071649 ? '#B50A2A' :
         density > 0.022322 ? '#E8453C' :
         density > 0.000740 ? '#F67E4F' :
         density > 0.000122 ? '#FDB366' :
         density > 0.000026 ? '#FFE08A' :
         density > 0        ? '#FFFFCC' :
                             '#f0f9e7';
}

/**
Gets percentile info for dredging density
 */
function getDredgingPercentileInfo(density) {
  if (density > 0.991064) return "Above 99th percentile (Extreme)";
  if (density > 0.071649) return "95th-99th percentile (Severe)";
  if (density > 0.022322) return "90th-95th percentile (High)";
  if (density > 0.000740) return "75th-90th percentile (Elevated)";
  if (density > 0.000122) return "50th-75th percentile (Moderate)";
  if (density > 0.000026) return "25th-50th percentile (Low)";
  if (density > 0) return "Below 25th percentile (Minimal)";
  return "No dredging activity";
}

/**
Style function for dredging layer
 */
function dredgingStyleFunctionEurope(properties, zoom) {
  return {
    fill: true,
    fillColor: getColorDredgingEurope(properties.density),
    fillOpacity: 0.5,
    stroke: zoom >= 8,
    color: 'white',
    weight: zoom >= 8 ? 0.5 : 0,
    opacity: 0.7,
    dashArray: '3'
  };
}

/**
Creates features for dredging layer
 */
function createDredgingGeoJsonFeatures(data, zoom) {
  const minDensity = zoom < 3 ? 0.01 :
                   zoom < 5 ? 0.001 :
                   zoom < 7 ? 0.0001 : 0;
  
  const cellSizeHalf = 0.05;

  return data
    .filter(item => item[2] >= minDensity)
    .map(item => {
      const [lng, lat, density] = item;
      
      return {
        type: 'Feature',
        properties: { density },
        geometry: {
          type: 'Polygon',
          coordinates: [[
            [lng - cellSizeHalf, lat - cellSizeHalf],
            [lng + cellSizeHalf, lat - cellSizeHalf],
            [lng + cellSizeHalf, lat + cellSizeHalf],
            [lng - cellSizeHalf, lat + cellSizeHalf],
            [lng - cellSizeHalf, lat - cellSizeHalf]
          ]]
        }
      };
    });
}

/**
Applies interactivity to dredging layer
 */
function applyDredgingInteractivity(layer) {
  if (!window.dredgingTooltip) {
    window.dredgingTooltip = L.tooltip();
  }
  if (!window.dredgingPopup) {
    window.dredgingPopup = L.popup();
  }

  const tooltip = window.dredgingTooltip;
  const popup = window.dredgingPopup;

  layer.on('mouseover', function(e) {
    const density = e.layer.properties.density;
    tooltip.setContent(`Dredging Intensity: ${density.toFixed(6)}`)
      .setLatLng(e.latlng)
      .addTo(props.mapInstance);
  });

  layer.on('mouseout', () => props.mapInstance.closeTooltip(tooltip));

  layer.on('click', function(e) {
    const density = e.layer.properties.density;
    popup.setContent(`
      <div style="font-weight: bold;">Dredging Activity Details</div>
      <div>Normalized Intensity: ${density.toFixed(6)}</div>
      <div>Risk Category: ${getDredgingPercentileInfo(density)}</div>
    `)
    .setLatLng(e.latlng)
    .openOn(props.mapInstance);
  });
}

/**
Activates dredging layer
 */
function onDredgingLayerChoroplethActivatedEurope() {
  if (dredgingLayerChoroplethEurope) {
    dredgingLayerChoroplethEurope.addTo(props.mapInstance);
    isDredgingActiveChoroplethEurope.value = true;
    return;
  }

  const currentZoom = props.mapInstance.getZoom();
  const features = createDredgingGeoJsonFeatures(dredgingDataChoroplethEurope, currentZoom);
  const geojsonData = { type: 'FeatureCollection', features };

  dredgingLayerChoroplethEurope = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: { sliced: dredgingStyleFunctionEurope },
    interactive: true,
    maxZoom: 16,
    buffer: 256,
    updateWhenIdle: true
  });

  applyDredgingInteractivity(dredgingLayerChoroplethEurope);
  dredgingLayerChoroplethEurope.addTo(props.mapInstance);
  isDredgingActiveChoroplethEurope.value = true;
}

/**
Deactivates dredging layer
 */
function onDredgingLayerChoroplethDeactivatedEurope() {
  if (dredgingLayerChoroplethEurope) {
    props.mapInstance.removeLayer(dredgingLayerChoroplethEurope);
    isDredgingActiveChoroplethEurope.value = false;
  }
}

