  return triValue > 0.318765 ? '#67001D' :
         triValue > 0.011803 ? '#B50A2A' :
         triValue > 0.003999 ? '#E8453C' :
         triValue > 0.001295 ? '#F67E4F' :
         triValue > 0.000408 ? '#FDB366' :
         triValue > 0        ? '#FEEDA9' :
                             '#f0f9e7';
