  if (triValue > 0.003999) return "75th-90th percentile (Elevated)";
  if (triValue > 0.001295) return "50th-75th percentile (Moderate)";
  if (triValue > 0.000408) return "25th-50th percentile (Low)";
  if (triValue > 0) return "Below 25th percentile (Minimal)";
  return "Flat terrain (No ruggedness)";
}

/**
Style function for terrain layer
 */
function terrainStyleFunctionEurope(properties, zoom) {
  return {
    fill: true,
    fillColor: getColorTerrainEurope(properties.triValue),
    fillOpacity: 0.5,
    stroke: zoom >= 8,
    color: 'white',
    weight: zoom >= 8 ? 0.5 : 0,
    opacity: 0.7,
    dashArray: '3'
  };
}

/**
Creates features for terrain layer
 */
function createTerrainGeoJsonFeatures(data, zoom) {
  const minValue = zoom < 3 ? 0.01 :
                 zoom < 5 ? 0.001 :
                 zoom < 7 ? 0.0001 : 0;
  
  const cellSizeHalf = 0.05;

  return data
    .filter(item => item[2] >= minValue)
    .map(item => {
      const [lng, lat, triValue] = item;
      
      return {
        type: 'Feature',
        properties: { triValue },
        geometry: {
          type: 'Polygon',
          coordinates: [[
