  });

  layer.on('mouseout', () => props.mapInstance.closeTooltip(tooltip));

  layer.on('click', function(e) {
    const triValue = e.layer.properties.triValue;
    popup.setContent(`
      <div style="font-weight: bold;">Terrain Ruggedness Details</div>
      <div>Normalized TRI Value: ${triValue.toFixed(6)}</div>
      <div>Ruggedness Category: ${getTerrainPercentileInfo(triValue)}</div>
    `)
    .setLatLng(e.latlng)
    .openOn(props.mapInstance);
  });
}

/**
Activates terrain layer
 */
function onTerrainLayerChoroplethActivatedEurope() {
  if (!terrainDataChoroplethEurope.value) {
    console.error('Terrain data not loaded yet.');
    return;
  }

  if (terrainLayerChoroplethEurope) {
    terrainLayerChoroplethEurope.addTo(props.mapInstance);
    isTerrainActiveChoroplethEurope.value = true;
    return;
