  terrainLayerChoroplethEurope = L.vectorGrid.slicer(geojsonData, {
    rendererFactory: L.canvas.tile,
    vectorTileLayerStyles: { sliced: terrainStyleFunctionEurope },
    interactive: true,
    maxZoom: 16,
    buffer: 256,
    updateWhenIdle: true
  });

  applyTerrainInteractivity(terrainLayerChoroplethEurope);
  terrainLayerChoroplethEurope.addTo(props.mapInstance);
  isTerrainActiveChoroplethEurope.value = true;
}

/**
Deactivates terrain layer
 */
function onTerrainLayerChoroplethDeactivatedEurope() {
  if (terrainLayerChoroplethEurope) {
    props.mapInstance.removeLayer(terrainLayerChoroplethEurope);
    isTerrainActiveChoroplethEurope.value = false;
  }
}

