import { defineStore } from "pinia";
import router from "@/router/index";
import { loadCables } from "@/service/hasura";
import { useLandingSitesStore } from "@/store/landingSites";

export const useDataCablesStore = defineStore("dataCables", {
  state: () => ({
    dataCables: [] as any[],
    selectCable: [] as any[],
    landingSite: useLandingSitesStore(),
    firstLoad: null as any,
    selectedFromSidebar: false,
    cableDetails: null as any,
    mapInstance: null as any,
    viewingVesselTrack: false,
    combinedRiskData: [] as Array<[number, number, number]>, //Set in GlobalRiskMap.vue
    combinedRiskDataEurope: [] as Array<[number, number, number]>, //Set in EuropeanRiskMap.vue
  }),
  getters: {
    getDataCables: (state) => {
      return state.dataCables;
    },
    isNoneSelected: (state) => {
      return state.selectCable.length == 0;
    },
    getSelectedCable: (state) => {
      const cableId = state.selectCable[0];
      return state.dataCables.find(cable => cable.id === cableId);
    },
    getMapInstance: (state) => {
      return state.mapInstance;
    }
  },
  actions: {
    async initialize() {
      if (this.firstLoad) {
        await this.firstLoad;
      } else {
        this.firstLoad = Promise.all([
          this.fetchDataCables(),
          this.landingSite.fetchLandingSites(),
        ]);
      }
    },
    async fetchDataCables() {
      const cables = ((await loadCables()) as any).data._rawValue.CableSystems;
      cables.forEach((e: any) => {
        e.geoData = JSON.parse(e.geoData);
        e.opacity = 1; // Ensure opacity is defined and reactive
      });
      this.dataCables = cables;
    },
    setAllCablesOpacity(opacity: number) {
      this.dataCables.forEach(cable => {
        cable.opacity = opacity;
    
        if (cable.layer && this.mapInstance.hasLayer(cable.layer)) {
          // Remove the layer and re-add it to force a re-render
          this.mapInstance.removeLayer(cable.layer);
          cable.layer.options.opacity = opacity; // Update the layer's opacity
          cable.layer.addTo(this.mapInstance);
        }
      });
    },
    setSelectCable(cableId: string, fromSidebar = false){
      this.selectCable = [cableId];
      this.selectedFromSidebar = fromSidebar;
      this.fetchCableDetails(cableId);
      router.push({ path: `/cables/${cableId}` });
    },
    setManySelectedCables(cableIds: string[]) {
      this.selectCable = cableIds;
    },
    deselectCables() {
      if (this.selectCable.length) {
        this.selectCable = [];
        this.cableDetails = [];
        router.push({ path: "/" });
      }
    },
    isCableSelected(cableId: string): boolean {
      return this.selectCable.includes(cableId);
    },
    async fetchCableDetails(cableId: string) {
      const cable = this.dataCables.find(c => c.id === cableId);
      if (cable) {
        this.cableDetails = cable;
      }
    },
    setMapInstance(map: any) {
      this.mapInstance = map;
    },
    setViewingVesselTrack(value: boolean) {
      this.viewingVesselTrack = value;
    },
    countCablesByRFSYear() {
      const yearCounts = new Map<number, number>();
      
      this.dataCables.forEach(cable => {
        const rfsDate = cable.readyForService;
        if (!rfsDate) return; // Skip cables without a RFS date
  
        const date = new Date(rfsDate);
        if (isNaN(date.getTime())) {
          console.error(`Invalid RFS date for cable ${cable.id}: ${rfsDate}`);
          return;
        }
  
        const year = date.getFullYear();
        yearCounts.set(year, (yearCounts.get(year) || 0) + 1);
      });
  
      // Sort years from oldest to newest
      const sortedYears = Array.from(yearCounts.entries()).sort((a, b) => a[0] - b[0]);
  
      // Log results
      console.log('Cables per Ready for Service Year:');
      sortedYears.forEach(([year, count]) => {
        console.log(`${year}: ${count} cable${count !== 1 ? 's' : ''}`);
      });
    },
    setCombinedRiskData(data: Array<[number, number, number]>) {
      this.combinedRiskData = data;
    },
    setCombinedRiskDataEurope(data: Array<[number, number, number]>) {
      this.combinedRiskDataEurope = data;
    },
  },
});