<!-- 
  RiskLayers.vue - Component that manages all risk-related map layers
  Includes:
  - European and Global risk maps
  - Various heatmap layers (fishing, anchorage, dredging, seismic)
  - Seabed substrates, shallow waters, and other specialized layers
  - Associated popups and legends
-->
  
<template>
  <l-layer-group
    name="Risk Layers" 
    layer-type="overlay" 
    :visible="false"
    @add="onRiskLayersActivated"
    @remove="onRiskLayersDeactivated"
  >
    <!-- European Risk Map Layer Group -->
    <l-layer-group
      name="European Risk Map"
      layer-type="overlay"
      :visible="false"
      @add="riskEuropeLayerRef?.onCombinedRiskEuropeLayerGroupActivated"
      @remove="riskEuropeLayerRef?.onCombinedRiskEuropeLayerGroupDeactivated"
    ></l-layer-group>
  
    <!-- Global Risk Map Layer Group -->
    <l-layer-group
      name="Global Risk Map"
      layer-type="overlay"
      :visible="false"
      @add="globalRiskMapRef?.onCombinedRiskLayerGroupActivated"
      @remove="globalRiskMapRef?.onCombinedRiskLayerGroupDeactivated"
    ></l-layer-group>
  
    <!-- Fishing Activity Heatmap Layer -->
    <l-layer-group
      name="Aggregated Fishing Activity"
      layer-type="overlay"
      :visible="false"
      @add="onFishingRiskLayerActivated"
      @remove="onFishingRiskLayerDeactivated"
    >
      <l-tile-layer
        url=""
        attribution='<a href="https://globalfishingwatch.org">Powered by Global Fishing Watch (2012 - 2024)</a>'
      />
    </l-layer-group>
  
    <!-- Anchorage Heatmap Layer -->
    <l-layer-group
      name="Aggregated Anchorage"
      layer-type="overlay"
      :visible="false"
      @add="onAnchorageRiskLayerActivated"
      @remove="onAnchorageRiskLayerDeactivated"
    >
      <l-tile-layer
        url=""
        attribution='<a href="https://globalfishingwatch.org">Powered by Global Fishing Watch</a>'
      />
    </l-layer-group>
  
    <!-- Dredging Heatmap Layer -->
    <l-layer-group
      name="Aggregated Dredging (Europe)"
      layer-type="overlay"
      :visible="false"
      @add="onDredgingRiskLayerActivated"
      @remove="onDredgingRiskLayerDeactivated"
    >
      <l-geo-json
        :geojson="dredgingData"
        :options="{ pointToLayer: () => null }"
        attribution='<a href="https://emodnet.ec.europa.eu/en/human-activities">Powered by EMODnet (1964 - 2023)</a>'
      />
    </l-layer-group>
  
    <!-- Shipping Density Layer -->
    <l-layer-group
      name="Maritime Traffic Density (Sep. 2024)"
      layer-type="overlay"
      :visible="false"
      @add="onGMTDSLayerActivated"
      @remove="onGMTDSLayerDeactivated"
    >
      <l-tile-layer
        url=""
        attribution='<a href="https://globalmaritimetraffic.org/gmtds-data.html">Powered by Global Maritime Traffic Density Service</a>'
      />
    </l-layer-group>
  
    <!-- Seismic Activity Heatmap Layer -->
    <l-layer-group
      name="Heatmap Seismic Activity"
      layer-type="overlay"
      :visible="false"
      @add="onSeismicRiskLayerActivated"
      @remove="onSeismicRiskLayerDeactivated"
    ></l-layer-group>

    <!-- Seabed Substrates Layer -->
    <l-layer-group
      name="Seabed Substrates (Europe)"
      layer-type="overlay"
      :visible="false"
      @add="onSeabedSubstrateLayerActivated"
      @remove="onSeabedSubstrateLayerDeactivated"
    >
      <l-tile-layer
        url=""
        attribution='<a href="https://emodnet.ec.europa.eu/en/human-activities">Powered by EMODnet</a>'
      />
    </l-layer-group>
  
    <!-- Volcanoes Layer -->
    <l-layer-group
      name="Volcanoes"
      layer-type="overlay"
      :visible="false"
      @add="volcanoLayerRef?.onVolcanoLayerActivated"
      @remove="volcanoLayerRef?.onVolcanoLayerDeactivated"
    >
      <l-tile-layer
        url=""
        attribution='Global Volcanism Program, 2025. [Database] Volcanoes of the World (v. 5.2.7; 21 Feb 2025). Distributed by Smithsonian Institution, compiled by Venzke, E. <a href="https://doi.org/10.5479/si.GVP.VOTW5-2024.5.2">https://doi.org/10.5479/si.GVP.VOTW5-2024.5.2</a>'
      />
    </l-layer-group>
    
    <!-- Shallow Waters Layer -->
    <l-layer-group
      name="Shallow Waters (-1 to -200m)"
      layer-type="overlay"
      :visible="false"
      @add="onShallowWatersLayerActivated"
      @remove="onShallowWatersLayerDeactivated"
    >
      <l-tile-layer
        url=""
        attribution='<a href="https://www.gebco.net/data_and_products/gridded_bathymetry_data/">Powered by General Bathymetric Chart of the Oceans (2024)</a>'
      />
    </l-layer-group>
  
    <!-- Cable Age Layer -->
    <l-layer-group
      name="Cable Age"
      layer-type="overlay"
      :visible="false"
      @add="cableAgeLayerRef?.activateCableAgeLayer"
      @remove="cableAgeLayerRef?.deactivateCableAgeLayer"
    ></l-layer-group>
  
    <!-- Distance to Cable Ships Layer -->
    <l-layer-group
      name="Distance to Cable Ships"
      layer-type="overlay" 
      :visible="false"
      @add="distanceToShipsRef?.activateDistancePolygonGridLayer"
      @remove="distanceToShipsRef?.deactivateDistancePolygonGridLayer"
    ></l-layer-group>
  </l-layer-group>

  <!-- Popups and Legends Container -->
  <div class="map-popup-container">
    <!-- European Risk Map Popup -->
    <RiskEuropeLayer 
      :current-translations="currentTranslations"
      :map-instance="mapInstance"
      :risk-layers-toggle-ref="riskLayersToggleRef"
      :fishing-data-europe="fishingDataEurope"
      :terrain-data-choropleth-europe="terrainDataChoroplethEurope"
      ref="riskEuropeLayerRef"
    />
  
    <!-- Global Risk Map Popup -->
    <GlobalRiskMap 
      :current-translations="currentTranslations"
      :map-instance="mapInstance"
      :risk-layers-toggle-ref="riskLayersToggleRef"
      ref="globalRiskMapRef"
    />
  
    <!-- Volcano Layer Popup -->
    <VolcanoLayer 
      :map-instance="mapInstance"
      :current-translations="currentTranslations"
      :risk-layers-toggle-ref="riskLayersToggleRef"
      ref="volcanoLayerRef"
    />
  
    <!-- Seabed Substrates Legend Popup -->
    <div v-if="isSeabedActive" class="map-popup">
      <h3>{{ currentTranslations.seabedSubstratesTitle }}</h3>
      <div class="popup-content">
        <div class="legend-grid">
          <div class="legend-item"><span class="color-box" style="background-color:#92ddfa"></span><span class="legend-text">{{ currentTranslations.mudToMuddySand }}</span></div>
          <div class="legend-item"><span class="color-box" style="background-color:#f7f37c"></span><span class="legend-text">{{ currentTranslations.sand }}</span></div>
          <div class="legend-item"><span class="color-box" style="background-color:#cfcc67"></span><span class="legend-text">{{ currentTranslations.coarseSubstrate }}</span></div>
          <div class="legend-item"><span class="color-box" style="background-color:#dbb09f"></span><span class="legend-text">{{ currentTranslations.mixedSediment }}</span></div>
          <div class="legend-item"><span class="color-box" style="background-color:#7e0004"></span><span class="legend-text">{{ currentTranslations.rockBoulders }}</span></div>
        </div>
      </div>
    </div>
  
    <!-- Cable Age Layer Popup -->
    <CableAgeLayer 
      ref="cableAgeLayerRef"
      :current-language="currentLanguage"
    />
  
    <!-- Distance to Ships Popup -->
    <DistanceToShips 
      :vessel-positions="vesselPositions"
      :current-translations="currentTranslations"
      :map-instance="mapInstance"
      :risk-layers-toggle-ref="riskLayersToggleRef"
      ref="distanceToShipsRef"
    />
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, watch, onBeforeUnmount, nextTick } from 'vue';
import {
  LLayerGroup,
  LTileLayer,
  LGeoJson
} from '@vue-leaflet/vue-leaflet';
import 'leaflet.heat';
import 'leaflet.vectorgrid';

// Components
import RiskEuropeLayer from './EuropeanRiskMap.vue';
import GlobalRiskMap from './GlobalRiskMap.vue';
import VolcanoLayer from './VolcanoLayer.vue';
import CableAgeLayer from './CableAgeLayer.vue';
import DistanceToShips from './DistanceToShips.vue';

// ======================
// PROPS DEFINITION
// ======================
const props = defineProps({
  currentTranslations: {
    type: Object,
    required: false
  },
  mapInstance: {
    type: Object,
    required: false
  },
  riskLayersToggleRef: {
    type: Object,
    required: false
  },
  currentLanguage: {
    type: String,
    required: false
  },
  vesselPositions: {
    type: Array,
    required: false
  },
  fishingDataEurope: {
    type: Object,
    required: false
  },
  terrainDataChoroplethEurope: {
    type: Object,
    required: false
  },
  dredgingData: {
    type: Object,
    required: false
  },
  aggregatedSeismicData: {
    type: Array,
    required: false
  },
  anchorageRiskData: {
    type: Object,
    required: false
  },
  aggregatedFishingData: {
    type: Array,
    required: false
  }
});

// ======================
// COMPONENT REFERENCES
// ======================
const riskEuropeLayerRef = ref(null);
const globalRiskMapRef = ref(null);
const volcanoLayerRef = ref(null);
const cableAgeLayerRef = ref(null);
const distanceToShipsRef = ref(null);

// ======================
// LAYER VISIBILITY STATES
// ======================
const isSeabedActive = ref(false);
const gmtdsLayerVisible = ref(false);
const seismicRiskLayerVisible = ref(false);
const anchorageLayerVisible = ref(false);
const fishingLayerVisible = ref(false);
const isShallowWatersLayerActive = ref(false);

// ======================
// LAYER INSTANCES & CACHES
// ======================
let gmtdsWMSLayer = null;
let seismicRiskHeatmap = null;
let anchorageHeatmapLayer = null;
let dredgingRiskHeatmap = null;
let seabedLayer = null;
let fishingRiskHeatmap = null;
let shallowWatersLayers = [];
const imageCache = {};
const heatmapCache = new Map();

// ======================
// DATA WATCHERS
// ======================
watch(() => props.aggregatedFishingData, (newData) => {
  if (newData && newData.length > 0 && !heatmapCache.has('fishing')) {
    // Process in background without blocking
    processHeatmapInChunks(
      newData, 
      'fishing',
      { radius: 10, blur: 10, maxZoom: 10, max: 100 }
    ).catch(err => console.error('Error processing fishing data:', err));
  }
}, { immediate: true });

watch(() => props.aggregatedSeismicData, (newData) => {
  if (newData && newData.length > 0 && !heatmapCache.has('seismic')) {
    // Process in background without blocking
    processHeatmapInChunks(
      newData,
      'seismic', 
      { radius: 25, blur: 15, maxZoom: 10 }
    ).catch(err => console.error('Error processing seismic data:', err));
  }
}, { immediate: true });

watch(() => props.anchorageRiskData, (newData) => {
  if (newData?.features?.length > 0 && !heatmapCache.has('anchorage')) {
    const heatmapData = newData.features
      .filter(feature => feature.geometry?.coordinates)
      .map(feature => {
        const [longitude, latitude] = feature.geometry.coordinates;
        return [latitude, longitude, 1];
      });
    
    if (heatmapData.length > 0) {
      // Process in background without blocking
      processHeatmapInChunks(
        heatmapData,
        'anchorage',
        { radius: 25, blur: 15, maxZoom: 13 }
      ).catch(err => console.error('Error processing anchorage data:', err));
    }
  }
}, { immediate: true });
// ======================
// LIFECYCLE HOOKS
// ======================
onMounted(() => {
  // Use intersection observer to only preprocess when component is visible
  nextTick(() => {
    setTimeout(() => {
      if ('IntersectionObserver' in window) {
        const observer = new IntersectionObserver((entries) => {
          entries.forEach(entry => {
            if (entry.isIntersecting) {
              preloadShallowWatersImages();
              observer.disconnect();
            }
          });
        });
        
        const target = document.querySelector('.risk-layers-container') || document.body;
        observer.observe(target);
      } else {
        preloadShallowWatersImages();
      }
    }, 500); // Give map time to initialize
  });
});
onBeforeUnmount(() => {
  // Clean up all timeouts and intervals
  debouncedOperations.forEach(timeoutId => clearTimeout(timeoutId));
  debouncedOperations.clear();
  
  // Clear all cached layers
  heatmapCache.forEach(layer => {
    if (layer && props.mapInstance && props.mapInstance.hasLayer(layer)) {
      props.mapInstance.removeLayer(layer);
    }
  });
  heatmapCache.clear();
});
// ======================
// LAYER PRE-PROCESSING
// ======================

function processHeatmapInChunks(data, layerKey, options) {
  return new Promise((resolve) => {
    const CHUNK_SIZE = 1000;
    const SAMPLE_RATE = 2; // Adjust this to control memory usage and rendering time

    let processedData = [];
    let currentIndex = 0;

    function processChunk() {
      const endIndex = Math.min(currentIndex + CHUNK_SIZE, data.length);

      for (let i = currentIndex; i < endIndex; i++) {
        // Sample every Nth item to reduce final size
        if (i % SAMPLE_RATE === 0) {
          processedData.push(data[i]);
        }
      }

      currentIndex = endIndex;
      if(currentIndex == 800000 || currentIndex == 1800000){
        console.log("CURRENT INDEX: ", currentIndex);
      }
      if (currentIndex < data.length) {
        setTimeout(processChunk, 0); // Yield to browser
      } else {
        const heatmapLayer = L.heatLayer(processedData, options);
        heatmapCache.set(layerKey, heatmapLayer);
        resolve();
      }
    }

    setTimeout(processChunk, 200); // Initial delay for readiness
  });
}




const layerLoadingStates = ref({
  fishing: false,
  seismic: false,
  anchorage: false,
  gmtds: false
});
// ======================
// MAIN RISK LAYERS GROUP HANDLERS
// ======================
function onRiskLayersActivated() {
  const mapContainer = props.mapInstance._container;
  mapContainer.classList.add('risk-layers-active');
  
  // Ensure all sub-layers are initially hidden (only non-component layers)
  [ 
    gmtdsWMSLayer, 
    seismicRiskHeatmap, 
    anchorageHeatmapLayer, 
    dredgingRiskHeatmap,
    seabedLayer, 
    fishingRiskHeatmap
  ].forEach(layer => {
    if (layer && props.mapInstance.hasLayer(layer)) {
      props.mapInstance.removeLayer(layer);
    }
  });

  // Tell component-based layers to deactivate
  if (volcanoLayerRef.value?.isVolcanoActive) {
    volcanoLayerRef.value.onVolcanoLayerDeactivated();
  }
  
  if (distanceToShipsRef.value?.distanceGridActive) {
    distanceToShipsRef.value.deactivateDistancePolygonGridLayer();
  }
  
  // Deactivate Cable Age layer through component if active
  if (cableAgeLayerRef.value) {
    cableAgeLayerRef.value.deactivateCableAgeLayer();
  }
  
  // Deactivate European risk layers through child component
  if (riskEuropeLayerRef.value) {
    riskEuropeLayerRef.value.onCombinedRiskEuropeLayerGroupDeactivated();
  }
  
  // Deactivate Global risk layers through child component
  if (globalRiskMapRef.value) {
    globalRiskMapRef.value.onCombinedRiskLayerGroupDeactivated();
  }
  
  // Reset all layer visibility states for non-component layers
  gmtdsLayerVisible.value = false;
  seismicRiskLayerVisible.value = false;
  anchorageLayerVisible.value = false;
  isSeabedActive.value = false;
  
  if (props.riskLayersToggleRef) {
    props.riskLayersToggleRef.show();
  }
}

function onRiskLayersDeactivated() {
  const mapContainer = props.mapInstance._container;
  mapContainer.classList.remove('risk-layers-active');
  if (props.riskLayersToggleRef) {
    props.riskLayersToggleRef.hide();
  }
}

// ======================
// INDIVIDUAL LAYER HANDLERS
// ======================

// GMTDS (Shipping Density) Layer
function onGMTDSLayerActivated() {
  if (!props.riskLayersToggleRef?.isManualToggle) return;
  gmtdsLayerVisible.value = true;
  
  if (!gmtdsWMSLayer) {
    gmtdsWMSLayer = L.tileLayer.wms('https://gmtds.maplarge.com/ogc/ais:density/wms?', {
      layers: 'ais:density',
      format: 'image/png',
      transparent: true,
      attribution: 'Traffic density data &copy; <a href="https://globalmaritimetraffic.org/gmtds-data.html" target="_blank">GMTDS</a>',
      maxZoom: 12,
      minZoom: 0,
      crs: L.CRS.EPSG3857,
      time: '2024-01-01T00:00:00Z'
    }).addTo(props.mapInstance);
  }
}

function onGMTDSLayerDeactivated() {
  gmtdsLayerVisible.value = false;
  if (gmtdsWMSLayer) {
    props.mapInstance.removeLayer(gmtdsWMSLayer);
    gmtdsWMSLayer = null;
  }
}

// Seismic Risk Layer
function onSeismicRiskLayerActivated() {
  if (!props.riskLayersToggleRef?.isManualToggle) return;
  
  debounceLayerOperation('seismic-activate', () => {
    layerLoadingStates.value.seismic = true;
    seismicRiskLayerVisible.value = true;

    const cachedLayer = heatmapCache.get('seismic');
    if (cachedLayer) {
      nextTick(() => {
        cachedLayer.addTo(props.mapInstance);
        layerLoadingStates.value.seismic = false;
      });
      return;
    }

    if (!props.aggregatedSeismicData || props.aggregatedSeismicData.length === 0) {
      layerLoadingStates.value.seismic = false;
      return;
    }

    requestIdleCallback(() => {
      seismicRiskHeatmap = L.heatLayer(props.aggregatedSeismicData, {
        radius: 20,       // Reduced from 25
        blur: 12,         // Reduced from 15
        maxZoom: 10,
        minOpacity: 0.2
      });
      
      seismicRiskHeatmap.addTo(props.mapInstance);
      heatmapCache.set('seismic', seismicRiskHeatmap);
      layerLoadingStates.value.seismic = false;
    }, { timeout: 1000 });
  });
}

function onSeismicRiskLayerDeactivated() {
  seismicRiskLayerVisible.value = false;
  const cachedLayer = heatmapCache.get('seismic');
  if (cachedLayer && props.mapInstance.hasLayer(cachedLayer)) {
    props.mapInstance.removeLayer(cachedLayer);
  }
  if (seismicRiskHeatmap && props.mapInstance.hasLayer(seismicRiskHeatmap)) {
    props.mapInstance.removeLayer(seismicRiskHeatmap);
  }
}

// Seabed Substrate Layer
function createSeabedSubstrateLayer() {
  return L.tileLayer.wms('https://drive.emodnet-geology.eu/geoserver/gtk/wms?', {
    layers: 'gtk:seabed_substrate_1m',
    format: 'image/png',
    transparent: true,
    version: '1.1.1',
    crs: L.CRS.EPSG3857
  });
}

function onSeabedSubstrateLayerActivated() {
  if (!props.riskLayersToggleRef?.isManualToggle) return;
  if (!isSeabedActive.value) {
    seabedLayer = createSeabedSubstrateLayer();
    seabedLayer.addTo(props.mapInstance);
    isSeabedActive.value = true;
  }
}

function onSeabedSubstrateLayerDeactivated() {
  if (isSeabedActive.value && seabedLayer) {
    props.mapInstance.removeLayer(seabedLayer);
    isSeabedActive.value = false;
  }
}

// Fishing Risk Layer
function onFishingRiskLayerActivated() {
  if (!props.riskLayersToggleRef?.isManualToggle) return;
  
  layerLoadingStates.value.fishing = true;
  fishingLayerVisible.value = true;
  
  // Use cached layer if available
  const cachedLayer = heatmapCache.get('fishing');
  if (cachedLayer) {
    // Add layer in next tick to prevent blocking
    nextTick(() => {
      cachedLayer.addTo(props.mapInstance);
      layerLoadingStates.value.fishing = false;
    });
    return;
  }
  
  // Show loading indicator and create layer asynchronously
  if (!props.aggregatedFishingData || props.aggregatedFishingData.length === 0) {
    layerLoadingStates.value.fishing = false;
    return;
  }
  
  // Create layer with reduced complexity for better performance
  requestIdleCallback(() => {
    fishingRiskHeatmap = L.heatLayer(props.aggregatedFishingData, {
      radius: 10,
      blur: 10,
      maxZoom: 10,
      max: 100,
    });
    
    fishingRiskHeatmap.addTo(props.mapInstance);
    heatmapCache.set('fishing', fishingRiskHeatmap);
    layerLoadingStates.value.fishing = false;
  }, { timeout: 1000 });
}

function onFishingRiskLayerDeactivated() {
  fishingLayerVisible.value = false;
  const cachedLayer = heatmapCache.get('fishing');
  if (cachedLayer && props.mapInstance.hasLayer(cachedLayer)) {
    props.mapInstance.removeLayer(cachedLayer);
  }
  if (fishingRiskHeatmap && props.mapInstance.hasLayer(fishingRiskHeatmap)) {
    props.mapInstance.removeLayer(fishingRiskHeatmap);
  }
}

const debouncedOperations = new Map();

function debounceLayerOperation(key, operation, delay = 100) {
  if (debouncedOperations.has(key)) {
    clearTimeout(debouncedOperations.get(key));
  }
  
  const timeoutId = setTimeout(() => {
    operation();
    debouncedOperations.delete(key);
  }, delay);
  
  debouncedOperations.set(key, timeoutId);
}

function cleanupUnusedLayers() {
  // Clean up layers that haven't been used recently
  const MAX_CACHE_SIZE = 5;
  if (heatmapCache.size > MAX_CACHE_SIZE) {
    // Remove oldest entries (simple FIFO)
    const entries = Array.from(heatmapCache.entries());
    const toRemove = entries.slice(0, entries.length - MAX_CACHE_SIZE);
    
    toRemove.forEach(([key, layer]) => {
      if (layer && props.mapInstance.hasLayer(layer)) {
        props.mapInstance.removeLayer(layer);
      }
      heatmapCache.delete(key);
    });
  }
}

// Anchorage Risk Layer
function onAnchorageRiskLayerActivated() {
  if (!props.riskLayersToggleRef?.isManualToggle) return;
  
  debounceLayerOperation('anchorage-activate', () => {
    layerLoadingStates.value.anchorage = true;
    anchorageLayerVisible.value = true;

    if (!props.mapInstance) {
      layerLoadingStates.value.anchorage = false;
      return;
    }

    const cachedLayer = heatmapCache.get('anchorage');
    if (cachedLayer) {
      nextTick(() => {
        cachedLayer.addTo(props.mapInstance);
        layerLoadingStates.value.anchorage = false;
      });
      return;
    }

    const data = props.anchorageRiskData;
    if (!data || !Array.isArray(data.features)) {
      layerLoadingStates.value.anchorage = false;
      return;
    }

    // Process data in chunks to prevent blocking
    requestIdleCallback(() => {
      const heatmapData = data.features
        .filter((feature) => feature.geometry && Array.isArray(feature.geometry.coordinates))
        .map((feature) => {
          const [longitude, latitude] = feature.geometry.coordinates;
          return [latitude, longitude, 1];
        });

      if (heatmapData.length === 0) {
        layerLoadingStates.value.anchorage = false;
        return;
      }

      anchorageHeatmapLayer = L.heatLayer(heatmapData, {
        radius: 25,
        blur: 15,
        maxZoom: 13,
      });

      anchorageHeatmapLayer.addTo(props.mapInstance);
      heatmapCache.set('anchorage', anchorageHeatmapLayer);
      layerLoadingStates.value.anchorage = false;
    }, { timeout: 1000 });
  });
}

function onAnchorageRiskLayerDeactivated() {
  anchorageLayerVisible.value = false;
  const cachedLayer = heatmapCache.get('anchorage');
  if (cachedLayer && props.mapInstance.hasLayer(cachedLayer)) {
    props.mapInstance.removeLayer(cachedLayer);
  }
  if (anchorageHeatmapLayer && props.mapInstance.hasLayer(anchorageHeatmapLayer)) {
    props.mapInstance.removeLayer(anchorageHeatmapLayer);
  }
}

// Dredging Risk Layer
function onDredgingRiskLayerActivated() {
  if (!props.riskLayersToggleRef?.isManualToggle || !props.mapInstance) return;
  if (!props.dredgingData || !props.dredgingData?.features) return;

  if (dredgingRiskHeatmap) {
    dredgingRiskHeatmap.addTo(props.mapInstance);
    return;
  }

  const heatData = props.dredgingData.features.map(feature => {
    const coords = feature.geometry.coordinates;
    const intensity = feature.properties.extracted_amount_m3
      ? parseFloat(feature.properties.extracted_amount_m3)
      : 1;
    return [coords[1], coords[0], intensity];
  });

  dredgingRiskHeatmap = L.heatLayer(heatData, {
    radius: 15,
    blur: 10,
    maxZoom: 10
  });

  if (props.mapInstance) {
    dredgingRiskHeatmap.addTo(props.mapInstance);
  }
}

function onDredgingRiskLayerDeactivated() {
  if (dredgingRiskHeatmap && props.mapInstance) {
    props.mapInstance.removeLayer(dredgingRiskHeatmap);
    dredgingRiskHeatmap = null;
  }
}

// Shallow Waters Layer

const imageLoadPromises = new Map();

function preloadShallowWatersImages() {
  const shallowWatersImages = [
    { url: require('@/assets/shallowWaters/1.webp'), bounds: [[0.0, -180.0], [90.0, -90.0]] },
    { url: require('@/assets/shallowWaters/2.webp'), bounds: [[0.0, 90.0], [90.0, 180.0]] },
    { url: require('@/assets/shallowWaters/3.webp'), bounds: [[0.0, -90.0], [90.0, 0.0]] },
    { url: require('@/assets/shallowWaters/4.webp'), bounds: [[0.0, 0.0], [90.0, 90.0]] },
    { url: require('@/assets/shallowWaters/5.webp'), bounds: [[-90.0, 90.0], [0.0, 180.0]] },
    { url: require('@/assets/shallowWaters/6.webp'), bounds: [[-90.0, -90.0], [0.0, 0.0]] },
    { url: require('@/assets/shallowWaters/7.webp'), bounds: [[-90.0, 0.0], [0.0, 90.0]] }
  ];

  // Load images progressively
  shallowWatersImages.forEach((img, index) => {
    if (!imageLoadPromises.has(img.url)) {
      const promise = new Promise((resolve) => {
        const imgObj = new Image();
        imgObj.onload = () => {
          imageCache[img.url] = imgObj;
          resolve(imgObj);
        };
        imgObj.onerror = () => resolve(null); // Handle errors gracefully
        // Delay loading to prevent blocking initial render
        setTimeout(() => {
          imgObj.src = img.url;
        }, index * 100); // Stagger loading
      });
      imageLoadPromises.set(img.url, promise);
    }
  });
}

function monitorLayerPerformance(layerName, operation) {
  const startTime = performance.now();
  
  return operation().finally(() => {
    const endTime = performance.now();
    const duration = endTime - startTime;
    
    if (duration > 100) { // Log slow operations
      console.warn(`Layer ${layerName} took ${duration.toFixed(2)}ms to load`);
    }
  });
}

function onShallowWatersLayerActivated() {
  if (!props.riskLayersToggleRef?.isManualToggle) return;
  if (shallowWatersLayers.length === 0) {
    const shallowWatersImages = [
      { url: require('@/assets/shallowWaters/1.webp'), bounds: [[0.0, -180.0], [90.0, -90.0]] },
      { url: require('@/assets/shallowWaters/2.webp'), bounds: [[0.0, 90.0], [90.0, 180.0]] },
      { url: require('@/assets/shallowWaters/3.webp'), bounds: [[0.0, -90.0], [90.0, 0.0]] },
      { url: require('@/assets/shallowWaters/4.webp'), bounds: [[0.0, 0.0], [90.0, 90.0]] },
      { url: require('@/assets/shallowWaters/5.webp'), bounds: [[-90.0, 90.0], [0.0, 180.0]] },
      { url: require('@/assets/shallowWaters/6.webp'), bounds: [[-90.0, -90.0], [0.0, 0.0]] },
      { url: require('@/assets/shallowWaters/7.webp'), bounds: [[-90.0, 0.0], [0.0, 90.0]] }
    ];

    shallowWatersLayers = shallowWatersImages.map(img => 
      L.imageOverlay(img.url, img.bounds, { opacity: 0.7 })
    );
  }

  shallowWatersLayers.forEach(layer => props.mapInstance.addLayer(layer));
  isShallowWatersLayerActive.value = true;
}

function onShallowWatersLayerDeactivated() {
  shallowWatersLayers.forEach(layer => props.mapInstance.removeLayer(layer));
  isShallowWatersLayerActive.value = false;
}
</script>