<!--
  CableSourceSelector Component
  
  A dropdown component that allows users to switch between different cable data sources:
  - Telegeography: Primary cable data source
  - Mixed Sources: Alternative data from grouped cables JSON
  
  Features:
  - Dropdown selection with hover states
  - Tooltips for each source option
  - Automatic cable data updating when source changes
  - Preserves original data when switching back from mixed sources
-->

<template>
  <div class="cable-source-selector">
    <!-- Main dropdown toggle button -->
    <button @click.stop="toggleDropdown" class="select-button">
      {{ currentTranslations.cableSource }}
      <i :class="['arrow', isOpen ? 'up' : 'down']"></i>
    </button>

    <!-- Dropdown options -->
    <div v-if="isOpen" class="dropdown">
      <!-- Telegeography source option -->
      <div 
        class="dropdown-item" 
        @mouseenter="currentHoveredItem = 'Telegeography'" 
        @mouseleave="currentHoveredItem = ''"
      >
        <div class="source-container">
          <button 
            @click="selectSource('Telegeography')" 
            :class="{ active: selectedSource === 'Telegeography' }" 
            class="dropdown-button"
          >
            {{ currentTranslations.telegeography }}
          </button>
          <div 
            class="info-icon-wrapper" 
            :class="{ 
              'hovered': currentHoveredItem === 'Telegeography', 
              'selected': selectedSource === 'Telegeography' 
            }"
          >
            <span class="info-icon">
              <span class="tooltip">{{ currentTranslations.telegeographyTooltip }}</span>
            </span>
          </div>
        </div>
      </div>

      <!-- Mixed sources option -->
      <div 
        class="dropdown-item" 
        @mouseenter="currentHoveredItem = 'MixedSources'" 
        @mouseleave="currentHoveredItem = ''"
      >
        <div class="source-container">
          <button 
            @click="selectSource('MixedSources')" 
            :class="{ active: selectedSource === 'MixedSources' }" 
            class="dropdown-button"
          >
            {{ currentTranslations.mixedSources }}
          </button>
          <div 
            class="info-icon-wrapper" 
            :class="{ 
              'hovered': currentHoveredItem === 'MixedSources', 
              'selected': selectedSource === 'MixedSources' 
            }"
          >
            <span class="info-icon">
              <span class="tooltip">{{ currentTranslations.mixedSourcesTooltip }}</span>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { useDataCablesStore } from "@/store/datacables";
import groupedCables from "@/assets/groupedCables.json";

// ========================
// PROPS DEFINITION
// ========================

const props = defineProps({
  currentTranslations: {
    type: Object,
    required: true
  },
  currentLanguage: {
    type: String,
    required: true
  }
});

// ========================
// STATE MANAGEMENT
// ========================

// Store reference
const dataCablesStore = useDataCablesStore();

// Component state
const selectedSource = ref("Telegeography");
const isOpen = ref(false);
const currentHoveredItem = ref("");

// Data preservation for source switching
const originalCableData = ref([]);

// ========================
// UI INTERACTION METHODS
// ========================

/**
 * Toggles the dropdown open/closed state
 */
const toggleDropdown = () => {
  isOpen.value = !isOpen.value;
};

/**
 * Handles source selection from dropdown
 * @param {string} source - The selected source ('Telegeography' or 'MixedSources')
 */
const selectSource = (source) => {
  selectedSource.value = source;
  isOpen.value = false;
  updateCableSource(source);
};

// ========================
// DATA MANAGEMENT METHODS
// ========================

/**
 * Updates cable data based on selected source
 * @param {string} source - The source to switch to
 */
const updateCableSource = async (source) => {
  if (source === "MixedSources") {
    // Store original data if not already stored
    if (originalCableData.value.length === 0) {
      originalCableData.value = dataCablesStore.dataCables.map((cable) => ({
        name: cable.name,
        geoData: JSON.parse(JSON.stringify(cable.geoData)),
      }));
    }
    updateMixedSourceCables();
  } else {
    // Restore original data if switching back to Telegeography
    if (originalCableData.value.length > 0) {
      dataCablesStore.dataCables.forEach((cable) => {
        const originalCable = originalCableData.value.find((orig) => orig.name === cable.name);
        if (originalCable) {
          cable.geoData = originalCable.geoData;
        }
      });
      originalCableData.value = [];
    }
    await dataCablesStore.initialize();
  }
};

/**
 * Updates cable data with mixed source coordinates
 */
const updateMixedSourceCables = () => {
  dataCablesStore.dataCables.forEach((cable) => {
    const mixedSourceCable = groupedCables.features.find((item) => item.properties.name === cable.name);
    if (mixedSourceCable) {
      const geoData = [
        mixedSourceCable.geometry.coordinates.map((segment) => segment.map((coord) => [coord[0], coord[1]])),
      ];
      cable.geoData = geoData;
    }
  });
};
</script>

<style>
/* ========================
   MAIN COMPONENT STYLES
   ======================== */

.cable-source-selector {
  background-color: #48506a;
  position: absolute;
  top: -80px;
  left: 0px;
  z-index: 1000;
  color: #ffffff;
}

/* ========================
   DROPDOWN TOGGLE BUTTON
   ======================== */

/* Arrow icon for dropdown state indication */
.arrow {
  border: solid #ffffff;
  color: #ffffff;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  margin-left: 8px;
}

.arrow.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.arrow.up {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

/* ========================
   DROPDOWN MENU STYLES
   ======================== */

.dropdown {
  position: absolute;
  background-color: #48506a;
  color: #ffffff;
  border: none;
  padding: 6px 0;
  width: 100%;
  z-index: 1;
}

.dropdown-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 3px 0;
}

.source-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

/* ========================
   DROPDOWN BUTTON STYLES
   ======================== */

.dropdown button {
  width: 0px;
  padding: 6px 6px;
  text-align: left;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #ffffff;
  flex-grow: 1;
}

.dropdown button:hover,
.dropdown button.active {
  background-color: #697fc7;
  color: #ffffff;
}

/* ========================
   INFO ICON & TOOLTIP STYLES
   ======================== */

.info-icon-wrapper {
  display: flex;
  align-items: center;
  padding: 4.5px;
  height: 120%;
  border-radius: 0px;
}

.info-icon-wrapper.hovered,
.info-icon-wrapper.selected {
  background-color: #697fc7;
  height: 120%;
}

.info-icon {
  position: relative;
  font-size: 14px;
  cursor: pointer;
}

/* Tooltip styling */
.info-icon .tooltip {
  position: absolute;
  top: 100%;
  left: 20px;
  width: 300px;
  background-color: #333;
  color: #ffffff;
  border: none;
  font-size: 12px;
  padding: 10px;
  border-radius: 4px;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.2s, opacity 0.2s;
  z-index: 10;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  white-space: normal;
  line-height: 1.4;
}

.info-icon:hover .tooltip {
  visibility: visible;
  opacity: 1;
}
</style>