<template>
  <!-- Distance Grid Popup -->
  <div v-if="distanceGridActive" class="map-popup distance-popup">
    <h3>{{ currentTranslations.cableRepairTimes }}</h3>
    <div class="popup-content">
      <div class="legend-grid">
        <div class="legend-item"><span class="color-box" style="background-color:#6DAED4"></span><span class="legend-text">{{ currentTranslations.repairUnder100km }}</span></div>
        <div class="legend-item"><span class="color-box" style="background-color:#A3D39C"></span><span class="legend-text">{{ currentTranslations.repair100to500km }}</span></div>
        <div class="legend-item"><span class="color-box" style="background-color:#FFE08A"></span><span class="legend-text">{{ currentTranslations.repair500to1000km }}</span></div>
        <div class="legend-item"><span class="color-box" style="background-color:#FDB366"></span><span class="legend-text">{{ currentTranslations.repair1000to1500km }}</span></div>
        <div class="legend-item"><span class="color-box" style="background-color:#F67E4F"></span><span class="legend-text">{{ currentTranslations.repair1500to2000km }}</span></div>
        <div class="legend-item"><span class="color-box" style="background-color:#E8453C"></span><span class="legend-text">{{ currentTranslations.repair2000to2500km }}</span></div>
        <div class="legend-item"><span class="color-box" style="background-color:#B50A2A"></span><span class="legend-text">{{ currentTranslations.repair2500to3000km }}</span></div>
        <div class="legend-item"><span class="color-box" style="background-color:#67001D"></span><span class="legend-text">{{ currentTranslations.repairOver3000km }}</span></div>
      </div>
      <div class="progress-container">
        <div v-if="isCalculating" class="progress-bar">
          <div class="progress-fill" :style="{ width: calculationProgress + '%' }"></div>
          <span class="progress-text">{{ Math.round(calculationProgress) }}%</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted } from 'vue';
import L from 'leaflet';
import type { FeatureCollection } from 'geojson';
import oceanCellsData from '@/assets/global_ocean_cells.json';

// ==============================================
// COMPONENT PROPS
// ==============================================

const props = defineProps({
vesselPositions: {
  type: Array,
  required: false
},
currentTranslations: {
  type: Object,
  required: false
},
mapInstance: {
  type: Object,
  required: false
},
riskLayersToggleRef: Object
});

// ==============================================
// REACTIVE STATE
// ==============================================

const distanceGridPolygonLayer = ref(null);
const distanceGridActive = ref(false);
const isCalculating = ref(false);
const calculationProgress = ref(0);
const calculationWorker = ref(null);
const renderQueue = ref([]);
const isRendering = ref(false);

// ==============================================
// CONSTANTS & OPTIMIZATIONS
// ==============================================

// Spatial indexing for faster lookups
const GRID_SIZE = 10; // degrees
const spatialIndex = new Map();

// Performance settings
const RENDER_BATCH_SIZE = 100; // Render features in batches
const RENDER_DELAY = 16; // ~60fps
const MIN_ZOOM_LEVEL = 4; // Don't render at very low zoom
const MAX_DISTANCE = 3001;

// Pre-process and spatially index ocean cells
const processedOceanCells = (() => {
const processed = oceanCellsData.map(cell => {
  const [centerLon, centerLat] = cell.center;
  return {
    ...cell,
    centerLat,
    centerLon,
    cosLat: Math.cos(centerLat * Math.PI / 180)
  };
});

// Build spatial index
processed.forEach(cell => {
  const gridX = Math.floor(cell.centerLon / GRID_SIZE);
  const gridY = Math.floor(cell.centerLat / GRID_SIZE);
  const key = `${gridX},${gridY}`;
  
  if (!spatialIndex.has(key)) {
    spatialIndex.set(key, []);
  }
  spatialIndex.get(key).push(cell);
});

return processed;
})();

// ==============================================
// UTILITY FUNCTIONS
// ==============================================

const calculateDistance = (lat1, lon1, lat2, lon2) => {
const R = 6371;
const dLat = (lat2 - lat1) * Math.PI / 180;
const dLon = (lon2 - lon1) * Math.PI / 180;

const a =
  Math.sin(dLat / 2) * Math.sin(dLat / 2) +
  Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
  Math.sin(dLon / 2) * Math.sin(dLon / 2);
const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

return R * c;
};

const getDistanceCategory = (distance) => {
if (distance <= 100) return { category: "<100km", score: 0.1 };
if (distance <= 500) return { category: "100-500km", score: 0.2 };
if (distance <= 1000) return { category: "500-1000km", score: 0.35 };
if (distance <= 1500) return { category: "1000-1500km", score: 0.5 };
if (distance <= 2000) return { category: "1500-2000km", score: 0.65 };
if (distance <= 2500) return { category: "2000-2500km", score: 0.8 };
if (distance <= 3000) return { category: "2500-3000km", score: 0.9 };
return { category: ">3000km", score: 1.0 };
};

// ==============================================
// WEB WORKER FOR CALCULATIONS
// ==============================================

const createWorker = () => {
const workerScript = `
  self.onmessage = function(e) {
    const { cells, vessels, chunkIndex, totalChunks } = e.data;
    const results = new Map();
    
    // Process this chunk of cells
    cells.forEach(cell => {
      let minDistance = Infinity;
      
      vessels.forEach(vessel => {
        // Quick bounding box check
        if (Math.abs(vessel.lat - cell.centerLat) > 27 || 
            Math.abs(vessel.lon - cell.centerLon) > 27) {
          return;
        }
        
        // Calculate distance
        const R = 6371;
        const dLat = (vessel.lat - cell.centerLat) * Math.PI / 180;
        const dLon = (vessel.lon - cell.centerLon) * Math.PI / 180;
        
        const a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
          Math.cos(cell.centerLat * Math.PI / 180) * Math.cos(vessel.lat * Math.PI / 180) *
          Math.sin(dLon / 2) * Math.sin(dLon / 2);
        const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
        const distance = R * c;
        
        if (distance < minDistance) {
          minDistance = distance;
        }
      });
      
      results.set(cell.cell_id, minDistance === Infinity ? 3001 : minDistance);
    });
    
    self.postMessage({
      chunkIndex,
      results: Array.from(results.entries()),
      progress: ((chunkIndex + 1) / totalChunks) * 100
    });
  };
`;

const blob = new Blob([workerScript], { type: 'application/javascript' });
return new Worker(URL.createObjectURL(blob));
};

// ==============================================
// OPTIMIZED DISTANCE CALCULATION
// ==============================================

const calculateDistancesAsync = async (vesselPositions) => {
return new Promise((resolve, reject) => {
  const validVessels = vesselPositions.filter(vessel => 
    vessel?.position?.lat && vessel?.position?.lon
  ).map(vessel => ({
    lat: vessel.position.lat,
    lon: vessel.position.lon
  }));

  if (validVessels.length === 0) {
    resolve(new Map());
    return;
  }

  calculationWorker.value = createWorker();
  const cellDistances = new Map();
  const CHUNK_SIZE = 500; // Smaller chunks for better progress updates
  const chunks = [];
  
  // Create chunks
  for (let i = 0; i < processedOceanCells.length; i += CHUNK_SIZE) {
    chunks.push(processedOceanCells.slice(i, i + CHUNK_SIZE));
  }

  let completedChunks = 0;
  calculationProgress.value = 0;

  calculationWorker.value.onmessage = (e) => {
    const { results, progress } = e.data;
    
    // Merge results
    results.forEach(([cellId, distance]) => {
      if (!cellDistances.has(cellId) || distance < cellDistances.get(cellId)) {
        cellDistances.set(cellId, distance);
      }
    });

    completedChunks++;
    calculationProgress.value = progress;

    if (completedChunks === chunks.length) {
      calculationWorker.value.terminate();
      calculationWorker.value = null;
      resolve(cellDistances);
    }
  };

  calculationWorker.value.onerror = (error) => {
    calculationWorker.value?.terminate();
    calculationWorker.value = null;
    reject(error);
  };

  // Send chunks to worker
  chunks.forEach((chunk, index) => {
    calculationWorker.value.postMessage({
      cells: chunk,
      vessels: validVessels,
      chunkIndex: index,
      totalChunks: chunks.length
    });
  });
});
};

// ==============================================
// OPTIMIZED RENDERING
// ==============================================

const renderBatch = async () => {
if (isRendering.value || renderQueue.value.length === 0) return;

isRendering.value = true;
const batch = renderQueue.value.splice(0, RENDER_BATCH_SIZE);

// Create features for this batch
const features = batch.map(cell => {
  const { category, score } = getDistanceCategory(cell.distance);
  const [minLon, minLat, maxLon, maxLat] = cell.bounds;
  
  return {
    type: "Feature",
    properties: {
      cell_id: cell.cell_id,
      distance_km: cell.distance,
      distance_category: category,
      score: score
    },
    geometry: {
      type: "Polygon",
      coordinates: [[
        [minLon, maxLat],
        [maxLon, maxLat],
        [maxLon, minLat],
        [minLon, minLat],
        [minLon, maxLat]
      ]]
    }
  };
});

// Add batch to layer
if (distanceGridPolygonLayer.value && features.length > 0) {
  const batchLayer = L.geoJSON({ type: "FeatureCollection", features }, {
    style: (feature) => {
      const colorMap = {
        "<100km": "#6DAED4",
        "100-500km": "#A3D39C",
        "500-1000km": "#FFE08A",
        "1000-1500km": "#FDB366",
        "1500-2000km": "#F67E4F",
        "2000-2500km": "#E8453C",
        "2500-3000km": "#B50A2A",
        ">3000km": "#67001D"
      };

      return {
        fillColor: colorMap[feature.properties.distance_category] || '#808080',
        weight: 0.1, // Reduced weight for better performance
        color: 'rgba(0,0,0,0.1)',
        fillOpacity: 0.6
      };
    }
  });

  batchLayer.addTo(distanceGridPolygonLayer.value);
}

isRendering.value = false;

// Continue rendering if there are more items
if (renderQueue.value.length > 0) {
  setTimeout(renderBatch, RENDER_DELAY);
}
};

const displayDistancePolygonGrid = async (vesselPositions) => {
if (!props.mapInstance?.addLayer) return;

// Check zoom level
const currentZoom = props.mapInstance.getZoom();
if (currentZoom < MIN_ZOOM_LEVEL) {
  console.warn(`Zoom level too low (${currentZoom}). Minimum required: ${MIN_ZOOM_LEVEL}`);
  return;
}

isCalculating.value = true;
calculationProgress.value = 0;

try {
  // Calculate distances asynchronously
  const cellDistances = await calculateDistancesAsync(vesselPositions);

  // Create layer group for efficient management
  distanceGridPolygonLayer.value = L.layerGroup();
  
  // Prepare render queue with visible cells only
  const mapBounds = props.mapInstance.getBounds();
  renderQueue.value = processedOceanCells
    .filter(cell => {
      // Only include cells that are visible
      return mapBounds.contains([cell.centerLat, cell.centerLon]);
    })
    .map(cell => ({
      ...cell,
      distance: cellDistances.get(cell.cell_id) || MAX_DISTANCE
    }));

  // Add layer to map
  distanceGridPolygonLayer.value.addTo(props.mapInstance);
  distanceGridPolygonLayer.value.bringToBack();

  // Start batch rendering
  renderBatch();

} catch (error) {
  console.error('Error calculating distances:', error);
} finally {
  isCalculating.value = false;
  calculationProgress.value = 100;
}
};

// ==============================================
// LAYER MANAGEMENT WITH CLEANUP
// ==============================================

const onDistancePolygonGridActivated = (vesselPositions) => {
displayDistancePolygonGrid(vesselPositions);
};

const onDistancePolygonGridDeactivated = () => {
// Cancel any ongoing calculations
if (calculationWorker.value) {
  calculationWorker.value.terminate();
  calculationWorker.value = null;
}

// Clear render queue
renderQueue.value = [];
isRendering.value = false;
isCalculating.value = false;
calculationProgress.value = 0;

// Remove layer
if (distanceGridPolygonLayer.value) {
  distanceGridPolygonLayer.value.clearLayers();
  distanceGridPolygonLayer.value.remove();
  distanceGridPolygonLayer.value = null;
}
};

// ==============================================
// PUBLIC API
// ==============================================

const activateDistancePolygonGridLayer = () => {
if (!props.riskLayersToggleRef?.isManualToggle) return;
distanceGridActive.value = true;
onDistancePolygonGridActivated(props.vesselPositions);
};

const deactivateDistancePolygonGridLayer = () => {
distanceGridActive.value = false;
onDistancePolygonGridDeactivated();
};

// ==============================================
// LIFECYCLE HOOKS
// ==============================================

onUnmounted(() => {
onDistancePolygonGridDeactivated();
});

// Expose public methods
defineExpose({
activateDistancePolygonGridLayer,
deactivateDistancePolygonGridLayer,
distanceGridActive
});
</script>

<style scoped>
/* Progress Bar */
.progress-container {
margin-top: 10px;
}

.progress-bar {
position: relative;
width: 100%;
height: 20px;
background-color: #f0f0f0;
border-radius: 10px;
overflow: hidden;
}

.progress-fill {
height: 100%;
background: linear-gradient(90deg, #4CAF50, #8BC34A);
transition: width 0.3s ease;
border-radius: 10px;
}

.progress-text {
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
font-size: 12px;
font-weight: bold;
color: #333;
z-index: 1;
}
</style>