<template>
  <!-- 
    LeafletMap.vue - Main map component for submarine cable visualization
    Features:
    - Displays submarine cables, landing sites, and related infrastructure
    - Multiple interactive layers (ocean depth, fishing activity, seismic data)
    - Risk assessment visualization
    - Vessel tracking functionality
    - Multi-language support
  -->
  <l-map
    ref="map"
    :zoom="zoom"
    :center="center"
    :minZoom="1.5"
    :maxZoom="20"
    :options="{ zoomControl: true, markerZoomAnimation: false, worldCopyJump: true }"
    @baselayerchange="layerChanged"
    @overlayadd="overlayAdded"
    @overlayremove="overlayRemoved"
    @click="(e: LeafletMouseEvent) => onMapClick(e)"
    @ready="onMapReady"
    @mousemove="(e: LeafletMouseEvent) => updateMousePosition(e)"
  >
    <!-- *********************** -->
    <!-- *** CABLE RENDERING *** -->
    <!-- *********************** -->
    <template v-for="cable in cableStore.dataCables">
      <l-polyline
        v-for="section in cable.geoData"
        :lat-lngs="section"
        :key="section"
        :color="cable.color"
        :opacity="
          !viewingVesselTrack && (cableStore.isNoneSelected ||cableStore.isCableSelected(cable.id))
            ? 1
            : 0.15
        "
        :dashArray="cable.outOfService ? '5 5' : undefined"
        @click="(e) => onCableClick(e, cable)"
        @ready="(e) => onCreateLine(e, cable.id)"
        @mouseover="(e) => showTooltip(e, cable.name)"
        @mouseout="hideTooltip"
      >
      </l-polyline>
    </template>

    <!-- **************************** -->
    <!-- *** LANDING SITE MARKERS *** -->
    <!-- **************************** -->
    <l-circle-marker
      v-for="tee in landingSiteStore.getLandingSites"
      :lat-lng="tee.geoData"
      :key="tee"
      :radius="
        landingSiteStore.isLandingSiteSelected(toRaw(tee))
          ? circle.selectedRadius
          : circle.radius
      "
      :color="
        landingSiteStore.isLandingSiteSelected(toRaw(tee))
          ? circle.selectedColor
          : circle.color
      "
      @click="(e) => onLandingSiteClick(e, tee)"
    >
      <l-tooltip>{{ tee.name }}, {{ tee.country }}</l-tooltip>
    </l-circle-marker>

    <!-- ************************ -->
    <!-- *** LAYER MANAGEMENT *** -->
    <!-- ************************ -->
    <LayerManager
      :map-instance="mapInstance"
      :current-language="currentLanguage"
      :current-translations="currentTranslations"
      :clustered-data-centers-ref="clusteredDataCentersRef"
      :reset-map-layer="resetMapLayer"
      @layer-control-ready="onLayerControlReady"
      @reset-map="resetMap"
      ref="layerManagerRef"
    />

    <!-- *********************** -->
    <!-- *** SEISMIC ACTIVITY *** -->
    <!-- *********************** -->
    <SeismicActivity 
      :current-translations="currentTranslations"
      :show-seismic-overlay="showSeismicOverlay"
      :current-language="currentLanguage"
    />

    <!-- *********************** -->
    <!-- *** FISHING ACTIVITY *** -->
    <!-- *********************** -->
    <l-tile-layer
      name="Fishing Activity"
      :url="modifiedPostUrl"
      :visible="false"
      attribution='<a href=" https://globalfishingwatch.org">Powered by Global Fishing Watch</a>'
      layer-type="base"
    ></l-tile-layer>

    <!-- ******************** -->
    <!-- *** OCEAN DEPTH *** -->
    <!-- ******************** -->
    <OceanDepth
      ref="oceanDepthRef"
      :map-instance="mapInstance"
      :translations="currentTranslations"
      :is-ocean-selected="oceanSelected"
      :is-drawing-mode-active="isDrawingModeActive"
      @update:isDrawingModeActive="(val) => isDrawingModeActive = val"
    />

    <l-tile-layer
      v-if="oceanSelected"
      name="Ocean Depth"
      url="https://{s}.basemaps.cartocdn.com/rastertiles/voyager_only_labels/{z}/{x}/{y}{r}.png"
      attribution="Tiles &copy; Esri &mdash; Sources: GEBCO, NOAA, CHS, OSU, UNH, CSUMB, National Geographic, DeLorme, NAVTEQ, and Esri"
      :maxZoom="10"
      :visible="false"
    >
    </l-tile-layer>

    <!-- *********************** -->
    <!-- *** RISK LAYERS *** -->
    <!-- *********************** -->
    <RiskLayers 
      :current-translations="currentTranslations"
      :map-instance="mapInstance"
      :risk-layers-toggle-ref="riskLayersToggleRef"
      :current-language="currentLanguage"
      :vessel-positions="vesselPositions"
      :fishing-data-europe="fishingDataEurope"
      :terrain-data-choropleth-europe="terrainDataChoroplethEurope"
      :dredging-data="dredgingData"
      :aggregated-seismic-data="aggregatedSeismicData"
      :anchorage-risk-data="anchorageRiskHeatmapData"
      :aggregated-fishing-data="aggregatedFishingData"
    />

    <!-- ******************************** -->
    <!-- *** ANCHORAGE & SEABED USE *** -->
    <!-- ******************************** -->
    <l-layer-group 
      name="Anchorage and seabed use" 
      layer-type="overlay" 
      :visible="false"
    >
      <!-- Seamarks Layer -->
      <l-tile-layer
        name="Seamarks"
        url="https://tiles.openseamap.org/seamark/{z}/{x}/{y}.png"
        attribution='Map data: &copy; <a href="http://www.openseamap.org">OpenSeaMap</a> contributors'
        :visible="false"
      />

      <!-- Fixed Infrastructure Layer -->
      <l-layer-group name="Fixed Infrastructure" layer-type="overlay">
        <l-circle-marker
          v-for="infrastructure in infrastructurePositions"
          :key="infrastructure.id"
          :lat-lng="infrastructure.position"
          :radius="5"
          color="#FFFFFF"
          fill-color="#1d8f82"
          :opacity="1"
          :fill-opacity="1"
          :weight="1"
        >
          <l-tooltip>
            {{ getTranslatedInfrastructureName(infrastructure) }}, 
            {{ currentTranslations.since }}: {{ infrastructure.lastSeen }}
          </l-tooltip>
        </l-circle-marker>
      </l-layer-group>
    </l-layer-group>

    <!-- *********************** -->
    <!-- *** BASE LAYER GROUP *** -->
    <!-- *********************** -->
    <l-layer-group name="Base Layer" layer-type="base" :visible="true">
    </l-layer-group>
    
    <!-- *********************** -->
    <!-- *** MARITIME ZONES *** -->
    <!-- *********************** -->
    <l-geo-json
      :geojson="eez12nmJson"
      layer-type="overlay"
      name="Territorial Waters (12nm)"
      :visible="false"
      :options-style="getTerritorialOptionsStyle"
    ></l-geo-json>

    <l-geo-json
      :geojson="eezBoundariesJson"
      layer-type="overlay"
      name="Exclusive Economic Zone (200nm)"
      :visible="false"
      :options-style="getEEZOptionsStyle"
    ></l-geo-json>

    <!-- *********************** -->
    <!-- *** GAS PIPELINES *** -->
    <!-- *********************** -->
    <GasPipelines
      ref="gasPipelineLayerRef"
      :gas-pipeline-data="gasPipelineData"
      :map-instance="mapInstance"
      :current-translations="currentTranslations"
    />

    <!-- *********************** -->
    <!-- *** VESSEL TRACKING *** -->
    <!-- *********************** -->
    <VesselTracking
      :mapReady="mapReady"
      :mapInstance="mapInstance"
      :controlInstance="controlInstance"
      :currentLanguage="currentLanguage"
      :cableStore="cableStore"
      :layer-group="cableShipsLayerGroup"
      v-model:vesselPositions="vesselPositions"
      v-model:hoveredMarker="hoveredMarker"
      v-model:viewingVesselTrack="viewingVesselTrack"
      ref="vesselTrackingRef"
    />

    <l-layer-group
      :visible="false"
      name="Cable Ships"
      layer-type="overlay"
      @remove= "exitVesselTrackingMode()"
    >
      <l-marker 
        v-for="vessel in vesselPositions" 
        :key="vessel.id" 
        :lat-lng="vessel.position" 
        :icon="createShipIcon(vessel, 'OVERLAY')"
        @click="(e) => onShipClick(e, vessel)">
      </l-marker>
    </l-layer-group>

    <!-- *********************** -->
    <!-- *** UI CONTROLS *** -->
    <!-- *********************** -->
    <!-- Help Button -->
    <div @click="questionMarkClicked" class="question-mark">
      <h2>?</h2>
    </div>

    <div @click="showAbortModal = true" class="abort-evaluation-button">
  <h2></h2>
</div>

<!-- Abort Evaluation Modal -->
<div v-if="showAbortModal" class="abort-modal-overlay" @click="showAbortModal = false">
  <div class="abort-modal" @click.stop>
    <h3>{{ currentTranslations.abortEvaluation || 'Abort Evaluation' }}</h3>
    <p>{{ currentTranslations.abortEvaluationText || 'Are you sure you want to abort the evaluation? This will remove your current session replay.' }}</p>
    <div class="modal-buttons">
      <button @click="showAbortModal = false" class="cancel-button">
        {{ currentTranslations.cancel || 'Cancel' }}
      </button>
      <button @click="abortEvaluation" class="confirm-button">
        {{ currentTranslations.confirm || 'Confirm' }}
      </button>
    </div>
  </div>
</div>

    <!-- Functionality Overview Modal -->
    <FunctionalityOverview 
      ref="functionalityOverviewRef"
      :current-translations="currentTranslations"
      :currentLanguage="currentLanguage"
    />

    <!-- Fishing Date Picker -->
    <FishingDatePicker
      :visible="fishingSelected"
      :current-translations="currentTranslations"
      @update:fishingUrl="(url) => modifiedPostUrl = url"
      @fishing-layer-ready="(ready) => fishLayerReady = ready"
    />

    <!-- Language Selector -->
    <div class="language-selector">
      <div class="language-icon"></div>
      <select v-model="currentLanguage" @change="changeLanguage(currentLanguage)">
        <option value="ENGLISH">English</option>
        <option value="GERMAN">Deutsch</option>
        <option value="SPANISH">Espaol</option>
        <option value="FRENCH">Franais</option>
        <option value="ITALIAN">Italiano</option>
        <option value="CHINESE"></option>
      </select>
    </div>

    <!-- Cable Source Selector -->
    <div class="cable-source-selector">
      <CableSourceSelector 
        :current-translations="currentTranslations"
        :currentLanguage="currentLanguage"
      />
    </div>

    <!-- *********************** -->
    <!-- *** CLUSTERED DATA *** -->
    <!-- *********************** -->
    <!-- Clustered Anchorage -->
    <ClusteredAnchorage 
      :mapInstance="mapInstance"
      :controlInstance="controlInstance"
      :map-ready="mapReady"
      :current-translations="currentTranslations"
    />

    <!-- Clustered Data Centers -->
    <ClusteredDataCenters
      ref="clusteredDataCentersRef"
      :mapInstance="mapInstance"
      :controlInstance="controlInstance"
      :mapReady="mapReady"
      :currentTranslations="currentTranslations"
    />

    <!-- *********************** -->
    <!-- *** LAYER TOGGLES *** -->
    <!-- *********************** -->
    <!-- Anchorage Layer Toggle -->
    <AnchorageLayerToggle 
      ref="anchorageLayerToggleRef"
      :map-instance="mapInstance"
      :control-instance="controlInstance"
      :current-translations="currentTranslations"
    />

    <!-- Risk Layers Toggle -->
    <RiskLayersToggle 
      ref="riskLayersToggleRef"
      :map-instance="mapInstance"
      :control-instance="controlInstance"
      :current-translations="currentTranslations"
      :currentLanguage="currentLanguage"
    />

    <!-- Maintenance Layer Toggle -->
    <MaintenanceLayerToggle 
      ref="maintenanceLayerToggleRef"
      :map-instance="mapInstance"
      :control-instance="controlInstance"
      :current-translations="currentTranslations"
      :viewingVesselTrack="viewingVesselTrack"
    />
  </l-map>

  <!-- *********************** -->
  <!-- *** DETAILS PANELS *** -->
  <!-- *********************** -->
  <!-- Earthquake Details Panel -->
  <div v-if="selectedEarthquake" class="earthquake-details">
    <h2>{{ currentTranslations.selectedEarthquakeDetails }}:</h2>
    <p><strong>{{ currentTranslations.value.magnitude }}:</strong> {{ selectedEarthquake.mag }}</p>
    <p><strong>{{ currentTranslations.value.location }}:</strong> {{ selectedEarthquake.place }}</p>
    <p><strong>{{ currentTranslations.value.time }}:</strong> {{ new Date(selectedEarthquake.time).toLocaleString() }}</p>
  </div>
</template>

<script setup lang="ts">
// ***********************
// *** IMPORTS SECTION ***
// ***********************
import FunctionalityOverview from '@/components/controls/FunctionalityOverview.vue';
import { ref, onMounted, watch, toRaw, computed } from 'vue';
import L from 'leaflet';
import type { LeafletMouseEvent } from 'leaflet';
import type { FeatureCollection } from 'geojson';
import 'leaflet/dist/leaflet.css';
import 'leaflet.polylinemeasure/Leaflet.PolylineMeasure.css';
import 'leaflet.polylinemeasure/Leaflet.PolylineMeasure';
import {
  LMap,
  LTileLayer,
  LTooltip,
  LPolyline,
  LMarker,
  LCircleMarker,
  LGeoJson,
  LLayerGroup,
} from '@vue-leaflet/vue-leaflet';
import { useDataCablesStore } from '@/store/datacables';
import { useLandingSitesStore } from '@/store/landingSites';

import LayerManager from '@/components/LayerManager.vue';
import CableSourceSelector from '@/components/controls/CableSourceSelector.vue';
import translations from "@/assets/translations/translations.json";

import VesselTracking from './layers/maritime/VesselTracking.vue';
import MaintenanceLayerToggle from '@/components/layers/infrastructure/MaintenanceLayerToggle.vue';
import eezBoundariesJson from "@/assets/eez_boundaries_v12.json";
import eez12nmJson from "@/assets/eez_12nm_v4.json";
import OceanDepth from '@/components/layers/natural/OceanDepth.vue';
import FishingDatePicker from '@/components/controls/FishingDatePicker.vue';
import GasPipelines from '@/components/layers/infrastructure/GasPipelines.vue';
import fixedInfrastructure from "@/assets/fixedInfrastructure.json";
import ClusteredDataCenters from '@/components/layers/infrastructure/ClusteredDataCenters.vue';
import AnchorageLayerToggle from '@/components/layers/infrastructure/AnchorageLayerToggle.vue';
import ClusteredAnchorage from '@/components/layers/infrastructure/ClusteredAnchorage.vue';
import SeismicActivity from '@/components/layers/natural/SeismicActivity.vue';
import RiskLayers from '@/components/layers/risk/RiskLayers.vue';
import RiskLayersToggle from '@/components/layers/risk/RiskLayersToggle.vue';

import debounce from "lodash.debounce";
import "@maptiler/leaflet-maptilersdk";
import 'leaflet-arrowheads';

const showAbortModal = ref(false);

const abortEvaluation = () => {
  try {
    // Access the global tracker instance
    const tracker = (window as any)._trackerInstance;
    
    if (tracker) {
      // Stop the current session
      tracker.stop();
      console.log('Session replay aborted');
    }
    
    // Close the modal
    showAbortModal.value = false;
    
    // Try different methods to close the window
    if (navigator.app) {
      // For mobile apps
      navigator.app.exitApp();
    } else if (window.opener) {
      // For popup windows
      window.close();
    } else {
      // For main windows - this may not work due to browser restrictions
      window.open('', '_self', '').close();
      // Fallback: redirect to a blank page or confirmation page
      window.location.href = 'about:blank';
    }
  } catch (error) {
    console.error('Error aborting evaluation:', error);
    showAbortModal.value = false;
  }
};

// **************************
// *** STORE DECLARATIONS ***
// **************************
const cableStore = useDataCablesStore();
const landingSiteStore = useLandingSitesStore();

// **************************
// *** REF DECLARATIONS ***
// **************************
const layerManagerRef = ref<any>(null);
const gasPipelineLayerRef = ref();
const vesselTrackingRef = ref<any>(null);
const maintenanceLayerToggleRef = ref(null);
const clusteredDataCentersRef = ref(null);
const anchorageLayerToggleRef = ref(null);
const oceanDepthRef = ref<any>(null);
const functionalityOverviewRef = ref(null);
const riskLayersToggleRef = ref(null);

// **************************
// *** REACTIVE STATE ***
// **************************
// Map configuration
const zoom = ref(2.5);
const center = ref([47.41322, -1.219482]);
const map = ref<LMap | null>(null);
const mapReady = ref(false);
const mapInstance = ref(null);
const controlInstance = ref(null);
const polylineMeasure = ref(null);

// Landing site markers style
const circle = {
  radius: 3,
  selectedRadius: 6,
  color: 'grey',
  selectedColor: 'red',
};

// Layer states
const oceanSelected = ref(false);
const isDrawingModeActive = ref(false);
const fishingSelected = ref(false);
const showSeismicOverlay = ref(false);
const modifiedPostUrl = ref('https://{s}.tile.openstreetmap.de/{z}/{x}/{y}.png');
const fishLayerReady = ref(false);
// Seismic
const selectedEarthquake = ref(null);
// Vessel tracking
const hoveredMarker = ref(null);
const eventMarkerLayer = ref(null);
const viewingVesselTrack = ref(false);
const vesselPositions = ref([]);
const cableShipsLayerGroup = ref(L.layerGroup());

// Infrastructure data
const infrastructurePositions = ref([]);
const resetMapLayer = ref(null);

// Data cache for lazy loading
const dataCache = ref({
  anchorage: null,
  dredging: null,
  fishing: null,
  gasPipelines: null,
  seismic: null,
  terrainEurope: null,
  fishingEurope: null,
  loaded: {
    anchorage: false,
    dredging: false,
    fishing: false,
    gasPipelines: false,
    seismic: false,
    terrainEurope: false,
    fishingEurope: false
  }
});

// Computed data properties
const anchorageRiskHeatmapData = computed(() => dataCache.value.anchorage);
const dredgingData = computed<FeatureCollection | undefined>(() => {return dataCache.value.dredging ?? undefined;});
const aggregatedFishingData = computed(() => dataCache.value.fishing);
const gasPipelineData = computed(() => dataCache.value.gasPipelines);
const aggregatedSeismicData = computed(() => dataCache.value.seismic);
const terrainDataChoroplethEurope = computed(() => dataCache.value.terrainEurope);
const fishingDataEurope = computed(() => dataCache.value.fishingEurope);

// **************************
// *** TRANSLATION SYSTEM ***
// **************************
const currentLanguage = ref('ENGLISH');
const currentTranslations = computed(() => {
  return translations[currentLanguage.value];
});

/**
 * Changes the application language and updates all translated elements
 * @param {string} language - The language code to switch to
 */
const changeLanguage = (language) => {
  currentLanguage.value = language;
  if (layerManagerRef.value) {
    layerManagerRef.value.changeLanguage(language);
  }
};

/**
 * Gets translated infrastructure name with confidence level
 * @param {Object} infrastructure - The infrastructure object
 * @returns {string} Translated name with confidence
 */
const getTranslatedInfrastructureName = (infrastructure) => {
  const name = currentTranslations.value[infrastructure.name] || currentTranslations.value.unknown;
  const confidence = currentTranslations.value[infrastructure.confidence];
  return `${name} (${currentTranslations.value.confidence} ${confidence})`;
};

// **************************
// *** MAP INITIALIZATION ***
// **************************
/**
 * Handles map ready event, initializes tools and layers
 * @param {L.Map} mapIns - The Leaflet map instance
 */
const onMapReady = (mapIns) => {
  mapInstance.value = mapIns;
  cableStore.setMapInstance(mapIns);

  // Initialize polyline measurement tool
  polylineMeasure.value = L.control.polylineMeasure({
    position: 'topleft',
    showUnitControl: true,
    unitControlUnits: ["kilometres", "nauticalmiles", "landmiles"],
    unitControlTitle: {
        text: 'Change Units',
        metres: 'metres',
        nauticalmiles: 'nautical miles',
        landmiles: 'land miles'
    },
    unitControlLabel: {
        metres: 'm',
        kilometres: 'km',
        feet: 'ft',
        landmiles: 'mi',
        nauticalmiles: 'nm'
    }
  });
  polylineMeasure.value.addTo(mapInstance.value);
  
  // Initialize tooltip for cable hover
  tooltip = L.tooltip({
    permanent: false,
    direction: 'top',
    offset: L.point(0, -10),
    className: 'custom-tooltip'
  });

  // Initialize marker layer for events
  eventMarkerLayer.value = L.layerGroup().addTo(mapInstance.value);

  mapReady.value = true;
};

/**
 * Handles layer control ready event
 * @param {L.Control.Layers} controlIns - The Leaflet layers control instance
 */
const onLayerControlReady = (controlIns) => {   
  controlInstance.value = controlIns;    
};

// **************************
// *** CABLE & POLYLINE FUNCTIONS ***
// **************************
const polylinesBounds = {};

/**
 * Handles polyline creation, stores bounds for each cable
 * @param {L.Polyline} polylineInstance - The created polyline instance
 * @param {string} cableId - ID of the associated cable
 */
const onCreateLine = (polylineInstance, cableId) => {
  const bounds = polylineInstance.getBounds();
  polylinesBounds[cableId] = polylinesBounds[cableId] ? polylinesBounds[cableId].extend(bounds) : bounds;

  polylineInstance.on('mouseover', () => {
    polylineInstance.openTooltip();
  });
  polylineInstance.on('mouseout', () => {
    polylineInstance.closeTooltip();
  });
};

// Watch for cable selection changes to pan/zoom map
watch(() => cableStore.selectCable, (state) => {
  if (!mapInstance.value) return;
  if (cableStore.selectedFromSidebar) {
    let bounds;
    for (const cable of state) {
      const bound = polylinesBounds[cable];
      bounds = bounds ? bounds.extend(bound) : bound;
    }
    if (bounds) {
      mapInstance.value.fitBounds(bounds);
    }
    cableStore.selectedFromSidebar = false;
  }
});

// Watch for landing site selection changes to pan map
watch(() => landingSiteStore.selectLandingSites, (state) => {
  if (!mapInstance.value) return;
  if (landingSiteStore.selectedFromSidebar) {
    const geo = state[0]?.geoData;
    if (!geo || geo.length < 2) return;  
    const latlng = L.latLng(geo[0], geo[1]);
    mapInstance.value.setView(latlng);
    landingSiteStore.selectedFromSidebar = false;
  }
});


// **************************
// *** TOOLTIP FUNCTIONS ***
// **************************
let tooltip = null;

/**
 * Shows tooltip at mouse position with cable name
 * @param {LeafletMouseEvent} e - Mouse event
 * @param {string} cableName - Name of the cable to display
 */
const showTooltip = (e, cableName) => {
  if (mapInstance.value && tooltip) {
    tooltip.setLatLng(e.latlng)
      .setContent(cableName)
      .addTo(mapInstance.value);
  }
};

/**
 * Hides the currently visible tooltip
 */
const hideTooltip = () => {
  if (mapInstance.value && tooltip) {
    mapInstance.value.removeLayer(tooltip);
  }
};

// **************************
// *** VESSEL FUNCTIONS ***
// **************************
/**
 * Creates a ship icon for vessel markers
 * @param {Object} vessel - Vessel data
 * @param {string} caller - Context where the icon is created
 * @returns {L.Icon} The created icon
 */
const createShipIcon = (vessel: Vessel, caller : string) => {
  if (!vesselTrackingRef.value) {
    console.warn('VesselTracking component reference not available');
    return null;
  }
  return vesselTrackingRef.value.createShipIcon('#ff7800', vessel.flag, vessel);
};

/**
 * Handles ship click events, delegates to VesselTracking component
 * @param {Event} e - Click event
 * @param {Object} vessel - Clicked vessel data
 */
const onShipClick = (e: Event, vessel: Vessel) => {
  if (!vesselTrackingRef.value) {
    console.warn('VesselTracking component reference not available');
    return;
  }
  vesselTrackingRef.value.onShipClick(e, vessel);
};

/**
 * Exits vessel tracking mode
 */
const exitVesselTrackingMode = () => {
  vesselTrackingRef.value.exitVesselTrackingMode();
}

// Watch for vessel position changes
watch(
  () => vesselPositions,
  (newPositions) => {
    if (newPositions && Array.isArray(newPositions) && newPositions.length > 0) {
      vesselPositions.value = newPositions;
    }
  },
  { deep: true }
);

// Watch for hovered marker changes
watch(
  () => hoveredMarker.value,
  (newMarker) => {
    if (newMarker && eventMarkerLayer.value) {
      eventMarkerLayer.value.addLayer(newMarker);
    } else if (eventMarkerLayer.value && newMarker === null) {
      eventMarkerLayer.value.clearLayers();
    }
  },
  { deep: true }
);

// **************************
// *** LAYER FUNCTIONS ***
// **************************
/**
 * Resets the map to its initial state
 */
const resetMap = () => {
  if (!mapInstance.value || !layerManagerRef.value) {
    console.warn('Map instance or layer manager not available');
    return;
  }

  // Reset state variables
  oceanSelected.value = false;
  fishingSelected.value = false;
  showSeismicOverlay.value = false;

  // Use LayerManager methods
  layerManagerRef.value.removeAllOverlays();

  // Remove class modifiers from map container
  const mapContainer = mapInstance.value._container;
  mapContainer.classList.remove(
    'ocean-depth-active',
    'fishing-active',
    'seismic-active',
    'anchorage-active',
    'cable-ships-active'
  );

  // Exit vessel tracking mode if active
  if (vesselTrackingRef.value?.isVesselTrackingMode.value) {
    vesselTrackingRef.value.exitVesselTrackingMode();
  }

  // Clear any selections
  cableStore.deselectCables();
  landingSiteStore.deselectLandingSite();

  // Hide any open toggles
  if (anchorageLayerToggleRef.value) {
    anchorageLayerToggleRef.value.hide();
  }
  if (maintenanceLayerToggleRef.value) {
    maintenanceLayerToggleRef.value.hide();
  }

  // Switch to the default base layer
  layerManagerRef.value.switchToBaseLayer("Base Layer");
};

/**
 * Handles base layer change events
 * @param {Object} e - Layer change event
 */
function layerChanged(e) {
  if (!mapInstance.value || !mapInstance.value._container) {
    return;
  }
  const mapContainer = mapInstance.value._container;
  mapContainer.classList.remove(
    'ocean-depth-active', 
    'fishing-active', 
    'seismic-active',
    'risk-layers-active'
  );

  if (vesselTrackingRef.value?.isVesselTrackingMode.value) {
    vesselTrackingRef.value.exitVesselTrackingMode();
  }
  

  if (e.originalName === "Reset Map") {
    resetMap();
    
    // Find and switch to Base Layer
    const baseLayer = controlInstance._layers.find(layer => 
      !layer.overlay && layer.originalName === "Base Layer"
    );
    
    if (baseLayer && baseLayer.layer) {
      // Remove current base layer if any
      controlInstance._layers
        .filter(layer => !layer.overlay && mapInstance.value.hasLayer(layer.layer))
        .forEach(layer => mapInstance.value.removeLayer(layer.layer));
      
      // Add Base Layer
      mapInstance.value.addLayer(baseLayer.layer);
      
      // Update layer control
      controlInstance._update();
    }
    return;
  }
  if(e.originalName === "Base Layer"){
    oceanSelected.value = false;
    fishingSelected.value = false;
    showSeismicOverlay.value = false;
  }
  else if(e.originalName === "Seismic Activity"){
    mapContainer.classList.add('seismic-active');
    oceanSelected.value = false;
    fishingSelected.value = false;
    showSeismicOverlay.value = true;
  }
  else if (e.originalName === "Fishing Activity"){
    mapContainer.classList.add('fishing-active');
    fishingSelected.value = true;
    oceanSelected.value = false;
    showSeismicOverlay.value = false;
  }
  else if (e.originalName === "Ocean Depth") {
    mapContainer.classList.add('ocean-depth-active');
    oceanSelected.value = true;
    fishingSelected.value = false;
    showSeismicOverlay.value = false;
  }
  else if (e.originalName === "Risk Layers") {
    mapContainer.classList.add('risk-layers-active');
  }
  else {
    oceanSelected.value = false;
    fishingSelected.value = false;
    showSeismicOverlay.value = false;
  }
}

/**
 * Handles overlay addition events
 * @param {Object} e - Overlay event
 */
function overlayAdded(e) {
  const mapContainer = mapInstance.value._container;
  if (e.originalName === "Anchorage and seabed use") {
    mapContainer.classList.add('anchorage-active');
    if (anchorageLayerToggleRef.value) {
      anchorageLayerToggleRef.value.show();
    }
  } else if (e.originalName === "Cable Ships") {
    mapContainer.classList.add('cable-ships-active');
    if (maintenanceLayerToggleRef.value) {
      maintenanceLayerToggleRef.value.show();
    }
  }
}

/**
 * Handles overlay removal events
 * @param {Object} e - Overlay event
 */
function overlayRemoved(e) {
  const mapContainer = mapInstance.value._container;

  if (e.originalName === "Anchorage and seabed use") {
    mapContainer.classList.remove('anchorage-active');
    if (anchorageLayerToggleRef.value) {
      anchorageLayerToggleRef.value.hide();
    }
  } else if (e.originalName === "Cable Ships") {
    mapContainer.classList.remove('cable-ships-active');
    if (maintenanceLayerToggleRef.value) {
      maintenanceLayerToggleRef.value.hide();
    }
  }
}

// **************************
// *** CLICK HANDLERS ***
// **************************
let set = false;

/**
 * Handles map click events
 * @param {LeafletMouseEvent} event - The click event
 */
const onMapClick = (event) => {
  if (gasPipelineLayerRef.value?.clearHighlight) {
    gasPipelineLayerRef.value.clearHighlight();
  }

  if (maintenanceLayerToggleRef.value) {
    const targetElement = event.originalEvent.target;
    if (targetElement && !targetElement.classList.contains('leaflet-interactive')) {
      maintenanceLayerToggleRef.value.clearHighlightedZones();
    }
  }

  if (!set) {
    cableStore.deselectCables();
    landingSiteStore.deselectLandingSite();
  }
  set = false;
};

/**
 * Handles cable click events
 * @param {Event} e - The click event
 * @param {Object} cable - The clicked cable
 */
const onCableClick = (e: Event, cable) => {
    // Original non-simulation behavior
    set = true;
    if (!landingSiteStore.isOneSelected() && cableStore.isCableSelected(cable.id)) {
      cableStore.deselectCables();
    } else {
      landingSiteStore.deselectLandingSite();
      cableStore.setSelectCable(cable.id, false);
    }

  // Display popup at click location
  L.popup()
    .setLatLng(e.latlng)
    .setContent(cable.name)
    .openOn(mapInstance.value);
};

/**
 * Handles landing site click events
 * @param {Event} e - The click event
 * @param {Object} landingSite - The clicked landing site
 */
const onLandingSiteClick = (e, landingSite) => {
  set = true
  cableStore.deselectCables();
  if(landingSiteStore.isLandingSiteSelected(toRaw(landingSite))) {
    landingSiteStore.deselectLandingSite();
  } else {
    landingSiteStore.setSelectLandingSite(toRaw(landingSite))
  }
}

// **************************
// *** MARITIME ZONE STYLES ***
// **************************
/**
 * Returns style options for territorial waters (12nm)
 * @returns {Object} Style options
 */
function getTerritorialOptionsStyle(){
  return {
    color: '#4a2727',
    dashArray: '8',
    fillColor: '#4a2727',
    fillOpacity: '0.2',
  };
}

/**
 * Returns style options for EEZ (200nm)
 * @returns {Object} Style options
 */
function getEEZOptionsStyle(){
  return{
    color: '#8a3030', 
    dashArray: '8'
  }
}

// **************************
// *** MOUSE INTERACTION ***
// **************************
/**
 * Updates mouse position for ocean depth layer
 * @param {LeafletMouseEvent} e - Mouse move event
 */
const updateMousePosition = debounce((e: LeafletMouseEvent) => {
  if (oceanSelected.value && oceanDepthRef.value) {
    oceanDepthRef.value.updateMousePosition(e);
  }
}, 100);

// **************************
// *** UI FUNCTIONS ***
// **************************
/**
 * Handles question mark click to show functionality overview
 */
const questionMarkClicked = () => {
  if(functionalityOverviewRef.value){
    functionalityOverviewRef.value.openModal();
  }
}

// **************************
// *** INITIALIZATION ***
// **************************
/**
 * Processes fixed infrastructure data from JSON
 */
const processInfrastructureData = () => {
  infrastructurePositions.value = fixedInfrastructure.results
    .filter(item => !(item.label === "noise") && item.structure_end_date === "")
    .map(item => ({
      id: item.detection_id,
      position: [item.lat, item.lon],
      name: item.label,
      confidence: item.label_confidence,
      lastSeen: item.detection_date
    }));
};

onMounted(() => {
  // Process infrastructure data immediately (small dataset)
  processInfrastructureData();
  resetMapLayer.value = L.layerGroup();
  resetMapLayer.value.on('add', () => {
    resetMap();
  });

  // Load critical data first (whatever's needed for initial view)
  const loadCriticalData = async () => {
    try {
      const response = await fetch('/data/GEM-GGIT-Gas-Pipelines-2023-12.json');
      dataCache.value.gasPipelines = await response.json();
      dataCache.value.loaded.gasPipelines = true;
    } catch (err) {
      console.error('Error loading gas pipelines data:', err);
    }
  };

  // Load non-critical data in background
  const loadBackgroundData = async () => {
    const backgroundData = [
      { key: 'anchorage', url: '/data/anchorage.json' },
      { key: 'dredging', url: '/data/dredgingEurope.json' },
      { key: 'fishing', url: '/data/riskFishing_2012-2024.json' },
      { key: 'seismic', url: '/data/riskSeismic_2000-2024.json' },
      { key: 'terrainEurope', url: '/data/riskEurope/terrain_europe_cells_normalised.json' },
      { key: 'fishingEurope', url: '/data/riskEurope/fishing_europe_cells_normalised.json' }
    ];

    for (const item of backgroundData) {
      try {
        const response = await fetch(item.url);
        dataCache.value[item.key] = await response.json();
        dataCache.value.loaded[item.key] = true;
      
      } catch (err) {
        console.error(`Error loading ${item.key} data:`, err);
      }
    }
  };

  // Start critical load immediately
  loadCriticalData();
  
  // Start background load after short delay to prioritize UI responsiveness
  setTimeout(loadBackgroundData, 500);
});
</script>

