<!--
  GasPipelines.vue
  
  A Vue component that displays gas pipelines on a Leaflet map with interactive features.
  Features:
  - Dual-layer rendering with border and fill styles for visual depth
  - Click-to-highlight functionality with toggle behavior
  - Hover tooltips showing pipeline names
  - Detailed popups with pipeline information (capacity, cost, countries, etc.)
  - Dynamic styling based on selection state
  - Cost formatting for large numbers (billions/millions)
  - Programmatic highlight clearing capability
-->

<template>
    <l-layer-group
      v-if="gasPipelineData"
      :visible="false"
      name="Gas Pipelines"
      layer-type="overlay"
    >
      <!-- Border layer - rendered first for proper layering -->
      <l-geo-json
        v-if="gasPipelineData"
        :geojson="gasPipelineData"
        :options-style="getGasPipelinesOptionsStyle()[0]"
        @click="(e) => onPipelineClick(e)"
        @mouseover="(e) => onPipelineHover(e)"
        @mouseout="(e) => onPipelineDehover(e)"
      />
      
      <!-- Fill layer - rendered on top of border for visual effect -->
      <l-geo-json
        v-if="gasPipelineData"
        :geojson="gasPipelineData"
        :options-style="getGasPipelinesOptionsStyle()[1]"
        @click="(e) => onPipelineClick(e)"
        @mouseover="(e) => onPipelineHover(e)"
        @mouseout="(e) => onPipelineDehover(e)"
      />
    </l-layer-group>
  </template>
  
  <script setup lang="ts">
    import { computed, watch, ref } from 'vue';
    import L from 'leaflet';
    import { LLayerGroup, LGeoJson } from '@vue-leaflet/vue-leaflet';
  
    // =============================================================================
    // TYPES & INTERFACES
    // =============================================================================
    
    interface Props {
      gasPipelineData: any;
      mapInstance: L.Map | null;
      currentTranslations: any;
    }
  
    // =============================================================================
    // PROPS DEFINITION
    // =============================================================================
    
    const props = defineProps<Props>();
  
    // =============================================================================
    // REACTIVE REFERENCES
    // =============================================================================
    
    const highlightedPipelineId = ref<string | null>(null);
    let tooltip = null;
  
    // =============================================================================
    // COMPUTED PROPERTIES
    // =============================================================================
    
    const gasPipelineData = computed(() => props.gasPipelineData);
  
    // =============================================================================
    // UTILITY FUNCTIONS
    // =============================================================================
    
    /**
     * Formats large numbers into billions or millions for better readability
     * @param cost - The cost value to format
     * @returns Formatted string with B/M suffix or original value
     */
    const formatCost = (cost) => {
      if (cost >= 1e9) {
        return `${(cost / 1e9).toFixed(2)} B`;
      } else if (cost >= 1e6) {
        return `${(cost / 1e6).toFixed(2)} M`;
      } else {
        return cost.toString();
      }
    };
  
    /**
     * Initializes the tooltip instance for hover interactions
     */
    const initializeTooltip = () => {
      if (props.mapInstance && !tooltip) {
        tooltip = L.tooltip({
          permanent: false,
          direction: 'top',
          offset: L.point(0, -10),
          className: 'custom-tooltip'
        });
      }
    };
  
    // =============================================================================
    // STYLING FUNCTIONS
    // =============================================================================
    
    /**
     * Returns an array of style functions for border and fill layers
     * Applies different styling based on highlight state
     */
    function getGasPipelinesOptionsStyle() {
      return [
        // Border style - creates outer stroke with dashed pattern
        (feature: any) => {
          const isPipelineHighlighted = highlightedPipelineId.value === feature.properties.PipelineID;
          return {
            color: isPipelineHighlighted ? '#FF5733' : '#333333',
            weight: isPipelineHighlighted ? 6 : 4,
            opacity: isPipelineHighlighted ? 0.9 : 0.5,
            dashArray: '5',
          };
        },
        // Fill style - creates inner solid line
        (feature: any) => {
          const isPipelineHighlighted = highlightedPipelineId.value === feature.properties.PipelineID;
          return {
            color: isPipelineHighlighted ? '#FFAA33' : '#3dff98',
            weight: isPipelineHighlighted ? 1.2 : 0.7,
            opacity: isPipelineHighlighted ? 0.8 : 0.5,
          };
        }
      ];
    }
  
    // =============================================================================
    // EVENT HANDLERS
    // =============================================================================
    
    /**
     * Handles pipeline click events - toggles highlight and shows popup
     * @param e - Click event from Leaflet
     */
    const onPipelineClick = (e) => {
      L.DomEvent.stopPropagation(e);
      
      const feature = e.layer.toGeoJSON();
      const properties = feature.properties;
      const pipelineId = properties.PipelineID;
      
      // Store the previously highlighted pipeline ID before updating
      const previouslyHighlighted = highlightedPipelineId.value;
      
      // Toggle highlighting - if clicking same pipeline, deselect it
      if (highlightedPipelineId.value === pipelineId) {
        highlightedPipelineId.value = null;
      } else {
        highlightedPipelineId.value = pipelineId;
      }
  
      // Force redraw of affected pipeline segments
      if (props.mapInstance) {
        props.mapInstance.eachLayer((layer) => {
          if (layer.feature && layer.feature.properties) {
            const isSamePipeline = layer.feature.properties.PipelineID === pipelineId;
            const wasPreviouslyHighlighted = layer.feature.properties.PipelineID === previouslyHighlighted;
            
            // Only update layers that are either newly selected or previously selected
            if (isSamePipeline || wasPreviouslyHighlighted) {
              const [borderStyle, fillStyle] = getGasPipelinesOptionsStyle();
              
              // Apply appropriate style based on layer type (border has dashArray)
              const currentStyle = layer.options;
              
              if (currentStyle.dashArray) {
                layer.setStyle(borderStyle(layer.feature));
              } else {
                layer.setStyle(fillStyle(layer.feature));
              }
            }
          }
        });
      }
  
      // Show detailed popup if a new pipeline was selected
      if (highlightedPipelineId.value === pipelineId) {
        const cost = properties.CostUSD ?? properties.Cost;
        const formattedCost = cost ? formatCost(cost) : 'N/A';
        
        const popupContent = `
  <div class="gaspipeline-popup-container">
    <header class="popup-header">
      <span class="popup-icon"></span>
      <h2 class="popup-title">${properties.PipelineName}</h2>
    </header>
    <section class="popup-info-grid">
      <div class="info-item">
        <div class="label">${props.currentTranslations.startCountry}</div>
        <div class="value">${properties.StartCountry}</div>
      </div>
      <div class="info-item">
        <div class="label">${props.currentTranslations.endCountry}</div>
        <div class="value">${properties.EndCountry}</div>
      </div>
      <div class="info-item">
        <div class="label">${props.currentTranslations.countries}</div>
        <div class="value">${properties.Countries}</div>
      </div>
      <div class="info-item">
        <div class="label">${props.currentTranslations.owner}</div>
        <div class="value">${properties.Owner}</div>
      </div>
      <div class="info-item">
        <div class="label">${props.currentTranslations.startYear}</div>
        <div class="value">${properties.StartYear1}</div>
      </div>
      <div class="info-item" title="${props.currentTranslations.capacityTooltip}">
        <div class="label">${props.currentTranslations.capacity}</div>
        <div class="value">${properties["CapacityBcm/y"]} Bcm/y</div>
      </div>
      <div class="info-item">
        <div class="label">${props.currentTranslations.cost}</div>
        <div class="value">${formattedCost}$</div>
      </div>
      <div class="info-item">
        <div class="label">${props.currentTranslations.length}</div>
        <div class="value">${properties.LengthKnownKm} km</div>
      </div>
    </section>
  </div>
`;

        
const popup = L.popup({ className: 'gaspipeline-popup' })
  .setLatLng(e.latlng)
  .setContent(popupContent)
  .openOn(props.mapInstance);

      }
    };
  
    /**
     * Handles pipeline hover events - shows tooltip with pipeline name
     * @param e - Hover event from Leaflet
     */
    const onPipelineHover = (e: Event) => {
      const feature = e.layer.toGeoJSON();
  
      if (props.mapInstance && tooltip) {
        tooltip.setLatLng(e.latlng)
          .setContent(feature.properties.PipelineName)
          .addTo(props.mapInstance);
      }
    }
  
    /**
     * Handles pipeline mouse leave events - removes tooltip
     * @param e - Mouse leave event from Leaflet
     */
    const onPipelineDehover = (e: Event) => {
      if (props.mapInstance && tooltip) {
        props.mapInstance.removeLayer(tooltip);
      }
    }
  
    // =============================================================================
    // PUBLIC METHODS
    // =============================================================================
    
    /**
     * Clears the current pipeline highlight and resets styling
     * Used for programmatic deselection from parent components
     */
    function clearHighlight() {
      const previouslyHighlighted = highlightedPipelineId.value;
      highlightedPipelineId.value = null;
  
      if (props.mapInstance && previouslyHighlighted) {
        props.mapInstance.eachLayer((layer) => {
          if (layer.feature?.properties?.PipelineID === previouslyHighlighted) {
            const [borderStyle, fillStyle] = getGasPipelinesOptionsStyle();
            
            if (layer.options.dashArray) {
              layer.setStyle(borderStyle(layer.feature));
            } else {
              layer.setStyle(fillStyle(layer.feature));
            }
          }
        });
      }
    }
  
    // =============================================================================
    // LIFECYCLE HOOKS
    // =============================================================================
    
    /**
     * Watch for mapInstance changes to initialize tooltip functionality
     */
    watch(() => props.mapInstance, (newMapInstance) => {
      if (newMapInstance) {
        initializeTooltip();
      }
    }, { immediate: true });
  
    // =============================================================================
    // COMPONENT EXPORTS
    // =============================================================================
    
    defineExpose({
      clearHighlight
    });
  </script>

  <style>
.gaspipeline-popup .leaflet-popup-content-wrapper {
  background: rgba(26, 32, 44, 0.9); /* dark translucent */
  border-radius: 10px;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.5);
  padding: 12px 16px;
  max-width: 500px !important; /* smaller */
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 13px;
  color: #e2e8f0;
  line-height: 1.4;
  user-select: none;
  pointer-events: auto;
  backdrop-filter: saturate(180%) blur(8px);
}

.gaspipeline-popup .leaflet-popup-content {
  margin: 0;
  padding: 0;
}

.gaspipeline-popup .leaflet-popup-close-button {
  color: #63b3ed;
  font-size: 1.2rem;
  opacity: 0.8;
  top: 8px;
  right: 8px;
  background: transparent;
  border: none;
  text-shadow: none;
}

.gaspipeline-popup .leaflet-popup-tip {
  display: none;
}

/* Keep your inner container styling separate */
.gaspipeline-popup-container {
  /* you can keep text layout styles here */
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.popup-header {
  display: flex;
  align-items: center;
  margin-bottom: 2px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  padding-bottom: 8px;
}

.popup-icon {
  font-size: 20px !important;
  margin-right: 10px;
  color: #63b3ed;
}

.popup-title {
  font-weight: 700;
  font-size: 0.85rem;
  color: #90cdf4;
  margin: 0;
}

.popup-info-grid {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 0.75rem;
}

.info-item {
  display: flex;
  justify-content: space-between;
  padding: 6px 10px;
  background: rgba(255, 255, 255, 0.07);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: background 0.2s ease;
  font-size: 0.75rem;
}

.info-item:hover {
  background: rgba(99, 179, 237, 0.15);
  border-color: rgba(99, 179, 237, 0.25);
}

.label {
  font-size: 0.75rem !important; 
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.2px !important;
  color: #a0aec0;
}

.value {
  font-size: 0.75rem !important;
  font-weight: 700;
  color: #e2e8f0;
  text-align: right;
  max-width: 350px;
  word-break: break-word;
}


  </style>