<!--
  OceanDepth.vue
  
  A Vue component that displays ocean depth data on a Leaflet map using GEBCO WMS services.
  Provides interactive ocean depth visualization with real-time depth querying, coordinate display,
  and drawing functionality. The component integrates with GEBCO (General Bathymetric Chart of 
  the Oceans) to show bathymetric data with color-coded depth levels.
  
  Features:
  - WMS layer integration with GEBCO bathymetric data
  - Real-time ocean depth querying on mouse movement
  - Interactive depth tooltip following cursor
  - Coordinate display (latitude/longitude)
  - Ocean depth legend with color coding
  - Country overlay for geographical context
  - Drawing layer integration for measurements/annotations
  - Debounced mouse tracking for performance optimization
-->

<template>
    <div>
      <!-- Ocean Depth WMS Layer Group -->
      <l-layer-group
        name="Ocean Depth"
        layer-type="base"
        :visible="false"
        @add="onOceanDepthActivated"
        @remove="onOceanDepthDeactivated"
      />
      
      <!-- Country Boundaries Overlay -->
      <l-geo-json
        v-if="isOceanSelected"
        :geojson="countries"
        name="countries"
        :visible="true"
        :options-style="{ color: '#000000', weight: '0.75' }"
      />
      
      <!-- Ocean Depth Legend -->
      <div v-if="isOceanSelected" class="custom-popup">
        <h2>{{ props.translations.oceanMapLegend }}</h2>
        <p><span style="color:#b1e5d7">&#9632;</span>: >-200m</p>
        <p><span style="color:#6fd7e6">&#9632;</span>: >-1000m</p>
        <p><span style="color:#275b99">&#9632;</span>: >-6000m</p>
        <p><span style="color:#0c4b7b">&#9632;</span>: >-11000m</p>
      </div>
      
      <!-- Coordinates Display Panel -->
      <div v-if="isOceanSelected" class="custom-popup3">
        <div class="popup-row">
          <span class="label">{{ translations.latitude }}:</span>
          <span class="value">{{ latitude }}</span>
        </div>
        <div class="popup-row">
          <span class="label">{{ translations.longitude }}:</span>
          <span class="value">{{ longitude }}</span>
        </div>
      </div>
      
      <!-- Interactive Depth Tooltip -->
      <div
        v-if="isOceanSelected && !isDrawingModeActive"
        ref="depthTooltip"
        class="depth-tooltip"
      >
        {{ currentOceanDepth }}
      </div>
      
      <!-- Drawing Layer Integration -->
      <div v-show="isOceanSelected">
        <DrawingLayer
          :current-translations="translations"
          :map-instance="mapInstance"
          :ocean-selected="isOceanSelected"
          ref="drawingLayerRef"
        />
      </div>
    </div>
  </template>
  
  <script setup lang="ts">
  import { ref, watch } from 'vue';
  import { LLayerGroup, LGeoJson } from '@vue-leaflet/vue-leaflet';
  import L from 'leaflet';
  import axios from 'axios';
  import debounce from 'lodash.debounce';
  import DrawingLayer from '@/components/layers/natural/DrawingLayer.vue';
  import countries from '@/assets/countries.json';
  
  // ==========================================
  // COMPONENT INTERFACE
  // ==========================================
  
  interface Props {
    mapInstance: L.Map | null;
    translations: Record<string, any>;
    isOceanSelected: boolean;
    isDrawingModeActive: boolean;
  }
  
  const props = withDefaults(defineProps<Props>(), {
    mapInstance: null,
    isOceanSelected: false,
    isDrawingModeActive: false,
  });
  
  const emit = defineEmits<{
    'update:isOceanSelected': [value: boolean];
    'update:isDrawingModeActive': [value: boolean];
  }>();
  
  // ==========================================
  // TEMPLATE REFERENCES
  // ==========================================
  
  // Reference to the depth tooltip DOM element for positioning
  const depthTooltip = ref<HTMLElement | null>(null);
  
  // Reference to the drawing layer component
  const drawingLayerRef = ref<any>(null);
  
  // Reference to the GEBCO WMS layer instance
  const oceanDepthWMSLayer = ref<L.TileLayer.WMS | null>(null);
  
  // ==========================================
  // REACTIVE STATE
  // ==========================================
  
  // Current mouse cursor coordinates
  const latitude = ref(15);
  const longitude = ref(15);
  
  // Current ocean depth information displayed in tooltip
  const currentOceanDepth = ref("Ocean Depth at this point: -5000m");
  
  // Cache for last depth query to prevent duplicate API calls
  let lastDepthQuery = '';
  
  // ==========================================
  // WATCHERS
  // ==========================================
  
  /**
   * Watch for drawing mode changes from child component
   * Propagates drawing mode state to parent component
   */
  watch(
    () => drawingLayerRef.value?.isDrawingModeActive,
    (newValue) => {
      emit('update:isDrawingModeActive', newValue);
    },
    { immediate: true }
  );
  
  // ==========================================
  // WMS LAYER MANAGEMENT
  // ==========================================
  
  const isLayerBeingRemoved = ref(false);

  /**
   * Activates the ocean depth WMS layer on the map
   * Creates and adds GEBCO bathymetric data layer
   */
   function onOceanDepthActivated() {
  if (!props.mapInstance || isLayerBeingRemoved.value) return;
  
  // Ensure any existing layer is completely removed first
  if (oceanDepthWMSLayer.value) {
    onOceanDepthDeactivated();
  }
  
  try {
    // Always create a fresh WMS layer instance
    oceanDepthWMSLayer.value = L.tileLayer.wms('https://wms.gebco.net/mapserv', {
      layers: 'gebco_latest_sub_ice_topo',
      format: 'image/jpeg',
      transparent: true,
      attribution: 'Map data &copy; <a href="https://www.gebco.net">GEBCO</a>',
      maxZoom: 12,
      minZoom: 0,
    });
    
    // Add to map only after creation is complete
    oceanDepthWMSLayer.value.addTo(props.mapInstance);
    
  } catch (error) {
    console.warn('Error creating ocean depth layer:', error);
    oceanDepthWMSLayer.value = null;
  }
}
  
  /**
   * Deactivates the ocean depth WMS layer
   * Removes the bathymetric layer from the map
   */
   function onOceanDepthDeactivated() {
  if (!props.mapInstance || !oceanDepthWMSLayer.value || isLayerBeingRemoved.value) return;
  
  // Set flag to prevent race conditions
  isLayerBeingRemoved.value = true;
  
  try {
    // Step 1: Remove the layer from the map
    if (props.mapInstance.hasLayer(oceanDepthWMSLayer.value)) {
      props.mapInstance.removeLayer(oceanDepthWMSLayer.value);
    }
    
    // Step 2: Force cleanup of any pending tile requests
    if (oceanDepthWMSLayer.value._container) {
      oceanDepthWMSLayer.value._container.innerHTML = '';
    }
    
    // Step 3: Remove any event listeners on the layer
    if (typeof oceanDepthWMSLayer.value.off === 'function') {
      oceanDepthWMSLayer.value.off();
    }
    
    // Step 4: Clear internal references
    if (typeof oceanDepthWMSLayer.value.remove === 'function') {
      oceanDepthWMSLayer.value.remove();
    }
    
    // Step 5: Manually clean up the layer's internal state
    if (oceanDepthWMSLayer.value._map) {
      oceanDepthWMSLayer.value._map = null;
    }
    if (oceanDepthWMSLayer.value._tiles) {
      oceanDepthWMSLayer.value._tiles = {};
    }
    
    // Step 6: Clear our reference
    oceanDepthWMSLayer.value = null;
    
    // Step 7: Reset other state
    lastDepthQuery = '';
    
    // Step 8: Hide the depth tooltip
    if (depthTooltip.value) {
      depthTooltip.value.style.display = 'none';
    }
    
    // Step 9: Force a map redraw to ensure clean state
    setTimeout(() => {
      if (props.mapInstance) {
        props.mapInstance.invalidateSize();
      }
      isLayerBeingRemoved.value = false;
    }, 100);
    
  } catch (error) {
    console.warn('Error during ocean depth layer cleanup:', error);
    oceanDepthWMSLayer.value = null;
    isLayerBeingRemoved.value = false;
  }
}
  
  // ==========================================
  // OCEAN DEPTH API INTEGRATION
  // ==========================================
  
  /**
   * Fetches ocean depth data for a specific coordinate from GEBCO WMS service
   * Uses GetFeatureInfo request to query bathymetric data at precise location
   * @param {number} lat - Latitude coordinate
   * @param {number} lng - Longitude coordinate
   */
  async function getOceanDepth(lat: number, lng: number) {
    // Create query string for caching and duplicate prevention
    const queryString = `${lat.toFixed(4)},${lng.toFixed(4)}`;
    if (queryString === lastDepthQuery) return;
    lastDepthQuery = queryString;
  
    try {
      // Create small bounding box around the point for WMS query
      const minx = lat - 0.0001;
      const miny = lng - 0.0001;
      const maxx = lat + 0.0001;
      const maxy = lng + 0.0001;
      
      // Construct GEBCO WMS GetFeatureInfo request URL
      const url = `https://wms.gebco.net/mapserv?service=WMS&version=1.3.0&request=GetFeatureInfo&layers=GEBCO_LATEST_2&styles=&bbox=${minx},${miny},${maxx},${maxy}&width=101&height=101&crs=EPSG:4326&query_layers=GEBCO_LATEST_2&info_format=text/plain&i=50&j=50`;
      
      const response = await axios.get(url);
      
      // Parse depth value from WMS response text
      let searchString = "value_list";
      let searchIndex = response.data.indexOf(searchString);
      let extractedText = response.data.substring(
        searchIndex + searchString.length + 4,
        response.data.length - 2
      );
      
      // Update depth display with parsed value
      currentOceanDepth.value = `${props.translations.oceanDepth}: ${extractedText}m`;
    } catch (error) {
      // Handle API errors gracefully
      currentOceanDepth.value = "Ocean Depth: Unknown";
    }
  }
  
  // ==========================================
  // UI INTERACTION HANDLERS
  // ==========================================
  
  /**
   * Updates the position of the depth tooltip to follow the mouse cursor
   * Converts map coordinates to screen coordinates for tooltip positioning
   * @param {L.LeafletMouseEvent} e - Leaflet mouse event containing coordinates
   */
  const updateDepthTooltip = (e: L.LeafletMouseEvent) => {
    if (props.isOceanSelected && depthTooltip.value && props.mapInstance) {
      // Convert lat/lng coordinates to container pixel coordinates
      const containerPoint = props.mapInstance.latLngToContainerPoint(e.latlng);
      
      // Position tooltip with slight offset from cursor
      depthTooltip.value.style.left = `${containerPoint.x + 15}px`;
      depthTooltip.value.style.top = `${containerPoint.y + 15}px`;
      depthTooltip.value.style.display = 'block';
    } else if (depthTooltip.value) {
      // Hide tooltip when not in ocean mode
      depthTooltip.value.style.display = 'none';
    }
  };
  
  /**
   * Debounced handler for mouse position updates
   * Updates coordinates display and triggers depth queries
   * Debounced to prevent excessive API calls during mouse movement
   */
   const updateMousePosition = debounce((e: L.LeafletMouseEvent) => {
  // Enhanced safety checks
  if (!props.isOceanSelected || 
      !oceanDepthWMSLayer.value || 
      !props.mapInstance ||
      !props.mapInstance.getContainer() ||
      isLayerBeingRemoved.value ||
      !props.mapInstance.hasLayer(oceanDepthWMSLayer.value)) {
    return;
  }
  
  try {
    const latlng = e.latlng;
    
    // Round coordinates to 4 decimal places for display
    const roundedLat = Math.round(latlng.lat * 1e4) / 1e4;
    const roundedLng = Math.round(latlng.lng * 1e4) / 1e4;
    
    // Update coordinate display
    latitude.value = roundedLat;
    longitude.value = roundedLng;
    
    // Only query depth and show tooltip when not in drawing mode
    if (!props.isDrawingModeActive) {
      getOceanDepth(roundedLat, roundedLng);
      updateDepthTooltip(e);
    }
  } catch (error) {
    console.warn('Error updating mouse position:', error);
  }
}, 100); // 100ms debounce delay for performance
  
  // ==========================================
  // COMPONENT INTERFACE EXPOSURE
  // ==========================================
  
  /**
   * Expose methods for parent component interaction
   * Allows parent to trigger mouse updates and layer management
   */
  defineExpose({
    updateMousePosition,
    onOceanDepthActivated,
    onOceanDepthDeactivated,
  });
  </script>
  
  <style scoped>
  </style>